.sngl {
  font-weight: 400;
  font-size: 18px;

  .sngl-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include lst;
    margin-bottom: 30px;
    padding: 40px 30px;
    background-color: $grey;
    border-radius: 10px;

    &__item {
      display: flex;
      flex-direction: column;
      margin-bottom: 0;
      padding-left: 0;
      padding-right: 40px;
      color: $grey-2;

      &::before {
        display: none;
      }

      &--foto {
        padding-left: 80px;
      }
    }

    &__foto {
      position: absolute;
      top: 50%;
      left: 0;
      width: 60px;
      height: 60px;
      overflow: hidden;
      background: $gr-grey;
      border-radius: 10px;
      transform: translateY(-50%);

      img {
        position: relative;
        left: 50%;
        max-width: 120%;
        margin: 5px 0;
        transform: translateX(-50%);
      }
    }

    &__text {
      margin-bottom: 3px;
      font-size: 12px;
      white-space: nowrap;
    }

    &__data {
      position: relative;
      font-weight: 500;
      font-size: 14px;

      &--icon {
        padding-left: 22px;

        svg {
          position: absolute;
          top: 50%;
          left: 0;
          width: 18px;
          height: 18px;
          fill: $grey-2;
          transform: translateY(-50%);
        }
      }
    }

    @include desktop {
      flex-wrap: wrap;
      padding: 40px;

      &__item {
        display: flex;
        flex-direction: column;
        
        padding-right: 30px;
  
        &--foto {
          display: flex;
          flex-direction: column;
          justify-content: center;
          width: 100%;
          min-height: 70px;
          margin-bottom: 10px;
          padding-left: 75px;
        }
      }
    }

    @include laptop {
      padding: 20px;
    }

    @include phones {
      margin-top: -20px;
      margin-bottom: 15px;
      padding: 0;
      background: none;

      &__foto {
        display: none;
      }

      &__item {
        flex-direction: row;
        align-items: flex-end;
        width: 100%;
        margin-bottom: 5px;
        padding-right: 0;

        &--foto {
          justify-content: flex-start;
          align-self: flex-end;
          width: 100%;
          min-height: auto;
          padding-left: 0;
        }
      }

      &__data {
        padding-left: 5px;
        svg {
          display: none;
        }
      }

      &__text {
        flex: 0 0 110px;
        width: 110px;
        margin-bottom: auto;
        line-height: 1.6;
      }
    }
  }

  .sngl-video {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    padding: 32px;
    background-color: $grey;
    border-radius: 10px;

    img {
      margin: 0;
    }

    .youtube {
      border-radius: 5px;
    }

    &__block {
      &--left {
        width: 55%;
      }
      &--right {
        width: 45%;
        padding-left: 25px;
      }
    }

    &__title {
      margin-bottom: 7px;
      font-weight: 600;
      font-size: 18px;
    }

    &__text {
      max-height: 80px;
      margin-bottom: 20px;
      overflow: hidden;
      font-size: 14px;
      color: $grey-2;
    }

    &__inner {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: 70px;
      // padding-left: 90px;
      line-height: 1.6;
      color: $grey-2;
    }

    &__examples {
      display: none;
    }

    &__foto {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 70px;
      height: 70px;
      overflow: hidden;
      background: $gr-grey;
      border-radius: 10px;

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 140%;
        height: 140%;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        transform: translate(-50%, -30%);
      }
  
      &--bodnar {
        &::before {
          background-image: url(../img/services/humans/bodnar-e-i_small.png);
        }
      }
  
      &--okladnikov {
        &::before {
          background-image: url(../img/services/humans/ilya-viktorovich_small.png);
        }
      }
  
      &--kopyrina {
        &::before {
          background-image: url(../img/services/humans/anzhelika-vladimirovna_small.png);
        }
      }
  
      &--efremov {
        &::before {
          background-image: url(../img/services/humans/aleksandr-igorevich_small.png);
        }
      }
    }

    &__author {
      margin-bottom: 2px;
      font-size: 12px;
    }
    &__name {
      margin-bottom: 2px;
      font-weight: 700;
      font-size: 14px;
    }
    &__post {
      font-size: 14px;
    }

    @include desktop {
      flex-direction: column;

      &__block {
        &--left {
          width: 100%;
          margin-bottom: 20px;
        }
        &--right {
          width: 100%;
          padding-left: 0;
        }
      }
    }

    @include laptop {
      padding: 20px;
    }

    @include phones {
      padding: 15px;

      &__block {
        &--left {
          margin-bottom: 10px;
        }
      }

      &__title {
        font-size: 16px;
      }

      &__name {
        line-height: 1.4;
      }

      &__post {
        font-size: 12px;
        line-height: 1.4;
      }
    }
  }

  figure {
    margin: 0;
  }

  img {
    margin: 45px 0;
    overflow: hidden;
    border-radius: 10px;
  }

  h2,
  h3 {
    margin-top: 55px;
    font-weight: 600;
  }

  p {
    margin-bottom: 0;

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  ul {
    @include lst;
  }

  ol {
    padding-left: 20px;

    li {
      margin-bottom: 15px;
      padding-left: 2px;

      &::marker {
        font-weight: 600;
        color: $accent;
      }

      &::before {
        display: none;
      }
    }

    ul {
      li {
        &::before {
          display: none;
        }
      }
    }
  }

  ul {
    li {
      ul {
        li {
          &::before {
            display: none;
          }
        }
      }
    }
  }

  li {
    position: relative;
    margin-bottom: 15px;
    padding-left: 20px;

    &::before {
      content: '';
      position: absolute;
      top: 10px;
      left: 0;
      width: 5px;
      height: 5px;
      background-color: $accent;
      border-radius: 50%;
    }
  }

  &__list {
    &--two {
      display: flex;
      flex-wrap: wrap;
      width: 50%;
      margin-left: 20px !important;
      margin-top: 15px !important;

      li {
        width: 50%;
        margin-bottom: 10px;

        &::before {
          background-color: $grey-2;
        }
      }
    }
  }

  &__btn {
    position: relative;
    display: inline-block;
    padding: 5px 0;
    padding-right: 20px;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.4;
    color: $accent;
    border-radius: 2px;
    transition: all $fastdown;
    
    &-arrow {
      position: absolute;
      top: 50%;
      right: 3px;
      border-style: solid;
      border-width: 10px 6px 0 6px;
      border-color: $accent transparent transparent transparent;
      transform: translateY(-50%);
      transition: border-color $fastdown;
    }

    &.open {
      .sngl__btn-arrow {
        transform: translateY(-50%) rotate(180deg);
      } 
    }
  }

  @include desktop {
    &__list {
      &--two {
        width: 100%;
      }
    }
  }

  @include tablets {
    font-size: 16px;

    &__list {
      &--two {
        li {
          width: 100%;
        }
      }
    }

    h2,
    h3 {
      margin-top: 35px;
      font-size: 18px;
    }
  }

  .tab-single {
    margin-bottom: 65px;
    padding: 30px;
    padding-left: 65px;
    background-color: $grey;
    border-radius: 10px;

    &__title {
      position: relative;
      margin-bottom: 25px;
      font-weight: 700;
      font-size: 22px;
      color: $grey-2;

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: -35px;
        width: 25px;
        height: 25px;
        background-image: url(../img/icons/tab.svg);
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        transform: translateY(-50%);
      }
    }

    &__list {
      margin-top: 20px;
      padding: 0;
      list-style-type: none;
    }

    &__item {
      position: relative;
      margin-bottom: 10px;
      padding-left: 0;
      font-size: 18px;
      color: $accent;

      &--sub {
        padding-left: 30px;
        font-size: 16px;
      }
    }

    @include laptop {
      padding: 20px;
      padding-left: 55px;

      &__title {
        font-size: 20px;
      }
    }

    @include tablets {
      &__title {
        margin-bottom: 15px;
        font-size: 16px;

        &::before {
          width: 20px;
          height: 20px;
        }
      }

      &__item {
        margin-bottom: 10px;
        padding-left: 0;
        font-size: 16px;

        &--sub {
          padding-left: 20px;
        }
      }
    }

    @include phones {
      padding-left: 45px;

      &__title {
        font-size: 16px;

        &::before {
          left: -30px;
        }
      }

      &__list {
        margin-top: 10px;
      }
    }
  }

  .spoiler {
    &__wrap {
      padding-left: 0;
      counter-reset: free-counter;
    }

    &__block {
      margin-bottom: 5px;

      &::before {
        position: absolute;
        left: 25px;
        top: 24px;
        display: block;
        content: counter(free-counter);
        counter-increment: free-counter;
        background: none;
      }
    }

    @include tablets {
      &__block {
        &::before {
          top: 14px;
          left: 15px;
          font-size: 14px;
          color: $default;
        }
      }

      &__head {
        padding: 15px;
      }

      &__content {
        padding: 15px;
        padding-top: 0;
      }

      &__btn {
        top: 12px;
        right: 12px;
      }
    }
  }
}

.info-single {
  position: relative;
  margin-top: 25px;
  margin-bottom: 25px;
  padding: 32px;
  padding-left: 70px;
  font-weight: 400;
  background-color: $grey;
  border-radius: 10px;

  &::before {
    content: '';
    position: absolute;
    top: 32px;
    left: 32px;
    width: 25px;
    height: 25px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }

  &--info {
    &::before {
      background-image: url(../img/icons/info.svg);
    }
  }

  &--attention {
    &::before {
      background-image: url(../img/icons/alert.svg);
    }
  }

  &--fakt {
    &::before {
      background-image: url(../img/icons/light.svg);
    }

    .info-single__text {
      font-weight: 400;
    }
  }

  &--foto {
    &::before {
      background-image: url(../img/icons/quotes.svg);
    }
  }

  &--relevance {
    &::before {
      background-image: url(../img/icons/law.svg);
    }
  }

  &__foto {
    position: absolute;
    top: 20px;
    left: 0;
    width: 47px;
    height: 47px;
    overflow: hidden;
    background: $gr-grey;
    border-radius: 7px;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 140%;
      height: 140%;
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      transform: translate(-50%, -30%);
    }

    &--bodnar {
      &::before {
        background-image: url(../img/services/humans/bodnar-e-i_small.png);
      }
    }

    &--okladnikov {
      &::before {
        background-image: url(../img/services/humans/ilya-viktorovich_small.png);
      }
    }

    &--kopyrina {
      &::before {
        background-image: url(../img/services/humans/anzhelika-vladimirovna_small.png);
      }
    }

    &--efremov {
      &::before {
        background-image: url(../img/services/humans/aleksandr-igorevich_small.png);
      }
    }
  }

  &__examples {
    display: none;
  }

  &__title {
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 18px;
    color: $grey-2;

    &--attention {
      color: $pink;
    }

    &--fakt {
      color: #35d977;
    }

    &--info {
      color: #5381f6;
    }
  }

  &__lst {
    @include lst;

    li {
      position: relative;
      padding-left: 20px;
      margin-bottom: 0;

      &:not(:last-child) {
        margin-bottom: 10px;
      }

      &::before {
        position: absolute;
        content: '';
        top: 8px;
        left: 0;
        width: 6px;
        height: 6px;
        background-color: $accent;
        border-radius: 50%;
      }
    }
  }

  &__text {
    margin-bottom: 0;

    &:not(:last-child) {
      margin-bottom: 25px;
    }

    &--small {
      display: flex;
      flex-direction: column;
      font-size: 14px;

      &:not(:last-child) {
        margin-bottom: 0;
      }

      p {
        margin-bottom: 10px;
      }
    }
  }

  &__block-author {
    position: relative;
    padding-top: 22px;
    padding-left: 57px;
    font-size: 14px;
    border-top: 2px solid #B5BDD1;
  }

  &__name {
    margin-bottom: 5px;
    font-weight: 700;
  } 

  @include laptop {
    padding: 20px;
    padding-left: 55px;

    &::before {
      top: 20px;
      left: 20px;
    }

    &__foto {
      top: 25px;
    }

    &__text {
      font-size: 16px;
    }
  }

  @include tablets {
    padding: 15px;
    padding-left: 45px;

    &::before {
      top: 17px;
      left: 15px;
      width: 20px;
      height: 20px;
    }

    &__title {
      margin-bottom: 12px;
      font-size: 16px;
    }

    &__foto {
      top: 20px;
      left: 0;
    }
  }
}

.table-single {
  margin: 25px 0;
  color: $grey-2;

  &__row {
    display: flex;
    justify-content: space-between;
  }

  &__col {
    width: calc(50% - 2px);
    padding: 15px 18px;
    font-size: 16px;
    border-radius: 5px;

    &--head {
      margin-bottom: 5px;
      font-size: 18px;
      background-color: $grey;
    }
  }

  @include phones {
    &__col {
      padding: 10px;
      font-size: 14px;

      &--head {
        margin-bottom: 5px;
        font-size: 16px;
      }
    }
  }
}

.yes-no {
  margin: 25px 0;
  padding: 25px;
  background-color: $grey;
  border-radius: 10px;

  &__row {
    display: flex;
    justify-content: space-between;

    &:not(:last-child) {
      margin-bottom: 15px;
    }

    &--horizon {
      flex-direction: column;
    }
  }

  &__title {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 22px;
    color: $default;
  }

  &__col {
    position: relative;
    width: calc(50% - 20px);
    padding-left: 70px;

    &--horizon {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }
  }

  &__lable {
    position: absolute;
    left: 0;
    top: 0;
    width: 55px;
    padding: 6px;
    font-weight: 700;
    font-size: 16px;
    color: $white;
    text-align: center;
    border-radius: 4px;

    &--yes {
      background: #35d977;
    }
    &--no {
      background: $pink;
    }
  }

  @include laptop {
    &__row {
      flex-direction: column;

      &:not(:last-child) {
        margin-bottom: 0;
      }
    }

    &__col {
      width: 100%;
      padding-left: 70px;
      padding-bottom: 10px;
    }

    &__title {
      margin-bottom: 15px;
    }
  }

  @include phones {
    padding: 15px;
    &__col {
      padding-left: 0;
      padding-top: 40px;
      padding-bottom: 15px;
    }

    &__title {
      font-size: 18px;
    }
  }
}

.heading {

  &__row {
    display: flex;
    width: 50%;
    margin: 25px 0;
    padding: 25px;
    background-color: $grey;
    border-radius: 10px;
  }

  &__lable {
    padding: 6px 20px;
    font-weight: 700;
    font-size: 16px;
    color: $white;
    border-radius: 4px;

    &--yes {
      background: #35d977;
    }
    &--no {
      background: $pink;
    }
    &--accent {
      background: $accent;
    }
  }

  @include laptop {
    &__row {
      width: 100%;
    }
  }
}

.author-single {
  margin-top: 30px;
  padding-top: 35px;
  font-size: 14px;
  color: $grey-2;
  border-top: 2px solid #d7deef;

  &__row {
    display: flex;
    margin-bottom: 35px;
  }

  &__call {
    display: flex;
    width: 100%;

    &:first-child {
      flex: 1 0 175px;
      margin-right: 35px;
    }

    &--v-center {
      align-items: center;
    }
  }

  &__foto {
    flex: 1 0 175px;
    width: 175px;
    height: 175px;
    overflow: hidden;
    margin-right: 35px;
    background: $gr-grey;
    border-radius: 10px;

    img {
      margin: 10px 0;
    }
  }
  &__author {
    display: flex;
    flex-direction: column;
  }
  &__name {
    margin-bottom: 8px;
    font-size: 16px;
    color: $default;
  }

  &__rating-lable {
    margin-left: 15px;
  }

  @include laptop {
    &__call {
      flex-direction: column;

      &--v-center {
        flex-direction: column;
        align-items: flex-start;
      }
    }

    &__name {
      margin-top: 10px;
    }

    &__rating-lable {
      margin-left: 0;
      margin-top: 5px;
    }
  }

  @include phones {
    &__row {
      flex-direction: column;
      margin-bottom: 25px;
    }

    &__call {
      display: flex;

      &:first-child {
        flex: 1 0 auto;
        margin-right: 0;
        margin-bottom: 10px;
      }
    }
  }
}

.related-single {
  &__list {
    display: none;
  }

  &__title {
    font-weight: 700;
    font-size: 26px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    width: 100%;

    &:hover {
      .related-single {
        &__name {
          color: $accent;
        }
      }
    }
  }

  &__link {
    width: 100%;
    height: 100%;
  }

  &__image {
    position: relative;
    width: 100%;
    height: auto;
    padding-top: 56.25%;
    overflow: hidden;
    margin-bottom: 10px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 10px;

    img {
      position: absolute;
      left: 0;
      top: 0;
      min-height: 100%;
    }
  }

  &__name {
    margin-bottom: 0;
    font-size: 18px;
    color: $default;
    transition: color $middledown;
  }

  @include tablets {
    &__list {
      display: flex;
      flex-direction: column;
      @include lst;
    }

    &__name {
      margin-bottom: 45px;
    }
  }

  @include phones {
    &__image {
      height: 170px;
    }
  }
}

.swiper-related-single {
  @include tablets {
    display: none;
  }
}

.wp-block-media-text {
  position: relative;
  display: flex;
  align-items: center;
  padding: 25px;
  padding-top: 75px;
  padding-bottom: 35px;
  background-color: $grey;
  border-radius: 10px;

  &__media {
    flex: 1 0 110px;
    max-width: 110px;
    height: 110px;
    overflow: hidden;
    border-radius: 7px;

    img {
      object-fit: cover;
      object-position: center;
      height: 100%;
      margin: 0;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    min-height: 110px;
    margin-left: 25px;

    h3 {
      position: absolute;
      top: 25px;
      left: 25px;
      margin: 0;
      font-weight: 700;
      font-size: 22px;
      color: $default;
    }

    p {
      margin-bottom: 15px;
      font-weight: 600;
      color: $default;

      a {
        color: inherit;

        &:hover {
          color: $accent;
        }
      }
    }

    .wp-block-buttons {
      position: relative;
      display: block;
      padding: 8px 18px;
      font-weight: 700;
      font-size: 14px;
      color: $color-default-white;
      text-align: center;
      background: linear-gradient(
          360deg,
          rgba(255, 255, 255, 0) 0%,
          rgba(255, 255, 255, 0.2) 100%
        ),
        #3c70f4;
      box-shadow: 0px 4px 14px rgba(0, 37, 102, 0.18);
      border: none;
      border-radius: 7px;
      transition: all $slowdown;
      cursor: pointer;

      &:hover {
        color: $color-default-white;
        text-decoration: none;
      }

      a {
        color: inherit;
      }
    }
  }

  & + .wp-block-media-text {
    top: -10px;
    padding-top: 0;
    border-top-left-radius: 0;
    border-top-right-radius: 0;

    & + .wp-block-media-text {
      top: -20px;
    }
  }

  @include phones {
    flex-direction: column;
    align-items: flex-start;
    padding: 15px;
    padding-top: 80px;

    h3 {
      top: 15px;
      left: 15px;
      padding-right: 40%;
      font-size: 18px;
    }

    &__content {
      min-height: auto;
      margin-left: 0;
      margin-top: 10px;
    }

    .wp-block-buttons {
      display: none;
    }
  }
}

.flow-single {
  margin: 25px 0;

  &__block {
    display: inline;
    width: 50%;
    margin: 25px;
    padding: 25px;
    background-color: $grey;
    border-radius: 10px;

    &--right {
      margin-right: 0;
      float: right;
    }

    &--left {
      margin-left: 0;
      float: left;
    }
  }

  &__subtitle {
    display: inline-block;
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 22px;
  }

  &__link {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &-text {
      margin-bottom: 15px;
      font-weight: 600;
      font-size: 16px;
      color: $default;
    }

    &-btn {
      padding: 8px 18px;
      font-size: 12px;
    }

    &:hover {
      color: $accent;

      .flow-single__link-text {
        color: $accent;
      }
    }
  }

  @include laptop {
    &__block {
      width: 100%;

      &--right {
        margin-left: 0;
        float: right;
      }

      &--left {
        margin-right: 0;
        float: left;
      }
    }
  }

  @include phones {
    &__block {
      padding: 15px;
    }

    &__subtitle {
      margin-bottom: 20px;
      font-size: 18px;
    }
  }
}

.interest-single {
  margin-bottom: 20px;
  padding: 25px;
  background-color: $grey;
  border-radius: 10px;

  &__title {
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 22px;
    color: $default;
  }

  &__block {
    display: flex;

    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &__media {
    flex: 1 0 110px;
    max-width: 96px;
    height: 96px;
    overflow: hidden;
    border-radius: 7px;

    img {
      object-fit: cover;
      object-position: center;
      height: 100%;
      margin: 0;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-left: 25px;
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 600;
    transition: color $middledown;
  }
  
  &__excerpt {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-size: 14px;
    color: $grey-2;
  }

  &__btn {
    padding: 8px 18px;
    font-size: 14px;
    border-radius: 7px;
  }

  @include phones {
    padding: 15px;
    margin-bottom: 10px;

    &__title {
      padding-right: 30%;
      font-size: 18px;
    }

    &__block {
      flex-direction: column;

      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }

    &__content {
      margin-left: 0;
    }

    &__name {
      margin-top: 10px;
      margin-bottom: 0;
    }

    &__btn {
      display: none;
    }
  }
}

.interest-hidden {
  display: none;
}

.info-video {
  max-width: 800px;
  margin: 35px auto;
}


//    
.thanks-single {
  margin-bottom: 50px;

  &__wrap {
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    margin-left: -20px;
  }

  &__item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: calc(50% - 20px);
    height: 200px;
    margin-left: 20px;
    margin-bottom: 20px;
    overflow: hidden;
    background-color: $grey;
    border-radius: 10px;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(30, 89, 179, 0.5), rgba(30, 89, 179, 0.5));
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      min-height: 100%;
      object-fit: cover;
      object-position: center;
      transition: opacity $fastdown;
    }

    &:nth-child(3n + 1) {
      width: 100%;
      height: auto;
      padding-top: 56.25%;

      .thanks-single {
        &__content {
          width: 80%;
        }

        &__title {
          font-size: 26px;
        }

        &__text {
          display: block;
        }
      
        &__btn {
          width: 150px;
          padding: 10px;
          font-size: 16px;
      
          svg {
            width: 20px;
            height: 20px;
          }
        }
      }
    }
  }

  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    text-align: center;
    transform: translate(-50%, -50%);
  }

  &__title {
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 16px;
    color: $white;
    text-align: center;
  }

  &__text {
    display: none;
    color: $white;
  }

  &__btn {
    width: 110px;
    padding: 5px;
    font-size: 14px;
    border-radius: 7px;

    svg {
      display: block;
      width: 14px;
      height: 14px;
      margin-right: 5px;
      fill: $white;
    }
  }

  @include laptop {
    &__wrap {
      margin-left: 0;
    }
    
    &__item {
      width: 100%;
      height: auto;
      padding-top: 56.25%;
      margin-left: 0;
      margin-bottom: 20px;

      &:nth-child(3n + 1) {
        padding-top: 56.25%;

        .thanks-single {
          &__content {
            width: 80%;
          }
  
          &__title {
            font-size: 16px;
          }

          &__text {
            display: none;
          }
        
          &__btn {
            width: 110px;
            padding: 5px;
            font-size: 14px;
        
            svg {
              width: 14px;
              height: 14px;
            }
          }
        }
      }
    }
  }

  @include phones {
    &__item {
      padding-top: 66.25%;

      &:nth-child(5n + 1) {
        padding-top: 66.25%;
      }
    }

    &__title {
      margin-bottom: 15px;
    }
  }
}