.lawyer {
  position: relative;
  display: block;
  width: 490px;
  height: 490px;
  margin: 0 auto;
  margin-top: 110px;
  margin-bottom: 20px;
  border-radius: 50%;
  background: $gr-grey;

  // &:not(:first-child) {
  //   display: none;
  // }

  &--inverse {

    &::before, &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }

    &::before {
      background-image: url(../img/bg.jpg);
      opacity: 0.2;
    }

    &::after {
      background-color: $white;
      opacity: 0.9;
    }
  }
  
  img {
    position: absolute;
    bottom: 0;
    left: 50%;
    z-index: 1;
    width: 490px;
    transform: translateX(-50%);
  }

  &__info {
    position: absolute;
    top: 75%;
    left: 60%;
    z-index: 2;
    width: 200px;
    color: $white;
    transform: translateX(-60%);
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 600;
  }

  &__post {
    font-weight: 400;
    font-size: 12px;
  }


  @include desktop-1500 {
    width: 365px;
    height: 365px;

    &__info {
      top: auto;
      bottom: 20px;
      width: 160px;
    }
  }

  @include desktop {
    width: 295px;
    height: 295px;

    &__info {
      bottom: 0;
      left: auto;
      right: 0;
      width: 190px;
      padding: 10px;
      color: $white;
      background-color: $accent;
      transform: translateX(0);
    }
  }

  @include laptop {
    width: 275px;
    height: 275px;

    &__name {
      margin-bottom: 5px;
      font-size: 14px;
    }
  
    &__post {
      font-size: 10px;
    }
  }

  @include tablets {
    margin-top: 50px;
  }

  @include phones {
    margin-top: 60px;
  }
}

.thanks {
  margin-bottom: 140px;

  &__title {
    margin-bottom: 40px;
    text-align: left;
  }

  &__inner {
    margin-top: 35px;
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    color: $grey-2;
  }

  &__lst {
    @include lst;
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;

    &--ul {
      .thanks__lst-item {

        &::before {
          content: '';
          position: absolute;
          top: 10px;
          left: 3px;
          width: 5px;
          height: 5px;
          background-color: $accent;
          border-radius: 50%;
        }
      }
    }

    &--ol {
      counter-reset: item;

      .thanks__lst-item {
        counter-increment: item;

        &::before {
          content: counter(item) '.';
          position: absolute;
          top: 0;
          left: 3px;
          color: $accent;
        }
      }
    }
  }

  &__lst-item {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-left: 25px;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__problem {
    margin-bottom: 5px;
  }

  &__answer {
    font-weight: 600;
    font-size: 22px;
    color: $accent;
  }

  &__question {
    margin-bottom: 10px;
    font-size: 26px;
  }

  &__phone {
    font-weight: 700;
    font-size: 26px;
    color: $accent;
  }

  &__btns {
    display: flex;
    justify-content: flex-start;
    margin-top: 20px;
    margin-bottom: 40px;
  }

  &__btn {
    width: 240px;
    font-size: 16px;

    &--yes {
      margin-right: 15px;
    }

    &--no {
      background: $gr-pink;
      transition: all $slowdown;

      &:hover {
        background: $gr-pink-hover;
      }
    }
  }

  &__block {
    margin-bottom: 75px;
  }

  &__block-info {
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &__block-question {
    margin-bottom: 75px;
  }

  &__fix {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px;
    font-weight: 600;
    font-size: 18px;
    color: $default;
    border-radius: 10px;
    box-shadow: $shadow-large;
  }

  &__back {
    padding: 8px 20px;
    font-size: 16px;
    border-radius: 7px;

    svg {
      display: block;
      width: 14px;
      height: 14px;
      margin-right: 8px;
      fill: $white;
    }
  }

  @include laptop {
    &__btn {
      width: 160px;
    }

    &__question {
      font-size: 24px;
    }

    &__answer {
      font-size: 20px;
    }

    &__fix {
      flex-direction: column;
      align-items: flex-start;
    }

    &__back {
      margin-top: 15px;
    }
  }

  @include tablets {
    margin-bottom: 100px;

    &__block {
      margin-bottom: 40px;
    }

    &__title {
      margin-bottom: 20px;
    }

    &__question {
      margin-bottom: 5px;
      font-size: 16px;
    }
  
    &__phone {
      font-weight: 700;
      font-size: 22px;
      color: $accent;
    }


    &__lst {
      font-size: 16px;
    }

    &__answer {
      font-size: 18px;
    }
  }

  @include phones {
    &__btn {
      width: 135px;
      padding: 10px 20px;
  
      &--yes {
        margin-right: 5px;
      }
    }
  }
}

.block-info {
  position: relative;
  padding: 25px;
  overflow: hidden;
  border-radius: 15px;

  &--callback {
    background-color: #E8F4FF;
  }

  &--doc {
    padding-right: 200px;
    background-color: #E8F4FF;

    &::before {
      content: '';
      position: absolute;
      top: 25px;
      right: -40px;
      width: 210px;
      height: 290px;
      background-size: contain;
      background-position: center top;
      background-repeat: no-repeat;
      box-shadow: $shadow-large;
    }
  }

  &--guarantees {
    &::before {
      background-image: url(../img/thanks/guarantees.png);
    }
  }

  &--reso {
    &::before {
      background-image: url(../img/thanks/reso.png);
    }
  }

  &--attention {
    padding-bottom: 50px;
    padding-right: 210px;
    background-color: #FFF6E1;

    &::before {
      content: '';
      position: absolute;
      bottom: -50px;
      right: 0;
      width: 240px;
      height: 330px;
      background-image: url(../img/thanks/new_phone.png);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  &__head {
    position: relative;
    margin-bottom: 18px;
    padding-left: 40px;
  }

  &__icon {
    position: absolute;
    top: 0;
    left: 0;
    height: 26px;
    width: 26px;
    color: $white;

    &--circle {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $accent;
      border-radius: 50%;
    }

    &--triangle {
      height: 28px;
      width: 28px;
      background-image: url(../img/thanks/triangle.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 18px;
  }

  &__subtitle {
    margin-top: -5px;
    margin-bottom: 10px;
    font-weight: 400;
    color: $grey-2;
  }

  &__content {
    font-weight: 400;
    font-size: 16px;

    p {
      margin-bottom: 0;
      &.last {
        margin-top: 25px;
      }
    }
  }

  &__phone {
    font-weight: 700;
    font-size: 18px;
    color: $accent;
    white-space: nowrap;
  }

  @include laptop {
    position: relative;
    padding: 25px;
    overflow: hidden;
    border-radius: 15px;
  
    &--attention {
      padding-bottom: 200px;
      padding-right: 25px;
  
      &::before {
        bottom: -85px;
        right: 50%;
        width: 230px;
        height: 330px;
        transform: translateX(50%);
      }
    }

    &--doc {
      padding-bottom: 200px;
      padding-right: 25px;

      &::before {
        top: auto;
        bottom: -180px;
        right: 50%;
        width: 250px;
        height: 350px;
        transform: translateX(50%);
      }
    }
  }

  @include phones {
    padding: 15px;
    &--attention {
      padding-bottom: 200px;
    }
    &--doc {
      padding-bottom: 200px;
    }
  }
}

