.reviews-art {
  padding: 100px 0;

  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
    margin-left: -30px;
  }

  &__item {
    position: relative;
    display: block;
    width: calc(33.3% - 30px);
    margin-left: 30px;
    margin-bottom: 40px;
  }

  &__video {
    margin-bottom: 20px;
    overflow: hidden;
  }

  &__content {
    display: block;
    &:hover {
      .reviews-art__name {
        color: $accent;
      }
    }
  }

  &__name {
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 22px;
    color: $default;
    transition: color $fastdown;
  }

  &__text {
    height: 78px;
    overflow: hidden;
    margin-bottom: 20px;
    font-size: 14px;
    color: $grey-2;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;

    p {
      margin-bottom: 0;
    }
  }

  &__link {
    margin-top: auto;
    font-size: 16px;
    color: $accent;
  }
  
  &__btn {
    width: 200px;
    margin: 0 auto;
  }

  @include laptop {
    padding: 80px 0;

    &__name {
      font-size: 18px;
    }
  }

  @include tablets {
    padding-bottom: 120px;

    &__wrap {
      flex-direction: column;
      margin-left: 0;
    }

    &__item {
      width: 100%;
      margin-left: 0;
    }

    &__text {
      margin-bottom: 10px;
    }
  }
}