.price {
  &__lst {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include lst;
    margin-top: 40px;
  }

  &__item {
    width: 385px;
    overflow: hidden;
    background-color: $white;
    border-radius: 20px;
    box-shadow: $shadow-large;
  }

  @include desktop-1500 {
    &__item {
      width: 32%;
    }
  }

  @include laptop {
    &__lst {
      flex-direction: column;
      align-items: center;
    }

    &__item {
      width: 360px;
      margin-bottom: 20px;
    }
  }

  @include small {
    &__item {
      width: 100%;
    }
  }
}

.price-item {

  &__head {
    position: relative;
    padding: 25px;
    border-bottom: 1px solid #CECECE;

    &--accent {
      color: $white;
      background-color: $accent;

      &::before, &::after {
        display: none;
      }
    }

    &::before, &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    &::before {
      background-image: url(../img/bg.jpg);
      opacity: 0.2;
    }

    &::after {
      background-color: $white;
      opacity: 0.9;
    }
  }

  &__timer {
    display: flex;
    align-items: center;
    padding: 25px;
    background: #EEF3FF;
  }

  &__inner {
    position: relative;
    z-index: 1;
  }

  &__uptitle {
    margin-bottom: 10px;
    font-size: 18px;
  }

  &__price {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 32px;
    line-height: 1.1;

    &--old {
      position: relative;
      margin-left: 10px;
      font-weight: 600;
      font-size: 18px;
      color: $grey-2;

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 1px;
        background-color: $red;
        transform: translate(-50%, -50%) rotate(12deg);
      }
    }

    &--grey {
      color: #E3E2E2;
    }
  }

  &__note {
    font-size: 14px;
    color: $grey-2;

    &--grey {
      color: #E3E2E2;
    }
  }

  &__body {
    padding: 25px;
    color: $grey-2;
  }

  &__text {
    margin-bottom: 20px;
  }

  &__subtitle {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 18px;
    color: $default;
  }

  &__lst {
    margin-bottom: 20px;
    padding-left: 0;
    list-style-type: none;
    counter-reset: my-counter; 
  }

  &__elem {
    position: relative;
    margin-bottom: 10px;
    padding-left: 25px;

    &::before {
      position: absolute;
      left: 0;
      content: counter(my-counter);
      counter-increment: my-counter;
      color: $accent;
    }
  }

  &__btn {
    padding: 10px 20px;
    font-size: 14px;
    border-radius: 7px;
  }

  @include desktop {
    &__timer {
      flex-direction: column-reverse;
      align-items: flex-start;
    }

    &__uptitle {
      font-size: 16px;
    }

    &__price {
      font-size: 28px;

      &--old {
        font-size: 16px;
      }
    }
  }
  
  @include laptop {
    &__timer {
      flex-direction: row;
      align-items: center
    }

    &__uptitle {
      font-size: 18px;
    }

    &__price {
      font-size: 32px;

      &--old {
        font-size: 18px;
      }
    }
  }

  @include tablets {

    &__head, &__body {
      padding: 20px;
    }
    
    &__uptitle {
      margin-bottom: 10px;
    }

    &__price {
      margin-bottom: 2px;
      font-size: 26px;

      &--old {
        font-size: 18px;
      }
    }

    &__timer {
      padding: 20px;
    }
  }

  @include phones {

    &__uptitle {
      font-size: 16px;
    }

    &__price {
      font-size: 22px;

      &--old {
        font-size: 18px;
      }
    }

    &__text {
      font-size: 14px;
    }

    &__subtitle {
      font-size: 16px;
    }

    &__elem {
      font-size: 14px;
    }

    &__btn {
      width: 100%;
    }
  }

  @include small {
    &__timer {
      flex-direction: column-reverse;
      align-items: flex-start;
      padding: 12px 20px;
    }
  }
}