.expenses {
  margin-top: 100px;
  margin-bottom: 100px;

  &__inner {
    &--top {
      margin-top: 50px;
    }

    &--grey {
      position: relative;

      &::before, &::after {
        content: '';
        position: absolute;
        top: 0;
        width: 200%;
        height: 100%;
        background-color: $grey;
      }

      &::before {
        left: -200%;
      }

      &::after {
        right: -200%;
      }

      .expenses__call {
        background-color: $grey;
      }
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
    font-weight: 400;
    color: $grey-2;

    &--head {
      font-weight: 600;
      font-size: 18px;
      color: $default;
    }
  }

  &__call {
    padding: 45px 0;
    padding-right: 20px;

    &--first {
      width: 24%;
    }

    &--second {
      width: 15%;
    }

    &--third {
      width: 18%;
    }

    &--fourth {
      width: 18%;
    }

    &--fifth {
      width: 25%;
    }
  }

  @include desktop {
    margin-top: 80px;
    margin-bottom: 80px;

    &__inner {
      &--top {
        margin-top: 30px;
      }
    }

    &__row {
      
      font-size: 14px;
  
      &--head {
        font-size: 16px;
      }
    }

    &__call {
      padding: 30px 0;
      padding-right: 20px;
    }
  }

  @include laptop {
    margin-top: 60px;
    margin-bottom: 60px;

    &__row {
      font-size: 12px;
  
      &--head {
        font-size: 14px;
      }
    }

    &__call {
      padding: 20px 0;
      padding-right: 10px;
  
      &--first {
        width: 20%;
      }
  
      &--second {
        width: 17%;
      }
  
      &--third {
        width: 20%;
      }
  
      &--fourth {
        width: 18%;
      }
  
      &--fifth {
        width: 25%;
      }
    }
  }

  @include tablets {
    margin-top: 40px;
    margin-bottom: 0;

    &__wrap {
      padding-bottom: 40px;
      overflow-x: scroll;

    }

    &__inner {
      padding: 0 22px;

      &--grey {
        width: 100%;
        background-color: $grey;

        &::before, &::after {
          display: none;
        }
      }
    }
    
    &__call {
      padding: 20px 0;
      padding-right: 10px;

      &--first {
        min-width: 220px;
      }
  
      &--second {
        min-width: 140px;
      }
  
      &--third {
        min-width: 160px;
      }
  
      &--fourth {
        min-width: 170px;
      }
  
      &--fifth {
        min-width: 230px;
      }
    }
  }

  @include phones {
    &__container {
      padding: 0;
    }
  }
}