.hurry {
  &__lst {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include lst;
    margin-top: 60px;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 33%;
    padding-left: 100px;

    &:not(:last-child) {
      padding-right: 30px;
    }
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 70px;
    height: 85px;
    background-size: contain;
    background-position: left top;
    background-repeat: no-repeat;

    &--1 {
      background-image: url(../img/services/hurry/procuracy.png);
    }
    &--2 {
      top: -5px;
      width: 85px;
      height: 100px;
      background-image: url(../img/services/hurry/law.png);
    }
    &--3 {
      width: 95px;
      height: 95px;
      background-image: url(../img/services/hurry/fine.png);
    }

    &.lazy {
      background-image: none;
    }
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 18px;
  }

  &__desc {
    color: $grey-2;
  }

  &__wrap {
    position: relative;
    display: flex;
    flex-direction: column;
    min-height: 280px;
    margin-top: 60px;
    padding: 25px;
    background-color: $white;
    border-radius: 20px;
    box-shadow: $shadow-large;
  }

  &__inner {
    flex: 1 0 auto;
    align-self: stretch;
    display: flex;
    width: 100%;
    margin-bottom: 10px;
  }

  &__tab {
    width: 100%;
  }

  &__title {
    margin-bottom: 10px;
    padding-right: 140px;
    font-weight: 700;
    font-size: 26px;
  }

  &__text {
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;
  }

  &__variants {
    display: flex;
    flex-wrap: wrap;
    margin-left: -8px;
  }

  &__slides {
    position: absolute;
    top: 25px;
    right: 25px;
    font-weight: 700;
    font-size: 18px;
    color: $default;
  }

  &__nav {
    display: flex;
    margin-top: auto;
  }

  &__content {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  &__form {
    margin-top: auto;
    width: 100%;
  }

  &__btn {
    margin-right: 10px;
    padding: 10px 25px;
    font-size: 14px;
    line-height: 1.15;
    border-radius: 7px;

    &.disabled {
      opacity: 0.3;
    }

    &--prev {
      background: linear-gradient(180deg, #959fb9 0%, #818aa3 100%);
      box-shadow: 0px 4px 14px rgba(0, 37, 102, 0.18);

      &:hover {
        background: linear-gradient(180deg, #959fb9 0%, #818aa3 100%);
        box-shadow: 0px 4px 14px rgba(0, 37, 102, 0.18);
      }
    }
  }

  @include laptop {
    &__wrap {
      min-height: 360px;
    }
    &__lst {
      flex-direction: column;
      align-items: center;
    }
    &__item {
      width: 70%;
      margin-bottom: 30px;
    }
  }

  @include tablets {
    &__wrap {
      min-height: 370px;
    }

    &__item {
      width: 100%;
    }

    &__title {
      padding-right: 70px;
      font-size: 22px;
    }

    &__text {
      font-size: 16px;
    }
  }

  @include phones {
    &__lst {
      margin-top: 40px;
    }

    &__item {
      padding-top: 80px;
      padding-left: 0;

      &:not(:last-child) {
        padding-right: 0;
      }
    }

    &__image {
      height: 75px;
      width: 65px;

      &--3 {
        width: 95px;
      }
    }

    &__wrap {
      margin-top: 40px;
    }

    &__wrap {
      min-height: 330px;
      padding: 20px;
      padding-top: 40px;
    }

    &__title {
      margin-bottom: 5px;
      padding-right: 0;
      font-size: 18px;
    }

    &__text {
      margin-bottom: 20px;
      font-size: 14px;
    }

    &__slides {
      top: 18px;
      right: 20px;
      font-size: 14px;
    }

    &__radio {
      padding: 5px 15px;
      padding-left: 40px;

      .radio__label {
        font-size: 14px;
      }
    }
  }

  @include small {
    &__wrap {
      min-height: 430px;
    }
  }
}
