.book {
  position: relative;
  padding: 35px;
  border-radius: 10px;
  box-shadow: $shadow-large;

  &__head {
    margin-bottom: 45px;
  }
  &__title {
    margin-bottom: 10px;
    font-size: 22px;
  }
  &__name {
    display: flex;
    flex-direction: column;
    margin-bottom: 0;
    font-weight: 700;
    font-size: 26px;
    color: $grey-2;
  }
  &__author {
    margin-top: 10px;
    font-size: 18px;
  }

  &__link {
    position: relative;
    display: block;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 350px;
      height: 350px;
      background: $gr-grey;
      transform: translate(-50%, -50%);
      border-radius: 50%;
    }

    picture {
      position: relative;
    }

    img {
      display: block;
      width: 260px;
      margin: 0 auto;
      box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.4);
    }
  }

  &__social-title {
    margin-top: 50px;
    margin-bottom: 10px;
    font-size: 18px;
    color: $grey-2;
  }

  &__social-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__social-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(50% - 2px);
    margin-bottom: 4px;
    padding: 8px;
    font-size: 14px;
    line-height: 1.45;
    border-radius: 7px;

    svg {
      fill: $white;
      width: 18px;
      height: 18px;
      margin-right: 10px;
    }

    &--ok {
      background: linear-gradient(360deg, #EE8108 0%, #F9A549 100%);

      &:hover {
        background: linear-gradient(360deg, #EE8108 0%, #F9A549 100%);
        opacity: 0.85;
      }

      &:active {
        transform: scale(0.95);
      }
    }

    &--vk {
      background: linear-gradient(360deg, #2787F5 0%, #61A5F3 100%);

      &:hover {
        background: linear-gradient(360deg, #2787F5 0%, #61A5F3 100%);
        opacity: 0.85;
      }

      &:active {
        transform: scale(0.95);
      }
    }

    &--fb {
      background: linear-gradient(360deg, #2787F5 0%, #61A5F3 100%);

      &:hover {
        background: linear-gradient(360deg, #2787F5 0%, #61A5F3 100%);
        opacity: 0.85;
      }

      &:active {
        transform: scale(0.95);
      }
    }

    &--mail {
      background: linear-gradient(180deg, #5B7FB5 0%, #345C99 100%);

      &:hover {
        background: linear-gradient(180deg, #5B7FB5 0%, #345C99 100%);
        opacity: 0.85;
      }

      &:active {
        transform: scale(0.95);
      }
    }

  }

  @include desktop-1500 {
    padding: 25px;

    &__title {
      font-size: 20px;
    }

    &__link {
      transform: scale(0.8);
    }

    &__social-link {
      font-size: 13px;
    }
  }

  @include desktop {
    &__head {
      margin-bottom: 0;
    }

    &__link {
      transform: scale(0.6);
    }

    &__social-title {
      margin-top: 0;
    }

    &__social-link {
      width: 100%;
    }
  }

  @include laptop {
    &__title {
      font-size: 18px;
    }
    &__name {
      font-size: 22px;
    }
    &__author {
      font-size: 14px;
    }
  }

  @include tablets {
    margin-bottom: 40px;

    &__link {
      transform: scale(0.8);
    }
  }

  @include phones {
    &__link {
      transform: scale(0.7);
    }
  }
}
