.contacts {
  position: relative;
  margin-top: 100px;
  margin-bottom: 120px;

  &--services {
    margin: 0;
    padding-top: 100px;
    padding-bottom: 120px;
  }
  
  // &::before {
  //   content: '';
  //   position: absolute;
  //   top: 0;
  //   left: 0;
  //   z-index: 1;
  //   width: 100%;
  //   height: 100%;
  //   background: linear-gradient(135deg, #ffffff 15%, #ffffff 85%);
  //   opacity: 0.9;
  // }

  &__title {
    display: none;
  }

  &__video {
    position: absolute;
    top: 50%;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    object-position: center;
    transform: translateY(-50%);
  }

  &__title {
    margin-bottom: 60px;
    font-weight: 700;
    font-size: 34px;
    line-height: 1.45;
    text-align: center;
    display: none;
  }

  &__wrap {
    position: relative;
    z-index: 2;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__block {
    &--top {
      width: 100%;
      height: 400px;
      margin-bottom: 60px;
    }

    &--content {
      width: 490px;
      padding-right: 35px;
    }

    &--map, &--info {
      width: 700px;
    }
  }

  &__inner {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__subtitle {
    margin-bottom: 5px;
    font-size: 16px;
    color: $grey-2;
  }

  &__text {
    margin-bottom: 12px;
    font-weight: 700;
    font-size: 26px;
    line-height: 1.4;
    color: $default;
  }

  &__metro-list {
    display: flex;
    justify-content: flex-start;
    @include lst;

  }
  &__metro-item {
    position: relative;
    padding: 2px 10px;
    padding-left: 31px;
    line-height: 1.4;
    border-radius: 50px;

    &:not(:last-child) {
      margin-right: 5px;
    }

    &--purple {
      background-color: #984AA9;
    }
    &--orange {
      background-color: #EC8A33;
    }
    &--blue {
      background-color: #1389D1;
    }

    svg {
      position: absolute;
      top: 50%;
      left: 10px;
      width: 16px;
      height: 16px;
      fill: $white;
      transform: translateY(-50%);
    }
  }
  &__metro-text {
    font-size: 14px;
    color: $white;
  }

  &__link {
    font-weight: 700;
    font-size: 26px;
    color: $default;
  }

  &__social {
    margin-top: 15px;

    .social__item {
      &:not(:last-child) {
        margin-right: 22px;
      }
    }

    .social__link {
      width: 26px;
      height: 26px;

      svg {
        width: 26px;
        height: 26px;
      }
    }
  }

  @include desktop-1500 {
    &__block {
      &--info {
        width: 600px;
      }
      &--map {
        width: 50%;
      }
    }
  }

  @include desktop {
    &__block {
      width: 50%;

      &--top {
        width: 100%;
      }
    }

    &__title {
      font-size: 32px;
    }
  }

  @include laptop {
    margin-top: 70px;
    margin-bottom: 80px;

    &__title {
      font-size: 30px;
    }

    &__wrap {
      flex-direction: column;
    }

    &__block {
      width: 100%;

      &--content {
        display: flex;
        flex-direction: column;
        margin-bottom: 50px;
        padding-right: 0;
      }

      &--map {
        height: 440px;
      }
    }

    &__inner {
      &--double {
        width: 50%;
      }
    }
  }

  @include tablets {
    margin-top: 30px;
    margin-bottom: 40px;

    &--services {
      margin-top: 0;
      margin-bottom: 0;
      padding-top: 80px;
      padding-bottom: 70px;
    }

    &__title {
      display: block;
      margin-bottom: 40px;
      text-align: left;
    }

    &__block {
      &--top {
        margin-bottom: 40px;
      }
    }
    
    &__inner {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    &__subtitle {
      font-weight: 400;
      font-size: 16px;
    }

    &__text {
      font-size: 24px;
    }

    &__link {
      font-size: 24px;
    }
  }

  @include phones {
    &__title {
      margin-bottom: 20px;
      font-size: 22px;
    }

    &__inner {
      margin-bottom: 20px;

      &--double {
        width: 100%;
      }
    }

    &__block {
      &--top {
        height: 415px;
        margin-bottom: 30px;
      }

      &--map {
        height: 340px;
      }
    }

    &__subtitle {
      font-size: 14px;
    }

    &__text {
      font-size: 20px;
    }

    &__link {
      font-size: 20px;
    }

    &__metro-text {
      font-size: 12px;
    }
  }
}

.map {
  position: relative;
  padding-top: 50px;

  &--top {
    padding-top: 0;
    padding-bottom: 50px;
  }

  &__wrap {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
    background-color: $grey;
    border: 1px solid $grey;
    border-radius: 8px;
  }

  &__loader {
    position: absolute;
    top: 50%;
    left: 50%;
    font-weight: 600;
    font-size: 22px;
    color: $grey-2;
    white-space: nowrap;
    transform: translate(-50%, -50%);
  }

  &__block {
    position: relative;

    &--google {
      display: none;
      height: 100%;

      iframe {
        width: 100%;
        height: 100%;
      }
    }
    &--yandex {
      display: block;
      height: 100%;

    }
    &--2gis {
      display: none;

      iframe {
        width: 100%;

        .leaflet-top.leaflet-left {
          display: none;
        }
      }
    }
  }

  @include tablets {
    padding-top: 40px;

    &--top {
      padding-bottom: 0;
    }
  }

  @include phones {
    &__loader {
      font-size: 18px;
    }
  }
}

.btns-map {
  position: absolute;
  top: 0;
  left: 0;

  &--bottom {
    top: auto;
    bottom: 0;  
  }

  &__btn {
    display: none;
    padding: 4px 12px;
    padding-right: 28px;
    font-weight: 400;
    font-size: 14px;
    color: $color-default-white;
    text-align: center;
    background: linear-gradient(
        360deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.2) 100%
      ),
      #3c70f4;
    border: none;
    border-radius: 5px;
    transition: $slowdown;
    cursor: pointer;

    &:hover {
      color: $color-default-white;
      background: $gr-blue-hover;
      text-decoration: none;
    }

    &:active {
      color: $color-default-white;
      background: $gr-blue-hover;
      text-decoration: none;
    }
  }

  &__arrow {
    position: absolute;
    top: 50%;
    right: 12px;
    border-style: solid;
    border-width: 9px 5px 0 5px;
    border-color: $white transparent transparent transparent;
    transform: translateY(-50%);

    &.open {
      transform: translateY(-50%) rotate(180deg);
    }
  }

  &__list {
    position: static;
    display: flex;
    width: auto;
    @include lst;
  }

  &__item {
    margin-right: 5px;
    padding: 6px 20px;
    font-weight: 400;
    font-size: 14px;
    color: $color-default-white;
    text-align: center;
    background: linear-gradient(
        360deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.2) 100%
      ),
      #3c70f4;
    border: none;
    border-radius: 8px;
    box-shadow: $shadow-small;
    transition: $slowdown;

    &:hover {
      color: $white;
    }

    .radio__new {
      display: none;
      border: 1px solid $accent;
      box-shadow: none;
    }

    .radio__label {
      background: none;
    }

    &.active {
      background: $gr-blue-hover;
    }
  }

  @include tablets {

    &--bottom {
      top: 0;
      bottom: auto;  
    }

    &__btn {
      display: inline-block;
    }

    &__list {
      position: absolute;
      top: 30px;
      left: 0;
      z-index: 10;
      display: none;
      padding: 20px;
      background-color: $white;
      border-radius: 10px;
      box-shadow: $shadow-small;

      &--show {
        display: block;
      }
    }

    &__item {
      padding: 3px 0;
      padding-left: 30px;
      color: $grey-2;
      background: none;
      box-shadow: none;
      cursor: pointer;
  
      &:not(:last-child) {
        margin-bottom: 10px;
      }

      &:hover {
        color: $accent;
      }

      &.active {
        background: none;
      }

      .radio__new {
        display: block;
      }
    }
  }
}

.contacts-info {
  &__title {
    font-weight: 500;
    font-size: 26px;
  }

  @include tablets {
    &__title {
      font-size: 22px;
    }
  }

  @include phones {
    &__title {
      font-size: 18px;
    }
  }
}