.footer {
  position: relative;
  padding: 65px 0;
  background-color: $grey;

  &--mfc {
    padding: 30px 0;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 70px;

    &--last {
      margin-bottom: 0;
    }
  }

  &__col {
    width: 25%;
    padding-right: 10px;

    &--big {
      width: 50%;
      padding-right: 8%;
    }
  }

  &__title {
    margin-bottom: 10px;
    font-size: 26px;
  }

  &__subtitle {
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 14px;
    color: $grey-2;
  }

  &__desc {
    margin-bottom: 35px;
    font-weight: 400;
    font-size: 14px;
    color: $grey-2;
  }

  &__insurance {
    margin-top: 15px;
  }

  @include desktop-1500 {
    &__col {
      &--big {
        padding-right: 20px;
      }
    }
  }

  @include desktop {
    padding: 50px 0 30px;

    &__row {
      margin-bottom: 0;
    }

    &__col {
      width: 50%;
      margin-bottom: 40px;
  
      &--big {
        width: 100%;
        padding-right: 10px;
      }
    }
  }

  @include tablets {
    padding: 60px 0;

    &__row {
      margin-bottom: 0;
    }

    &__col {
      width: 100%;
      padding-right: 0;
  
      &--big {
        width: 100%;
      }
    }
  }

  @include phones {

    &__col {
      &--big {
        margin-bottom: 30px;
      }

      &--first {
        margin-bottom: 20px;
      }
    }

    &__title {
      margin-bottom: 5px;
      font-size: 18px;
    }

    &__desc {
      margin-bottom: 20px;
      font-size: 12px;
    }
  }
}

.categories {
  display: flex;
  flex-direction: column;
  @include lst;
  margin-top: 25px;

  &__item {
    margin-bottom: 25px;
  }

  @include phones {
    margin-top: 20px;

    &__item {
      margin-bottom: 20px;
      font-size: 14px;
    }
  }
}

.popular {
  display: flex;
  flex-direction: column;
  @include lst;
  margin-top: 15px;

  &__item {
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__link {
    position: relative;
    display: block;
    padding-left: 40px;

    &:hover {
      .popular__title {
        color: rgba($accent, 0.8);
      }
    }
  }

  &__media {
    position: absolute;
    top: 2px;
    left: 0;
    width: 30px;
    height: 30px;
    overflow: hidden;
    margin: 0;
    border-radius: 2px;

    img {
      object-fit: cover;
      object-position: center;
      height: 100%;
      margin: 0;
    }
  }

  &__title {
    display: block;
    min-height: 40px;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.5;
    color: $default;
    transition: color $fastdown;
  }

}

.footer-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  @include lst;

  &__item {
    width: 60px;
    height: 60px;
    margin-top: 10px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;

    &:not(:last-child) {
      margin-right: 26px;
    }

    &--1 {
      background-image: url(../img/icons/emblem_1-min.png);
    }
    &--2 {
      background-image: url(../img/icons/emblem_2-min.png);
    }
    &--3 {
      background-image: url(../img/icons/emblem_3-min.png);
    }

    &--mastercard {
      background-image: url(../img/icons/mastercard-min.png);
    }
    &--visa {
      background-image: url(../img/icons/visa-min.png);
    }
    &--mir {
      width: 65px;
      background-image: url(../img/icons/mir-min.png);
    }
  }
}

.copyright {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  @include lst;

  &--center {
    align-items: center;
  }

  &__item {
    margin-bottom: 9px;
    font-weight: 400;
    font-size: 14px;
    color: $grey-2;

    &:not(:last-child) {
      margin-bottom: 9px;
    }

    &--center {
      font-size: 16px;
      text-align: center;
    }
  }

  @include desktop {
    &__item {
      &:not(:last-child) {
        margin-bottom: 5px;
      }

      &--center {
        text-align: center;
      }
    }
  }
}