.selection {
  &__wrap {
    display: flex;
    justify-content: space-between;
    @include lst;
    margin-top: 60px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 31%;
  }

  &__foto {
    position: relative;
    width: 280px;
    height: 280px;
    margin-top: 40px;
    margin-bottom: 20px;
    border-radius: 50%;
    background: $gr-grey;
    
    img {
      position: absolute;
      bottom: 0;
      left: 50%;
      z-index: 1;
      width: 280px;
      transform: translateX(-50%);
    }
  }

  &__number {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 10;
    width: 100px;
    height: 100px;
    font-weight: 700;
    font-size: 50px;
    line-height: 100px;
    color: $accent;
    text-align: center;
    background-color: $white;
    border-radius: 50%;
    box-shadow: $shadow-large;
  }

  &__content {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  &__name {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 18px;
  }

  &__text {
    color: $grey-2;
  }

  @include laptop {
    &__foto {
      width: 220px;
      height: 220px;
      margin-top: 40px;
      margin-bottom: 20px;

      
      img {
        width: 220px;
      }
    }
  
    &__number {
      z-index: 10;
      width: 70px;
      height: 70px;
      font-size: 40px;
      line-height: 70px;
    }

    &__name {
      font-size: 15px;
    }

    &__text {
      font-size: 14px;
    }
  }

  @include tablets {
    &__wrap {
      flex-direction: column;
      margin-top: 40px;
    }

    &__item {
      width: 100%;
      margin-bottom: 30px;
    }

    &__foto {
      width: 130px;
      height: 130px;
    }

    &__number {
      width: 50px;
      height: 50px;
      font-size: 32px;
      line-height: 50px;
    }

    &__name {
      margin-bottom: 10px;
      font-size: 16px;
    }
  }

  @include phones {
    &__foto {
      width: 100px;
      height: 100px;
    }

    &__number {
      width: 35px;
      height: 35px;
      font-size: 22px;
      line-height: 35px;
    }
  }
}