.staff {
  padding-top: 120px;
  padding-bottom: 40px;
  color: $white;
  background-color: #5381F6;

  &__text {
    margin-top: 20px;
    font-weight: 400;
    font-size: 18px;
  }

  &__wrap {
    position: relative;
    left: 50%;
    
    transform: translateX(-50%);

    &--desktop {
      display: grid;
      grid-template-rows: repeat(6, 1fr);
      grid-template-columns: repeat(17, 1fr);
      grid-row-gap: 18px;
      grid-column-gap: 18px;
      width: 1850px;
      margin-top: 60px;
    }
    &--mobile {
      display: none;
    }
  }

  &__tooltip {
    position: absolute;
    z-index: -1;
    top: calc(100% - 15px);
    left: 50%;
    display: none;
    flex-direction: column;
    align-items: center;
    width: 320px;
    padding: 20px;
    text-align: center;
    background-color: $white;
    border-radius: 8px;
    box-shadow: $shadow-small;
    opacity: 0;
    transform: translate(-50%, 50px);
    transition: opacity $fastdown, transform 0.4s ease-out;
  }

  &__name {
    font-size: 18px;
    color: $default;
  }

  &__post {
    color: $grey-2;
  }

  &__img {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 50%;
    background-color: $white;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.75;
    -webkit-opacity: 0.75;

    &--op-01 {
      opacity: 0.1;
      -webkit-opacity: 0.1;
    }
    &--op-25 {
      opacity: 0.25;
      -webkit-opacity: 0.25;
    }
    &--op-55 {
      opacity: 0.55;
      -webkit-opacity: 0.55;
    }
    &--op-no {
      opacity: 1;
      -webkit-opacity: 1;
    }
  }

