.menu {

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;

    &--footer {
      justify-content: center;
    }
  }

  &__item {
    position: relative;
  }

  &__link {
    position: relative;
    display: block;
    padding: 20px 0;
    font-size: 16px;
    color: $default;
    white-space: nowrap;
    transition: color $slowdown;
    cursor: pointer;

    &.active {
      color: $accent;

      span {
        position: relative;
        &::before {
          content: '';
          position: absolute;
          left: 0;
          bottom: -1px;
          height: 1px;
          width: 100%;
          background-color: $accent;
        }
      }

      .menu__arrow {
        position: absolute;
        border-color: $accent transparent transparent transparent;
      }
      
      svg {
        fill: $accent;
      }
    }

    &--submenu {
      margin-right: 15px;
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 160%;
        height: 100%;
        transform: translate(-40%, -50%);
      }
    }

    &--sub {
      padding: 0;
      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }

    &--footer {
      padding: 20px 25px;
    }

    &--lk {
      position: relative;
      color: $grey-2;
    }

    svg {
      position: absolute;
      left: 0;
      top: 50%;
      display: none;
      width: 18px;
      height: 18px;
      fill: $default;
      transform: translateY(-50%);
    }

    &:hover {
      color: $accent;

      svg {
        fill: $accent;
      }

      .menu__arrow {
        border-color: $accent transparent transparent transparent;
      }
    }
  }

  &__submenu {
    position: absolute;
    top: calc(100% - 10px);
    left: 50%;
    @include lst;
    padding: 20px;
    background-color: $white;
    border-radius: 10px;
    box-shadow: $shadow-large;
    transform: translateX(-50%);

    .menu__item {
      &:not(:last-child) {
        .menu__link {
          margin-bottom: 15px;
        }
      }
    }
  }

  &__arrow {
    position: absolute;
    top: 50%;
    right: -35%;
    border-style: solid;
    border-width: 8px 4px 0 4px;
    border-color: $default transparent transparent transparent;
    transform: translateY(-50%);
    transition: border-color $fastdown;

    &.open {
      transform: translateY(-50%) rotate(180deg);
    }
  }

  .beta {
    position: absolute!important;
    top: 8px;
    right: -2px;
    padding: 2px 3px;
    font-weight: 600;
    font-size: 10px;
    line-height: 1;
    color: $white;
    background: $gr-blue;
    border-radius: 2px;
  
    &--scroll {
      top: 1px;
    }

    &::before {
      display: none;
    }
  
    @include laptop {
      top: 4px;
      right: -4px;
  
      &--scroll {
        top: 1px;
      }
    }
  
    @include tablets {
      right: -25px;
    }
  }
  
  @include desktop {
    &__link {
      font-size: 15px;
    }
  }

  @include laptop {
    &__link {
      padding: 15px 0;
      font-size: 12px;

      &--footer {
        padding: 15px 10px;
      }

      &--sub {
        padding: 0;
      }
    }

    &__submenu {
      .menu__item {
        &:not(:last-child) {
          .menu__link {
            margin-bottom: 10px;
          }
        }
      }
    }
  }

  @include tablets {
    margin: 0 auto;
    margin-bottom: 40px;

    &__list {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 30px;
    }

    &__item {
      &--lk {
        align-self: flex-start;
      }
    }

    &__link {
      padding-left: 25px;
      font-size: 14px;

      svg {
        display: block;
      }

      &--submenu {
        margin-right: 0;
      }

      &--sub {
        margin-bottom: 10px;
        padding-left: 0;
      }

      &.active {
        .menu__arrow {
          position: relative;
        }
      }
    }

    &__submenu {
      position: static;
      display: flex;
      flex-direction: column;
      padding: 0;
      padding-left: 25px;
      background: none;
      border-radius: 0;
      box-shadow: none;
      transform: translateX(0);

      .menu__item:not(:last-child) .menu__link {
        margin-bottom: 15px;
      }
    }

    &__arrow {
      position: relative;
      right: -4px;
      display: inline-block;
      width: 8px;
      transform: translateY(-1px);

      &.open {
        transform: translateY(-1px) rotate(180deg);
      }
    }

    &__info {

      .copyright {
        align-items: flex-start;
        text-align: left;

        &__item {
          &:not(:last-child) {
            margin-bottom: 10px;
          }
        }
      }

      .links-header {
        display: flex;
        align-items: flex-start;
        margin-bottom: 40px;
      }

      .link--contact {
        font-size: 22px;
      }

      .social {
        display: block;
        margin-bottom: 60px;

        &--mfc {
          display: block;
        }
      }
    }

    .logo {
      margin-bottom: 30px;
    }
  }
}

.toggle-menu {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  margin: 0;
  padding: 0;
  background-color: $grey;
  border: 1px solid $white;
  border-radius: 8px;
  box-shadow: $shadow-small;
  cursor: pointer;

  span {
    width: 40px;
    height: 40px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    &.open {
      background-image: url(../img/icons/menu.svg);
    }
    
    &.close {
      background-image: url(../img/icons/menu-close.svg);
    }
  }

  &--close {
    background: none;
    border: none;
    box-shadow: none;
  }
}



.hide {
  display: none;
}