.fault {

  &__lst {
    display: flex;
    justify-content: space-between;
    @include lst;
    margin-top: 60px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 33%;

    &:not(:first-child) {
      margin-left: 30px;
    }
  }

  &__icon {
    width: 100px;
    height: 100px;
    margin-bottom: 20px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

    &--1 {
      background-image: url(../img/services/fault/debt.svg);
    }
    &--2 {
      background-image: url(../img/services/fault/zoom.svg);
    }
    &--3 {
      background-image: url(../img/services/fault/handcuffs.svg);
    }
  }

  &__name {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 18px;
  }

  &__desc {
    color: $grey-2;
  }

  @include laptop {
    &__lst {
      flex-direction: column;
      align-items: center;
    }

    &__item {
      width: 70%;
      margin-bottom: 30px;

      &:not(:first-child) {
        margin-left: 0;
      }
    }
  }

  @include tablets {
    &__lst {
      align-items: flex-start;
    }
  }

  @include phones {
    &__lst {
      margin-top: 40px;
    }

    &__item {
      width: 100%;
    }

    &__icon {
      width: 70px;
      height: 70px;
    }
  }
}