.dogovor {

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 60px;
  }

  &__block {
    &--image {
      width: 700px;
    }

    &--content {
      width: 490px;
    }
  }

  &__lst {
    list-style-type: none;
    margin: 0;
    padding: 0;
    counter-reset: dg-counter; 
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 32px;

    &:not(:last-child) {
      margin-bottom: 40px;
    }

    &::before {
      position: absolute;
      left: 0;
      content: counter(dg-counter) ".";
      counter-increment: dg-counter;
      font-weight: 700;
      font-size: 18px;
      color: $accent;
    }
  }

  &__name {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 18px;
  }

  &__point {
    margin-bottom: 10px;
    color: $grey-2;
  }

  &__promo {
    margin-top: 40px;
    margin-bottom: 20px;
    font-weight: 700;
    color: $default;
  }

  &__link {
    font-weight: 400;
    font-size: 18px;

    svg {
      width: 15px;
      height: 15px;
      fill: $accent;
    }
  }

  @include desktop {
    &__block {
      &--image {
        width: 420px;
      }
    }
  }

  @include laptop {
    &__block {
      &--image {
        width: 40%;
      }
      &--content {
        width: 60%;
      }
    }
  }

  @include tablets {
    &__wrap {
      flex-direction: column;
      margin-top: 30px;
    }

    &__block {
      &--image {
        width: 100%;
        margin-bottom: 30px;
        background-size: contain;
      }
      &--content {
        width: 100%;
      }
    }
  }

  @include phones {
    &__item {
      padding-left: 25px;

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    &__name {
      margin-bottom: 10px;
    }
  }
}