.breadcrumbs {
  margin: 25px 0;

  &__container {
    position: relative;
  }
}

#breadcrumbs {
  margin-bottom: 30px;
  font-size: 12px;
  color: rgba($grey-2, .5);

  a {
    position: relative;
    margin-right: 15px;
    padding-right: 20px;
    color: rgba($grey-2, .5);
    white-space: nowrap;

    &:hover {
      color: $accent;
    }

    &::before {
      content: '';
      position: absolute;
      top: 25%;
      right: 0;
      width: 5px;
      height: 5px;
      font-size: 6px;
    }
  }

  strong {
    font-weight: 400;
    color: rgba($grey-2, 1);
  }

  @include tablets {
    color: rgba($grey-2, .7);

    a {
      margin-right: 10px;
      padding-right: 15px;
      color: $accent;

      &:hover {
        color: rgba($accent, 0.6);
      }

      &:active {
        color: rgba($accent, 0.6);
      }

      &::before {
        color: rgba($grey-2, .5);
      }
    }

    strong {
      color: $accent;
    }
  }
}