.articles {
  margin-top: 100px;
  margin-bottom: 100px;

  &__container {
    position: relative;
  }

  &__wrap {
    margin-top: 10px;
  }

  &__block {
    &--settings {
      margin-top: 10px;
    }

    &--posts {
      margin-top: 30px;
    }

    &--hidden {
      display: none;
    }
  }

  &__wrap {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -30px;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-self: stretch;
    flex: 0 1 auto;
    width: calc(33.3% - 30px);
    // height: 460px;
    min-height: 460px;
    margin-bottom: 40px;
    margin-left: 30px;

    &:nth-child(15n + 1) {
      width: calc(66.6% - 30px);
      overflow: hidden;
      border-radius: 10px;

      .articles {
        &__image {
          position: absolute;
          width: 100%;
          height: 100%;
          padding-top: 0;
        }

        &__content {
          height: 100%;
          margin-top: auto;
          padding: 25px;
        }

        &__header {
          margin-top: auto;
        }

        &__link {
          height: 100%;

          &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: 2;
            width: 100%;
            height: 100%;
          }

          &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            width: 100%;
            height: 100%;
            background: linear-gradient(
              180deg,
              rgba(0, 0, 0, 0) 30%,
              rgba(0, 0, 0, 0.54) 80%
            );
          }

          &:hover {
            .articles {
              &__title {
                color: #90AFFD;
              }
            }
          }
        }

        &__title {
          color: $white;
        }

        &__text {
          height: 46px;
          color: #d7deef;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }

        &__data {
          position: relative;
          z-index: 1;
          color: #b5bdd1;
        }

        &__footer {
          margin: 25px;
          margin-top: 0;
        }
      }
    }

    &:nth-child(15n + 9) {
      flex: 1 1 100%;
      flex-direction: row-reverse;
      width: 100%;

      .articles {
        &__link {
          flex: 1 0 385px;
          max-width: calc(33.3% - 20px);
          margin-left: 30px;
        }

        &__block-hide {
          flex: 0 1 auto;
        }

        &__link {
          position: relative;
        }

        &__footer {
          position: absolute;
          bottom: 0;
          right: 0;
          width: calc(33% - 20px);
        }
      }
    }
  }

  &__link {
    display: flex;
    flex-direction: column;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    &:hover {
      .articles {
        &__title {
          color: $accent;
        }
      }
    }
  }

  &__block-hide {
    flex: 0 1 auto;

    &:empty {
      display: none;
    }
  }

  &__image {
    position: relative;
    width: 100%;
    padding-top: 65%;
    overflow: hidden;
    background-color: $grey;
    border-radius: 10px;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      min-height: 100%;
      object-fit: cover;
      object-position: center;
      transition: opacity $fastdown;
    }
  }

  &__content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 15px 0 0;
  }

  &__header {
    width: 100%;
  }

  &__title {
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 22px;
    line-height: 1.4;
    color: $default;
    transition: color $fastdown;
  }

  &__text {
    height: 68px;
    margin-bottom: 5px;
    overflow: hidden;
    font-size: 16px;
    color: $grey-2;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    margin-top: auto;
  }

  &__taxonomies {
    position: relative;
    z-index: 2;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;

    a {
      margin-right: 10px;
      font-size: 12px;
      color: #5381f6;

      &:hover {
        opacity: 0.85;
      }
    }
  }

  &__data {
    align-self: flex-end;
    font-size: 12px;
    color: $grey-2;
  }

  &__item-hide {
    height: 100%;
    margin-left: 25px;
    overflow: hidden;
    border-radius: 8px;
  }

  @include laptop {
    margin-top: 80px;
    margin-bottom: 80px;

    &__wrap {
      margin-left: -20px;
    }

    &__item {
      width: calc(50% - 20px);
      min-height: 400px;
      margin-bottom: 30px;
      margin-left: 20px;

      &:nth-child(15n + 1) {
        width: calc(50% - 20px);

        .articles {
          &__content {
            padding-bottom: 10px;
          }
        }
      }

      &:nth-child(15n + 9) {
        .articles {
          &__link {
            flex: 1 0 385px;
            max-width: calc(50% - 10px);
            margin-left: 20px;
          }

          &__footer {
            width: calc(50% - 10px);
          }
        }
      }
    }

    &__title {
      font-size: 18px;
    }
    &__text {
      font-size: 15px;
    }
  }

  @include tablets {
    margin-top: 30px;
    margin-bottom: 60px;

    .title-page {
      display: none;
    }

    &__wrap {
      margin-left: 0;
    }

    &__item {
      width: 100%;
      margin-bottom: 40px;
      margin-left: 0;

      &:nth-child(15n + 1) {
        width: 100%;

        &::before {
          display: none;
        }

        .articles {
          &__image {
            position: relative;
            padding-top: 65%;
            overflow: hidden;
            border-radius: 10px;
          }

          &__content {
            height: auto;
            margin-top: 10px;
            padding: 0;
          }

          &__link {
            &::after {
              display: none;
            }

            &:hover {
              .articles {
                &__title {
                  color: $accent;
                }
              }
            }
          }

          &__title {
            margin-top: 0;
            color: $default;
          }

          &__text {
            height: 60px;
            color: $grey-2;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
          }

          &__data {
            color: $grey-2;
          }

          &__footer {
            margin: 0;
          }
        }
      }

      &:nth-child(15n + 9) {
        flex-direction: column-reverse;

        .articles {
          &__link {
            position: static;
            flex: auto;
            max-width: 100%;
            margin-left: 0;
            margin-top: 40px;
          }

          &__footer {
            order: -1;
            position: static;
            width: 100%;
          }
        }
      }
    }

    &__text {
      height: 60px;
      margin-bottom: 10px;
      font-size: 14px;
    }

    &__taxonomies {
      display: none;
    }
  }

  @include phones {
    &:nth-child(15n + 1) {
      .articles {
        &__image {
          height: auto;
        }
      }
    }

    &__item {
      min-height: 360px;
    }
  }
}

//  
.yandex-zen {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 55px;
  color: $white;
  background: $gr-blue;
  border-radius: 8px;

  &__header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 35px;
  }

  &__logo {
    width: 200px;
    height: 38px;
    margin-bottom: 4px;

    svg {
      width: 200px;
      height: 38px;
    }
  }

  &__subscribers {
    font-weight: 400;
    font-size: 16px;
    color: #d7deef;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
  }

  &__message {
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 18px;
  }

  &__link {
    margin-top: auto;
    padding: 15px 30px;
    font-size: 14px;
    background: $gr-pink;

    &:hover {
      background: $gr-pink-hover;
    }
  }

  @include desktop {
    padding: 40px;
  }

  @include laptop {
    padding: 30px;
  }

  @include tablets {
    min-height: 350px;
    padding: 25px;

    &__header {
      margin-bottom: 25px;
    }

    &__message {
      margin-bottom: 20px;
      font-size: 18px;
    }

    &__link {
      padding: 10px 25px;
      border-radius: 7px;
    }
  }

  @include phones {
    &__header {
      margin-bottom: 15px;
    }
    &__message {
      font-size: 16px;
    }
  }
}

//   
.pagination {
  width: 100%;
  margin: 0 auto;
}

.nav-links {
  display: flex;
  justify-content: center;
}

.page-numbers {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin: 0 4px;
  font-weight: 700;
  font-size: 11px;
  color: $accent;
  text-align: center;
  background-color: $white;
  border-radius: 50%;
  transition: $fastdown;

  &:hover {
    background-color: #eaf3fc;
  }

  &.current {
    background-color: #eaf3fc;
  }

  &.dots {
    background: none;
    cursor: default;
  }
}

.prev,
.next {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    border-top: 5px solid transparent;
    border-right: 8px solid $accent;
    border-bottom: 5px solid transparent;
    transform: translate(-50%, -50%);
  }

  &:hover {
    background-color: #f3f4f4;
  }
}

.next {
  &::before {
    transform: translate(-50%, -50%) rotate(180deg);
  }
}
