.new {
  &__description {
    margin-bottom: 20px;
    font-size: 22px;
    color: $grey-2;
  }

  &__information {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-bottom: 20px;

    &--mmedia {
      padding-right: 100px;
    }
  }

  &__info {
    display: flex;
    justify-content: flex-start;
    @include lst;
    margin-right: 5px;
  }

  &__info-item {
    margin-right: 10px;
    font-size: 14px;
    color: $grey-2;
  }

  &__taxonomies {
    display: flex;
    flex-wrap: wrap;

    span {
      font-size: 14px;
      color: $grey-2;

      &:not(:last-child) {
        margin-right: 15px;
      }
    }
  }

  &__mmedia {
    position: absolute;
    top: 0;
    right: 0;
    width: 100px;
    height: 20px;
    background-size: contain;
    background-position: right center;
    background-repeat: no-repeat;

    &--kp {
      background-image: url(../img/news/KP.svg);
    }
    &--kom {
      background-image: url(../img/news/Kommersant.svg);
    }
    &--perv {
      background-image: url(../img/news/Pervyj.svg);
    }
    &--vedom {
      background-image: url(../img/news/Vedomosti.svg);
    }
    &--inter {
      background-image: url(../img/news/Interfax.svg);
    }
    &--poss {
      background-image: url(../img/news/Rossiya-24.svg);
    }

    img {
      height: 100%;
    }
  }

  &__content {
    font-weight: 400;
    font-size: 18px;

    :last-child {
      margin-bottom: 0;
    }

    p {
      margin-bottom: 20px;
    }

    blockquote {
      position: relative;
      margin: 0;
      margin-bottom: 20px;
      padding: 25px;
      padding-left: 70px;
      font-size: 22px;
      color: $grey-2;
      background-color: $grey;
      border-radius: 10px;

      &::before {
        content: '';
        position: absolute;
        left: 25px;
        top: 25px;
        width: 30px;
        height: 20px;
        background-image: url(../img/icons/quotes.svg);
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
      }
    }

    ul {
      @include lst;
      margin-bottom: 20px;

      li {
        position: relative;
        padding-left: 20px;

        &:not(:last-child) {
          margin-bottom: 10px;
        }

        &::before {
          content: '';
          position: absolute;
          top: 10px;
          left: 4px;
          width: 5px;
          height: 5px;
          background-color: $accent;
          border-radius: 50%;
        }
      }
    }
  }

  &__foto {
    position: relative;
    width: 100%;
    margin-bottom: 15px;
    padding-top: 56.25%;
    overflow: hidden;
    border-radius: 10px;

    img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  &__foto-desc {
    margin-bottom: 20px;
    font-style: italic;
    font-size: 16px;
    color: $grey-2;
  }

  @include laptop {
    &__description {
      font-size: 20px;
    }

    &__content {
      font-size: 16px;

      blockquote {
        font-size: 20px;
      }
    }
  }

  @include tablets {
    &__header {
      display: flex;
      flex-direction: column;
    }

    &__information {
      order: 3;

      &--mmedia {
        flex-direction: column;
        padding-right: 0;
      }
    }

    &__info {
      margin-bottom: 5px;
    }

    &__mmedia {
      position: static;
      order: -1;
      margin-bottom: 5px;
      background-position: left center;
    }

    &__description {
      font-size: 16px;
    }

    &__add-block {
      margin-top: 60px;
    }

    &__content {
      blockquote {
        font-size: 16px;
      }
    }

    &__foto {
      margin-bottom: 10px;
    }

    &__foto-desc {
      font-size: 14px;
    }
  }
}
