.related {
  
  &__title {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 26px;
  }

  &__item {
    width: 100%;
    margin-right: 15px;
    margin-bottom: 20px;
    color: $default;

    &:hover {
      color: $accent;
    }
  }

  &__foto {
    width: 100%;
    height: 175px;
    margin-bottom: 10px;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 10px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__text {
    font-weight: 700;
    font-size: 18px;
  }

  &__wrap {
    position: relative;
  }

  .swiper {
    &-container {
      width: calc(100% - 100px);
    }

    &-button-next, &-button-prev {
      top: 0;
      z-index: 2;
      margin-top: 0;
      width: 30px;
      height: 175px;
      margin-bottom: 0;
      background-color: $grey;
      border-radius: 10px;
      transition: all 0.2s ease;
      cursor: pointer;

      &::after {
        display: none;
      }

      svg {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 12px;
        height: 12px;
        fill: $accent;
        transform: translate(-40%, -50%);
        transition: $slowdown;
      }

      &:hover {
        background-color: darken($grey, 5%);
        svg {
          fill: $accent;
        }
      }

      &:active {
        transform: scale(0.9)
      }
    }

    &-button-prev {
      left: 0;
      svg {
        transform: translate(-60%, -50%) rotate(180deg);
      }
    }

    &-button-next {
      right: 0;
    }
  }

  @include tablets {
    &__title {
      font-size: 22px;
    }

    &__foto {
      height: 160px;
    }

    &__text {
      font-size: 16px;
    }

    .swiper {
      &-container {
        width: calc(100%);
      }
  
      &-button-next, &-button-prev {
        display: none;
      }
    }
  }

  @include phones {
    &__foto {
      height: 180px;
    }
  }

  @include small {
    &__foto {
      height: 160px;
    }
  }
}