.graph {
  position: relative;
  min-height: 650px;
  margin: 60px 0;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/cases/graph.png);
    background-size: contain;
    background-position: right bottom;
    background-repeat: no-repeat;
  }

  &__wrap {
    position: relative;
  }

  &__title {
    margin-bottom: 30px;
    font-size: 26px;
    line-height: 1.45;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 35px;
  }

  &__numeral {
    margin-bottom: 2px;
    font-weight: 700;
    font-size: 45px;
    font-family: $font-accent;
    line-height: 1;
    color: #5381F6;
  }

  &__text {
    margin-bottom: 0;
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;
  }

  &__note {
    width: 60%;
    font-size: 14px;
    color: #B5BDD1;
  }

  @include laptop {
    min-height: 600px;
  }

  @include phones {
    min-height: 650px;
    width: 280px;
    margin: 50px auto;
    padding-bottom: 200px;

    &::before {
      content: '';
      top: 0;
      bottom: 0;
      // width: 90%;
      background-image: url(../img/cases/graph-mobile.png);
      background-size: contain;
      background-position: left bottom;
    }

    &__title {
      font-size: 20px;
    }

    &__item {
      width: 200px;
    }

    &__numeral {
      font-size: 40px;
    }
  
    &__text {
      font-size: 14px;
    }

    &__note {
      width: 200px;
    }
  }
}