.reviews-services {
  
  .reviews__main--foto {
    .reviews__item {
      margin-bottom: 20px;
    }
  }

  .reviews__main--video {
    .reviews__item {
      margin-bottom: 40px;
    }
  }

  .swiper-container {
    &::before {
      background: $gr-grey;
    }
  }
     
  &__btn-wrap {
    display: flex;
    justify-content: center;
  }

  &__btn {
    margin-top: 20px;
    padding: 15px 30px;
  }

  @include phones {
    &__btn {
      width: 100%;
    }
  }
}