.reviews-aside {
  margin-bottom: 20px;
  padding: 25px;
  background-color: $white;
  border-radius: 10px;
  box-shadow: $shadow-large;

  &--full {
    padding: 10px 0;
    background: none;
    box-shadow: none;
  }

  &__title {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 22px;

    &--big {
      margin-bottom: 5px;
      font-weight: 700;
      font-size: 26px;
    }
  }

  &__link-back {
    display: block;
    margin-bottom: 20px;
    font-size: 14px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    @include lst;
    margin-bottom: 12px;
    margin-left: -10px;
  }

  &__item {
    width: calc(33% - 10px);
    margin-left: 10px;
    margin-bottom: 10px;
    border-radius: 6px;
  }

  &__link {
    position: relative;
    display: block;
    width: 100%;
    padding-top: 56.25%;
    overflow: hidden;
    border-radius: 6px;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 2;
      width: 21px;
      height: 15px;
      background-image: url(../img/icons/play.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      transform: translate(-50%, -50%);
    }
  }

  &__video {
    display: none;
  }

  &__foto {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__name {
    display: none;
  }

  &__text {
    display: none;
  }

  &__btn {
    width: 100%;
  }

  @include laptop {
    &__item {
      width: calc(50% - 10px);
    }
  }

  @include tablets {
    &__item {
      width: calc(33% - 10px);
    }

