.s-smi {
  margin-bottom: 25px;
  padding: 25px;
  border-radius: 10px;
  box-shadow: $shadow-large;

  &__head {
    margin-bottom: 15px;
  }

  &__title {
    margin-bottom: 0;
    font-weight: 700;
    font-size: 22px;
  }

  &__body {
    position: relative;

    &--interesting {
      padding-left: 115px;
    }
  }

  &__icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 95px;
    height: 95px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    margin-bottom: 5px;
    font-weight: 400;
  }

  &__desc {
    margin-bottom: 0;
    font-weight: 400;
    color: $grey-2;
  }

  @include desktop-1500 {
    &__body {
      &--interesting {
        padding-left: 105px;
      }
    }
  }

  @include desktop {
    &__body {
      &--interesting {
        padding-left: 0;
      }
    }

    &__icon {
      position: static;
      margin-bottom: 10px;
    }
  }

  @include tablets {
    margin-top: 40px;
    margin-bottom: 25px;
    padding: 0;
    box-shadow: none;
  }

  @include small {
    &__title {
      font-size: 18px;
    }

    &__desc {
      color: $default;
    }
  }
}
