.sngls {
  margin-top: 100px;

  &__wrap {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -30px;
  }

  &__item {
    width: calc(50% - 30px);
    margin-left: 30px;
    margin-bottom: 40px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: $shadow-small;
  }

  &__link {
    display: flex;
    flex-direction: column;
    height: 100%;
    color: $default;

    &:hover {

      .sngls {
        &__image {     
          img {
            opacity: 1;
          }
        }

        &__title {
          color: $accent;
        }

        &__data {
          color: $grey-2;
        }
      }
    }
  } 

  &__image {
    position: relative;
    width: 100%;
    padding-top: 56.25%;
    overflow: hidden;

    img {
      position: absolute;
      top: 0;
      left: 0;
      min-height: 100%;
      object-fit: cover;
      object-position: center;
      opacity: 0.9;
      transition: opacity $fastdown;
    }
  }
    
  &__content {
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
    padding: 20px;
  }

  &__title {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.4;
    transition: color $fastdown;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    margin-top: auto;
  }

  &__data {
    font-weight: 700;
    font-size: 12px;
    color: $grey-2;
  }

  @include laptop {
    margin-top: 70px;

    &__item {
      width: calc(50% - 20px);
      margin-left: 20px;
      margin-bottom: 20px;
    }

    &__content {
      padding: 15px;
    }
    &__title {
      font-size: 18px;
    }
  }

  @include tablets {
    margin-top: 30px;

    &__wrap {
      margin-left: 0;
    }

    &__item {
      width: 100%;
      margin-left: 0;
    }
  }

  @include phones {
    &__content {
      padding: 10px;
    }
    &__title {
      margin-bottom: 5px;
      font-size: 16px;
    }
    &__data {
      font-size: 10px;
    }
  }
}