h1,
h2,
h3,
h4 {
  margin-top: 0;
  font-weight: 700;
  line-height: 1.4;
}

p {
  margin-top: 0;
}

.text-accent {
  color: $accent;
}

.text-pink {
  color: $pink;
}

.text-red {
  color: $red;
}

.text-green {
  color: $green;
}

.text-up {
  text-transform: uppercase;
}

.text-nowrap {
  white-space: nowrap;
}

.note {
  display: inline-block;
  margin-bottom: 20px;
  padding: 5px 10px;
  font-weight: 500;
  font-size: 14px;
  color: $white;
  background-color: $pink;
  border-radius: 3px;

  &--blue {
    background-color: #5381f6;
  }
}

.section {
  margin-bottom: 50px;

  &--main {
    margin-bottom: 140px;
  }

  &--services {
    padding: 140px 0;
  }

  &--services-grey {
    padding: 120px 0;
    background: $gr-grey;
  }

  &__image {
    width: 150px;
    height: 150px;
    margin: 0 auto;
    margin-bottom: 10px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    &--calc {
      background-image: url(../img/services/calc.png);
    }
    &--reviews {
      background-image: url(../img/services/comment.png);
    }
    &--price {
      background-image: url(../img/services/rubl.png);
    }
    &--results {
      margin-bottom: 10px;
      background-image: url(../img/services/results.png);
    }
    &--free {
      margin-bottom: 10px;
      background-image: url(../img/services/heart.png);
    }
    &--hurry {
      background-image: url(../img/services/hurry.png);
    }

    &--reasons {
      background-image: url(../img/services/like.png);
    }

    &--questions {
      background-image: url(../img/services/faq.png);
    }

    &.lazy {
      background-image: none;
    }
  }

  @include desktop {
    margin-bottom: 40px;

    &--main {
      margin-bottom: 100px;
    }

    &__image {
      width: 100px;
      height: 100px;
      margin-bottom: 20px;
    }
  }

  @include laptop {
    &--services {
      padding: 100px 0;
    }

    &--services-grey {
      padding: 80px 0;
    }

    &__image {
      margin-bottom: 20px;
    }
  }

  @include tablets {
    margin-bottom: 30px;

    &--main {
      margin-bottom: 80px;
    }

    &--services {
      padding: 80px 0;
    }

    &--services-grey {
      padding: 60px 0;
    }
  }

  @include phones {
    &__image {
      &--hurry {
        width: 100px;
        height: 100px;
        margin-bottom: 30px;
      }
    }
  }
}

//     
.inner {
  margin-top: 100px;

  &__head {
    margin-bottom: 60px;

    &--flex {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &__wrap {
    margin-bottom: 140px;
  }

  &__row {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    &--mb {
      margin-bottom: 100px;
    }

    &--width {
      display: block;
    }
  }

  &__block {
    &--aside,
    &--aside-thanks,
    &--aside-show {
      position: relative;
      flex: 0 1 490px;
    }

    &--aside-slim {
      position: relative;
      flex: 0 1 385px;
    }

    &--main {
      flex: 1 0 700px;
      margin-left: 35px;
    }
  }

  @include desktop-1500 {
    &__block {
      &--aside,
      &--aside-thanks,
      &--aside-show {
        flex: 0 2 365px;
      }
    }
  }

  @include desktop {
    &__block {
      &--aside,
      &--aside-thanks,
      &--aside-show {
        flex: 0 2 295px;
      }
      &--aside-slim {
        flex: 0 1 295px;
      }
      &--main {
        flex: 1 0 600px;
      }
    }
  }

  @include laptop {
    margin-top: 70px;

    &__head {
      margin-bottom: 40px;
    }

    &__wrap {
      margin-bottom: 100px;
    }

    &__block {
      &--aside,
      &--aside-thanks,
      &--aside-show {
        flex: 0 2 260px;
      }

      &--aside-slim {
        flex: 0 1 250px;
        max-width: 250px;
      }

      &--main {
        flex: 1 0 400px;
        margin-left: 25px;
      }
    }
  }

  @include tablets {
    margin-top: 30px;

    &__head {
      margin-bottom: 30px;

      &--flex {
        justify-content: space-between;
      }
    }

    &__row {
      &--mb {
        margin-bottom: 40px;
      }
      &--mobile {
        flex-direction: column;
      }
    }

    &__block {
      &--aside {
        display: none;
      }

      &--aside-thanks, 
      &--aside-show {
        width: 100%;
        flex: 0 1 auto;
      }

      &--aside-slim {
        display: none;
      }

      &--main {
        flex: 1 0 100%;
        width: 100%;
        margin-left: 0;
      }
    }
  }

  @include phones {
    &--mobile {
      margin-top: 0;
    }
    &__head {
      margin-bottom: 20px;
    }
  }
}

.aside {
  @include desktop-1500 {
    width: 365px;
  }

  @include desktop {
    width: 295px;
  }

  @include laptop {
    width: 100%;
  }

  @include tablets {
    width: 100%;
  }
}

.title-page {
  margin-bottom: 0;
  font-weight: 700;
  font-size: 34px;
  line-height: 1.45;
  text-align: center;

  &--small {
    font-size: 28px;
  }

  &--tiny {
    font-size: 26px;
  }

  &--left {
    text-align: left;
  }

  &--acticle {
    margin-bottom: 20px;
    text-align: left;
  }

  &__accent {
    color: $accent;
    text-transform: uppercase;
  }

  @include desktop {
    font-size: 32px;
  }

  @include laptop {
    font-size: 30px;

    &--small {
      font-size: 26px;
    }
  }

  @include tablets {
    font-size: 26px;
    text-align: left;

    &--small {
      font-size: 22px;
    }

    br {
      display: none;
    }
  }

  @include phones {
    font-size: 22px;
  }
}

.subtitle-page {
  margin-top: 20px;
  margin-bottom: 0;
  font-weight: 400;
  font-size: 18px;
  color: $grey-2;
  text-align: center;

  &--mt-big {
    margin-top: 40px;
  }

  &--left {
    text-align: left;
  }

  @include tablets {
    text-align: left;

    &--mt-big {
      margin-top: 20px;
    }
  }

  @include phones {
    margin-top: 10px;
    font-size: 16px;
  }
}

.mobile-block {
  display: none;

  @include tablets {
    display: block;
  }
}

.mobile-element {
  display: none;

  @include laptop {
    display: inline;
  }
}

.desktop-element {
  @include laptop {
    display: none;
  }
}

// Logo
.logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  &__name {
    width: 160px;
  }

  @include desktop {
    &__name {
      width: 140px;
    }
  }

  @include laptop {
    &__name {
      width: 130px;
    }
  }

  @include tablets {
    &__name {
      width: 110px;
    }
  }

  @include phones {
    &__name {
      width: 90px;
    }
  }
}

// Buttons & Links
.btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  padding: 15px 30px;
  font-weight: 700;
  font-size: 18px;
  color: $color-default-white;
  text-align: center;
  background: linear-gradient(
      360deg,
      rgba(255, 255, 255, 0) 0%,
      rgba(255, 255, 255, 0.2) 100%
    ),
    #3c70f4;
  box-shadow: 0px 4px 14px rgba(0, 37, 102, 0.18);
  border: none;
  border-radius: 10px;
  transition: all $slowdown;
  cursor: pointer;

  &--show {
    min-width: 275px;
    margin: 0 auto;
    margin-top: 60px;
    font-size: 14px;
    line-height: 1.4;

    &.load {
      svg {
        animation-play-state: running;
      }
    }
  }

  &--link {
    display: inline-block;
    padding: 8px 18px;
    font-size: 14px;
    border-radius: 10px;
  }

  &--download {
    display: flex;
    max-width: 220px;
    margin: 0 auto;
    padding: 15px 20px;
    font-size: 14px;
  }

  &--services {
    padding: 15px 20px;
    line-height: 1.5;
    font-size: 16px;
  }

  &--big {
    min-width: 200px;
    padding: 15px 25px;
    font-size: 16px;
  }

  &__highlights {
    position: absolute;
    left: -150%;
    top: 0;
    height: 100%;
    width: 50px;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.01), rgba(255, 255, 255, 0.4));
    transform: skewX(-45deg);
    animation-name: highlightsAnimation;
    animation-duration: 5s;
    animation-delay: 5s;
    animation-play-state: running;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in;
  }

  &__text {
    display: flex;
    justify-content: center;
    align-items: center;

    &--new {
      font-weight: 600;
      font-size: 20px;
      white-space: nowrap;
    }
  }

  &__icon {
    width: 18px;
    height: 18px;
    margin-right: 10px;

    svg {
      width: 100%;
      height: 100%;
      animation-name: rotation;
      animation-play-state: paused;
      animation-duration: 1.5s;
      animation-fill-mode: forwards;
      animation-iteration-count: infinite;
      animation-timing-function: ease-in-out;
    }
  }

  &__download {
    width: 18px;
    height: 18px;
    margin-right: 10px;

    svg {
      width: 18px;
      height: 18px;
      fill: $white;
    }
  }

  &__total {
    display: none;
  }

  &__btn-arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    margin-right: 8px;
    background-color: $white;
    border-radius: 50%;

    svg {
      width: 15px;
      height: 15px;
    }

    &--accent {
      svg {
        fill: $accent;
      }
    }

    &--pink {
      svg {
        fill: $pink;
      }
    }

    &--red {
      svg {
        fill: $red;
      }
    }
  }

  &:hover {
    color: $color-default-white;
    background: $gr-blue-hover;
    text-decoration: none;

    .btn__highlights {
      animation: none;
    }
  }

  &--red {
    background: $gr-red;

    &:hover {
      background: $gr-red-hover;
    }
  }

  @include phones {
    border-radius: 7px;

    &--services {
      padding: 8px 20px;
    }

    &--download {
      max-width: 100%;
    }
  }
}

.no-btn {
  padding: 0;
  font-weight: 500;
  background: none;
  border: none;
  transition: color $slowdown;
  cursor: pointer;

  &--callback {
    font-size: 14px;
    color: $accent;
  }

  &:hover {
    color: rgba($accent, 0.8);
  }
}

.btn-show-category {
  position: relative;
  display: none;
  margin-bottom: 10px;
  padding-right: 12px;
  outline-color: rgba($accent, 0.4);

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__text {
    font-size: 14px;
    color: $accent;
  }

  &__arrow {
    position: absolute;
    top: 6px;
    right: 0;
    border-style: solid;
    border-width: 7px 4px 0 4px;
    border-color: $accent transparent transparent transparent;
  
    &.open {
      transform: rotate(180deg);
    }
  }

  @include laptop {
    display: block;
  }

  @include tablets {
    &__text {
      font-size: 12px;
    }
  }
}

.link {
  transition: color $fastdown;

  &--contact {
    font-weight: 600;
    font-size: 18px;
    color: $accent;

    &:hover {
      color: rgba($accent, 0.8);
    }
  }

  &--phone {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    margin: 0;
    margin-top: 10px;
    padding: 0;
    background-color: $grey;
    border: none;
    border-radius: 8px;

    span {
      width: 40px;
      height: 40px;
      background-image: url(../img/icons/phone.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &:active {
      span {
        transform: scale(0.9);
      }
    }
  }

  &--inverse {
    color: $default;

    &:hover {
      color: rgba($accent, 0.8);
    }
  }

  &--underline {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $accent;
    }

    &:hover {
      &::before {
        display: none;
        // background-color: rgba($accent, 0.8);
      }
    }
  }

  &__note {
    font-size: 14px;
    color: $grey-2;
  }
}

.phone-call {
  display: flex;
  position: relative;
  z-index: -1;
  border: 1px solid $white;
  box-shadow: $shadow-small;
  opacity: 0;
  transform: translateY(-54px);
  transition: transform 0.4s ease-in, opacity 0.5s ease-in;

  &--show {
    display: flex;
    z-index: 1;
    opacity: 1;
    transform: translateY(0);
  }
}

// Loader- 
.loader {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border-top: 3px solid $accent;
  border-right: 3px solid transparent;
  animation: loader 1.5s linear infinite;
}

.btn-loader {
  display: flex;
  justify-content: center;
  margin: 6px 0;

  &__point {
    display: block;
    margin: 0 3px;
    width: 6px;
    height: 6px;
    background-color: #ffffff;
    border-radius: 50%;
    animation-name: load-opacity;
    animation-duration: 1.2s;
    animation-fill-mode: backwards;
    animation-iteration-count: infinite;
    animation-timing-function: ease-out;

    &--1 {
      animation-delay: 0.4s;
    }
    &--2 {
      animation-delay: 0.8s;
    }
    &--3 {
      animation-delay: 1.2s;
    }

    &--accent {
      background-color: $accent;
    }
  }
}

// Social networks
.social {
  &--mfc {
    display: none;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    @include lst;
  }

  &__item {
    margin: 4px 0;
    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    height: 35px;
    background-color: #d7deef;
    border-radius: 50%;
    transition: all 0.2s ease;

    &--nobg {
      background: none;
    }

    svg {
      width: 16px;
      height: 16px;
    }

    &--middle {
      svg {
        width: 26px;
        height: 26px;
      }
    }

    &:hover {
      svg {
        width: 18px;
        height: 18px;
      }
    }

    &:active {
      transform: scale(0.9);

      svg {
        width: 16px;
        height: 16px;
      }
    }


  }

  &__title {
    margin-bottom: 10px;
    font-size: 14px;
    color: $grey-2;
  }

  &__info {
    display: inline-block;
    margin-top: 11px;
    padding: 8px 12px;
    text-align: center;
    background: $gr-blue;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transform: skewX(-20deg);
    transition: all $slowdown;

    &-text {
      display: block;
      font-weight: 700;
      font-size: 14px;
      color: $white;
      transform: skewX(20deg);
    }

    &:hover {
      color: $color-default-white;
      background: $gr-blue-hover;
      text-decoration: none;
    }
  }
}

//   header
.address {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-bottom: 10px;
  text-align: right;

  &__city {
    font-weight: 600;
    font-size: 18px;
    color: $default;
  }
  &__office {
    font-size: 14px;
    color: $grey-2;
  }

  @include laptop {
    &__office {
      &-metro {
        display: none;
      }
    }
  }

  @include tablets {
    align-items: flex-start;
    text-align: left;
    margin-bottom: 20px;

    &__office {
      &-metro {
        display: inline;
      }
    }
  }
}

//     Youtube
.youtube {
  position: relative;
  width: 100%;
  padding-top: 56.25%;
  overflow: hidden;
  background-color: #000;
  border-radius: 10px;
  cursor: pointer;

  &__no-bdrs {
    border-radius: 0;
  }

  img {
    position: absolute;
    top: -16.82%;
    left: 0;
    width: 100%;
    opacity: 0.7;
  }

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }

  &__button {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    width: 15%;
    height: 15%;
    transform: translate(-50%, -50%);
    transition: transform $middledown;
    cursor: pointer;

    svg {
      width: 100%;
      height: 100%;
    }

    &:active {
      transform: translate(-50%, -50%) scale(0.9);
    }
  }
}

//  
.rating {
  &__common {
    position: relative;
    flex: 0 1 530px;
    min-height: 500px;

    &::before {
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      width: 690px;
      height: 600px;
      background-image: url(../img/rating/main.png);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      transform: translateY(-50%);
    }
  }

  &__text {
    position: absolute;
    left: 50%;
    bottom: 60px;
    font-weight: 500;
    color: #838383;
    transform: translateX(-50%);
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    flex: 1 0 645px;
    @include lst;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 33%;
    margin: 30px 0;
    padding-right: 20px;

    &--small {
      width: 30%;
      margin: 0;
      margin-bottom: 10px;
      margin-right: 10px;
      padding: 10px;
      background-color: $white;
      border-radius: 5px;
      box-shadow: $shadow-small;
    }
  }

  &__icon {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-height: 34px;
    margin-bottom: 15px;

    &--yandex {
      width: 90px;
    }

    &--google {
      width: 105px;

      &.rating__icon--small {
        width: 95px;
      }
    }

    &--zoon {
      width: 140px;

      &.rating__icon--small {
        width: 100px;
      }
    }

    &--2gis {
      width: 85px;
    }

    &--yell {
      width: 90px;
    }

    &--reviewer {
      width: 135px;

      &.rating__icon--small {
        width: 100px;
      }
    }

    svg {
      display: block;
      width: 100%;
      height: 40px;
    }

    &--small {
      transform: scale(0.65);
      transform-origin: left top;
    }
  }

  &__stars {
    margin-bottom: 15px;

    &--small {
      position: absolute;
      top: 10px;
      right: 10px;
      display: flex;
      align-items: flex-end;
    }
  }

  &__reviews {
    font-size: 14px;

    &--small {
      font-size: 12px;
    }
  }

  &__value {
    margin-left: 5px;
    font-weight: 600;
    font-size: 26px;
    line-height: 1.1;
    color: $yellow;

    &--middle {
      font-size: 22px;
    }
    &--small {
      margin-left: 2px;
      font-weight: 600;
      font-size: 12px;
      color: #FF871D;
    }
  }

  @include desktop-1500 {
    &__text {
      left: 40%;
      bottom: 40px;
    }
  }

  @include desktop {
    &__common {
      flex: 0 1 390px;
      min-height: 400px;

      &::before {
        width: 450px;
        height: 450px;
      }
    }

    &__list {
      flex: 1 0 540px;
    }

    &__text {
      left: 55%;
      bottom: 50px;
    }

    &__value {
      font-size: 22px;

      &--small {
        font-size: 12px;
      }
    }

    &__icon {
      margin-bottom: 12px;
    }

    &__stars {
      margin-bottom: 10px;
    }
  }

  @include laptop {
    &__common {
      flex: 0 1 200px;
      min-height: 370px;

      &::before {
        transform: translateY(-50%) scale(0.65);
        transform-origin: right center;
      }
    }
    &__list {
      flex: 1 0 500px;
    }
    &__value {
      margin-left: 5px;
    }
    &__text {
      font-size: 12px;
    }
  }

  @include phones {
    &__list {
      &--small {
        justify-content: space-between;
      }
    }

    &__item {
      &--small {
        width: 48%;
        margin-right: 0;
      }
    }

