.filters-line {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-height: 30px;
  padding-top: 25px;

  &--mb {
    margin-bottom: 20px;
    flex-wrap: wrap;
  }

  &--new {
    align-items: flex-start;
    flex-direction: column;
    padding: 15px;
    background-color: $white;
    border-radius: 10px;
    box-shadow: $shadow-large;
  }

  &--sort {
    margin-top: -5px;
  }

  &__block {
    &--aside {
      min-width: 220px;
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 10px;
  }

  &__title {
    margin-bottom: 10px;
    font-weight: 700;
    color: $default;
  }
  
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
  }

  &__sort {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__lable {
    position: absolute;
    top: 0;
    left: 0;
    font-size: 14px;
    line-height: 1.4;
    color: $grey-2;
  }

  &__best {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;

    a,
    span {
      margin-right: 10px;
      padding: 5px 10px;
      font-size: 14px;
      color: $accent;
      border-radius: 40px;
      box-shadow: $shadow-small;
      cursor: pointer;
      transition: opacity $middledown;

      &:hover {
        opacity: 0.8;
      }
    }
  }

  &__categories {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: -10px;

    a,
    span {
      margin-right: 15px;
      margin-bottom: 10px;
      font-weight: 400;
      font-size: 12px;
      color: $grey-2;
      cursor: pointer;
      transition: opacity $middledown;

      &:hover {
        color: $accent;
        opacity: 0.8;
      }

      &.active {
        color: $accent;
      }
    }
  }

  &__filters {
    display: flex;
    justify-content: space-between;
    margin-left: auto;
  }

  @include desktop {
    &__best {
      &--reviews {
        .radio {
          &:nth-child(3) {
            display: none;
          }
        }
      }

      &--cases {
        .radio {
          &:nth-child(3) {
            display: none;
          }
        }
      }
    }
  }

  @include laptop {
    align-items: baseline;

    &__best {
      &--cases {
        .radio {
          &:nth-child(2) {
            display: none;
          }
        }
      }
    }

    &__categories {
      height: 50px;
      margin-bottom: 0;
      overflow: hidden;

      &.open {
        height: auto;
      }
    }

  }

  @include tablets {
    // display: none;

    &--show {
      display: block;
    }

    &__inner {
      flex-direction: column;
    }

    &__block {
      width: 100%;
    }
  }
}

.filters {
  position: relative;

  &__btn {
    position: relative;
    min-height: 30px;
    padding: 5px 12px;
    padding-right: 25px;
    font-size: 14px;
    background: $gr-blue;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: background $middledown;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
    }

    &:hover {
      background: $gr-blue-hover;
    }
  }

  &__text {
    font-size: 14px;
    color: $white;
    white-space: nowrap;
  }

  &__arrow {
    position: absolute;
    top: 50%;
    right: 10px;
    border-style: solid;
    border-width: 7px 4px 0 4px;
    border-color: $white transparent transparent transparent;
    transform: translateY(-50%);

    &.open {
      transform: translateY(-50%) rotate(180deg);
    }
  }

  &__list {
    display: none;
    position: absolute;
    top: calc(100% + 10px);
    right: -20px;
    z-index: 10;
    min-height: 180px;
    max-height: 300px;
    overflow-y: scroll;
    margin: 0;
    padding: 20px;
    list-style: none;
    white-space: nowrap;
    background: $white;
    border-radius: 10px;
    box-shadow: $shadow-large;
    scrollbar-width: thin;
    scrollbar-color: $accent $grey;

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: $grey;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $accent;
      border-radius: 5px;
      border: 1px solid $accent;
    }

    &--show {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__item {
    margin-bottom: 10px;
    padding: 3px 0;
    padding-left: 30px;
    font-size: 14px;
    color: $grey-2;
    cursor: pointer;

    &:hover {
      color: $accent;
    }

    &--link {
      padding: 3px 0;
    }

    &--title {
      padding: 5px 10px;
      background-color: $grey;
      border-radius: 4px;
      cursor: default;

      &:hover {
        color: $grey-2;
      }
    }

    &--reset {
      padding: 3px 0;
      font-weight: 400;
      font-size: 14px;
      color: $grey-2;
      transition: color $fastdown;

      .radio__label {
        cursor: default;
      }

      &.active {
        color: $accent;
        cursor: pointer;

        .radio__label {
          cursor: pointer;
        }

        &:hover {
          color: rgba($accent, 0.8);
        }
        &:active {
          color: $grey-2;
        }
      }

      &:hover {
        color: $grey-2;
      }
    }

    &--sort {
      padding: 3px 0;
      font-weight: 400;
      font-size: 14px;
      color: $grey-2;
      transition: color $fastdown;
    }
  }
}

.radio {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;

  &--reset {
    height: 30px;
    margin-right: 5px;
    padding: 0 10px;
    font-size: 14px;
    line-height: 30px;
    color: $white;
    background: $gr-pink;
    border-radius: 20px;
    transition: background $middledown;

    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 12px;
      height: 12px;
      fill: $white;
      transform: translate(-50%, -50%);
    }

    &:hover {
      background: $gr-pink-hover;
    }
  }

  &--link {
    margin-right: 10px;
    margin-bottom: 10px;
    overflow: hidden;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.5;
    color: $accent;
    border-radius: 40px;
    box-shadow: $shadow-small;
    transition: opacity $middledown;

    .radio__label {
      padding: 4px 10px 5px;
    }

    &:hover {
      opacity: 0.8;
    }

    .radio__default {
      &:checked + .radio__label {
        color: $white;
        background: $gr-blue;
      }
    }
  }

  &__default {
    display: none;

    &:checked + .radio__new {
      &::before {
        display: block;
      }
    }
  }

  &__new {
    position: absolute;
    left: 0;
    top: 50%;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    box-shadow: inset 0px 0px 5px rgba(3, 60, 112, 0.25);
    transform: translateY(-50%);

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      display: none;
      width: 14px;
      height: 14px;
      background: $gr-blue;
      border-radius: 50%;
      transform: translate(-50%, -50%);
    }

    &--accent {
      left: 20px;
      border: 1px solid $accent;
      box-shadow: none;
    }

    &--mfc {
      left: 15px;
      border: 1px solid $accent;
      box-shadow: none;
    }

    &--pink {
      border: 1px solid $pink;

      &::before {
        background: $pink;
      }
    }

    &--green {
      border: 1px solid $green;

      &::before {
        background: $green;
      }
    }

    &--grey {
      left: 20px;
      padding: 0;
      border: 1px solid #d7d7d7;
      box-shadow: none;

      &::before {
        width: 100%;
        height: 100%;
        background: $white;
        background-image: url(../img/icons/check-accent.svg);
        background-size: 10px;
        background-position: center;
        background-repeat: no-repeat;
      }
    }
  }

  &__label {
    white-space: nowrap;
    cursor: pointer;
    background-color: transparent;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
    }

    &--ws {
      color: #25d366;
    }
    &--tl {
      color: #2ea4e1;
    }
    &--vb {
      color: #693886;
    }
  }

  @include laptop {
    &--link {
      margin-bottom: 5px;
      font-size: 12px;

      .radio__label {
        padding: 6px 10px 6px;
      }
    }
  }

  @include phones {
    &__new {
      &--accent {
        left: 12px;
      }
    }

    @include phones {
      &__label {
        font-size: 14px;
      }
    }
  }
}

.checkbox {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 45px;
  background-color: $white;
  border-radius: 6px;
  cursor: pointer;

  &--btn {
    min-height: auto;
    width: 30px;
    height: 30px;
    margin-bottom: 10px;

    .checkbox__new {
      box-shadow: none;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 20px;
      height: 20px;
      margin: 0;
      padding: 0;
      border-radius: 0;
      border: none;
      box-shadow: none;
      transform: translate(-50%, -50%);
    }
  }

  &--link {
    min-height: 30px;
    margin-right: 10px;
    margin-bottom: 5px;
    overflow: hidden;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.5;
    color: $accent;
    border-radius: 40px;
    box-shadow: $shadow-small;
    transition: opacity $middledown;

    .checkbox__label {
      padding: 4px 10px 5px;
    }

    &:hover {
      opacity: 0.8;
    }

    .checkbox__default {

      &:checked + .checkbox__label {
        color: $white;
        background: $gr-blue;
      }
    }
  }

  &__default {
    display: none;

    &:checked + .checkbox__new {
      &::before {
        display: block;
      }
    }

    &:checked + .checkbox__new--sort {
      svg {
        transform: scale(-1, 1);
      }

      &::before {
        display: none;
      }
    }
  }

  &__new {
    position: absolute;
    top: 50%;
    left: 15px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid $accent;
    transform: translateY(-50%);

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      display: none;
      width: 14px;
      height: 14px;
      background-color: $accent;
      background-image: url(../img/icons/check.svg);
      background-position: center;
      background-size: 6px;
      background-repeat: no-repeat;
      border-radius: 50%;
      transform: translate(-50%, -50%);
    }

    &--pink {
      border: 1px solid $pink;

      &::before {
        background-color: $pink;
      }
    }

    &--green {
      border: 1px solid $green;

      &::before {
        background-color: $green;
      }
    }

    &--sort {
      svg {
        width: 100%;
        height: 100%;
        fill: #5381F6;
        transform: scale(1, -1);
      }
    }
  }

  &__label {
    background-color: transparent;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
    }
  }

  @include phones {
    &__label {
      font-size: 14px;
    }
  }
}

