    justify-content: space-between;
    margin-right: 15px;
  }

  &__title {
    font-weight: 600;
    font-size: 12px;
    color: $grey-2;

    &--mobile {
      display: none;
    }
  }

  &__number {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 40px;
    color: $white;

    &:not(:last-child) {
      margin-right: 15px;
      &::before {
        content: ':';
        position: absolute;
        top: 0;
        right: -10px;
        color: #BD2449;
      }
    }
  }

  &__time {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 28px;
    height: 25px;
    overflow: hidden;
    padding-left: 1px;
    font-weight: 700;
    font-size: 15px;
    letter-spacing: 1px;
    text-align: center;
    background: linear-gradient(180deg, #DE1646 0%, #DE1646 49.99%, #E3496F 50%, #E3496F 98.44%);
    box-shadow: inset 0px -2px 2px rgba(235, 62, 103, 0.9), inset 0px -5px 9px #FF6187;
    border-radius: 4px;
  }

  &__text {
    position: absolute;
    bottom: 0;
    left: 50%;
    font-size: 10px;
    color: $grey-2;
    transform: translateX(-50%);
  }

  @include phones {
    &__number {
      &:not(:last-child) {
        margin-right: 12px;

        &::before {
          right: -8px;
        }
      }
    }

    &__title {
      margin-bottom: 0;
    }
  }

  @include small {

    &__title {
      display: none;
      margin-bottom: 5px;

      br {
        display: none;
      }

      &--mobile {
        display: block;
      }

      &--show {
        display: block;
      }
    }

    &__text {
      font-size: 9px;
    }
  }
}

//  
.hint {
  position: relative;
  top: 2px;
  display: inline-block;
  width: 18px;
  height: 18px;
  background-image: url(../img/icons/info.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;

  &:hover {
    .hint__toggle {
      display: flex;
      animation-name: show-tooltip;
      animation-duration: 0.4s;
      animation-fill-mode: forwards;
      animation-play-state: running;
      animation-iteration-count: 1;
      animation-timing-function: ease-out;
    }
  }

  &--text {
    top: auto;
    display: inline;
    width: auto;
    height: auto;
    color: $accent;
    background: none;

    .hint__toggle {
      box-shadow: $shadow-large;
    }
  }

  &__toggle {
    display: none;
    position: absolute;
    bottom: 35px;
    left: 50%;
    z-index: 10;
    width: 370px;
    padding: 20px 24px;
    font-weight: 400;
    font-size: 14px;
    color: $default;
    background-color: $white;
    border-radius: 8px;
    box-shadow: $shadow-nano;
    opacity: 0;
    transform: translate(-50%, 50px);

    &::before {
      content: '';
      position: absolute;
      bottom: -10px;
      left: 50%;
      width: 0;
      height: 0;
      border-left: 20px solid transparent;
      border-right: 20px solid transparent;
      border-top: 20px solid $white;
      transform: translateX(-50%);
    }
  }
  @include desktop {
    &__toggle {
      width: 330px;
    }
  }

  @include tablets {
    &__toggle {
      width: 220px;
    }
  }

  @include phones {
    &__toggle {
      width: 200px;
      padding: 15px 20px;
    }
  }
}

// TOP button
.btn-up {
  position: fixed;
  bottom: 100px;
  right: 30px;
  z-index: 100;
  width: 50px;
  height: 50px;
  visibility: hidden;
  background-color: $grey;
  border: 1px solid $white;
  border-radius: 8px;
  box-shadow: $shadow-small;
  cursor: pointer;
  opacity: 1;

  &.btn-up--show {
    visibility: visible;
    opacity: 1;
  }

  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 22px;
    height: 22px;
    fill: $accent;
    transform: translate(-50%, -50%);
    transition: $fastdown;
  }

  &:hover {
    svg {
      fill: rgba($accent, 0.8);
    }
  }

  &:active {
    svg {
      fill: rgba($accent, 1);
      transform: translate(-50%, -50%) scale(0.9);
    }
  }

  @include tablets {
    width: 44px;
    height: 44px;
    svg {
      width: 18px;
      height: 18px;
    }
  }

  @include phones {
    right: 20px;
    bottom: 80px;
  }
}

//  - -
.recording {
  margin-top: 60px;

  &__title {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 22px;
    color: $default;
  }

  &__text {
    margin-bottom: 40px;
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;

    &::-webkit-scrollbar {
      height: 3px;
    }

    &::-webkit-scrollbar-track {
      background: $grey;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $accent;
      border-radius: 5px;
      border: 1px solid $accent;
    }
  }

  &__column {
    width: calc(16.3% - 5px);
  }

  &__head {
    display: flex;
    font-size: 14px;
  }

  &__day {
    margin-right: 10px;
    color: $grey-2;
  }

  &__date {
    color: #b5bdd1;
  }

  &__btn-time {
    width: 100%;
    margin-top: 8px;
    padding: 8px;
    font-weight: 500;
    font-size: 14px;
    color: $white;
    background: linear-gradient(180deg, #BACDFF 0%, #9CB8FF 100%);
    border: none;
    border-radius: 7px;
    cursor: pointer;
    transition: transform $middledown, opacity $fastdown;

    &:hover {
      background: $accent;
      // opacity: 0.85;
    }

    &:active {
      transform: scale(0.95);
    }

    &:disabled {
      color: #B5BDD1;
      text-decoration: line-through;
      background: none;
      cursor: default;
    }

    &.selected {
      background: $gr-pink;
      color: $white;
      text-decoration: none;
    }

    &.past {
      opacity: 0.35;
      cursor: default;

      &:active {
        transform: none;
      }
    }
  }

  @include desktop-1500 {
    &__head {
      font-size: 12px;
    }
  }

  @include desktop {
    &__wrap {
      max-width: 520px;
      overflow-x: scroll;
      padding-bottom: 10px;
    }

    &__column {
      flex: 1 0 auto;
      width: 110px;
      margin-right: 10px;
    }
  }

  @include laptop {
    &__wrap {
      max-width: 420px;
    }

    &__column {
      width: 95px;
      margin-right: 10px;
    }
  }

  @include tablets {
    width: 100%;

    &__wrap {
      max-width: 100%;
    }

    &__column {
      flex: none;
      width: 97px;
    }

    &__day {
      margin-right: 5px;
    }
  }

  @include phones {
    display: flex;
    flex-direction: column;
    width: calc(100% + 20px);

    &__wrap {
      flex: 1 0 100%;
      margin-bottom: 20px;
    }

    &__column {
      width: 80px;
      margin-right: 10px;
    }

    &__btn-time {
      padding: 6px;
      font-weight: 500;
      font-size: 12px;
      border-radius: 5px;
    }

    &__btn {
      width: 100%;
      padding: 8px;
      font-size: 16px;
      line-height: 1.4;
    }
  }
}


