//    
.date-time {
  position: relative;
  z-index: 1;

  &__btn {
    position: relative;
    min-height: 30px;
    min-width: 80px;
    margin-right: 5px;
    padding: 5px 0;
    padding-right: 25px;
    font-size: 14px;
    color: $accent;
    background: none;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: background $middledown;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
    }

    &:hover {
      .date-time {
        &__text {
          color: lighten($accent, 10%);
        }
      }
    }
  }

  &__text {
    font-size: 14px;
    color: $accent;
    white-space: nowrap;
  }

  &__arrow {
    position: absolute;
    top: 50%;
    right: 10px;
    border-style: solid;
    border-width: 7px 4px 0 4px;
    border-color: $accent transparent transparent transparent;
    transform: translateY(-50%);

    &.open {
      transform: translateY(-50%) rotate(180deg);
    }
  }

  &__wrap {
    display: none;
    position: absolute;
    top: 100%;
    right: 10px;
    z-index: 10;
    min-height: 180px;
    max-height: 300px;
    // overflow-y: scroll;
    margin: 0;
    padding: 10px;
    list-style: none;
    white-space: nowrap;
    background: $white;
    border-radius: 6px;
    box-shadow: $shadow-large;
    scrollbar-width: thin;
    scrollbar-color: $accent $grey;

    // &::-webkit-scrollbar {
    //   width: 6px;
    // }

    // &::-webkit-scrollbar-track {
    //   background: $grey;
    // }

    // &::-webkit-scrollbar-thumb {
    //   background-color: $accent;
    //   border-radius: 5px;
    //   border: 1px solid $accent;
    // }

    &--show {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
  }

  &__item {
    padding: 3px 0;
    font-size: 14px;
    color: $default;
    cursor: pointer;

    &:not(:last-child) {
      margin-bottom: 2px;
    }

    &:hover {
      color: $accent;
    }

    .radio__default {
      &:disabled + .radio__label {
        color: #B5BDD1;
        cursor: default;
      }
    }

    .radio__default {
      &:checked + .radio__label {
        color: $accent;
      }
    }
   
    &--reset {
      padding: 3px 0;
      font-weight: 400;
      font-size: 14px;
      color: $grey-2;
      transition: color $fastdown;

      .radio__label {
        cursor: default;
      }

      &.active {
        color: $accent;
        cursor: pointer;

        .radio__label {
          cursor: pointer;
        }

        &:hover {
          color: rgba($accent, 0.8);
        }
        &:active {
          color: $grey-2;
        }
      }

      &:hover {
        color: $grey-2;
      }
    }

    &--sort {
      padding: 3px 0;
      font-weight: 400;
      font-size: 14px;
      color: $grey-2;
      transition: color $fastdown;
    }
  }

  @include tablets {
    &__wrap {
      right: auto;
      left: 0;
    }
  }
}

//  
.calendar {
  width: 100%;
  font-size: 16px;
  text-align: center;
  border-collapse: separate;
  border-spacing: 6px;

  thead {
    tr {
      &:last-child {
        font-size: 12px;
        color: #B5BDD1;
      }

      &:nth-child(1){
        td {
          &:nth-child(2) {
            color: $default;
          }
        }
      }

      &:nth-child(1) {
        td {
          &:nth-child(1), &:nth-child(3) {
            position: relative;

            &::before {
              content: '';
              position: absolute;
              top: 50%;
              left: 50%;
              border-style: solid;
              border-width: 7px 4px 0 4px;
              border-color: $accent transparent transparent transparent;
              transform: translate(-50%, -50%) rotate(90deg);
            }

            &:hover {
              cursor: pointer;
            }
          }

          &:nth-child(3) {
            &::before {
              transform: translate(-50%, -50%) rotate(-90deg);
            }
          }
        }
      }
    }
  }

  tbody {

    tr {
      font-weight: 400;
      font-size: 14px;
      
      &:last-child {
        color: #E793A7;
      }
    }
    
    td {
      padding: 0 2px;
      color: #B5BDD1;

      &.rec {
        color: $white;
        background: linear-gradient(180deg, #BACDFF 0%, #9CB8FF 100%);
        border-radius: 2px;

        &:hover {
          cursor: pointer;
        }
      }

      &.today {
        color: $white;
        background: none;
        background-color: $accent;
        border-radius: 2px;

        &:hover {
          cursor: pointer;
        }
      }

      &:nth-child(n+7) {
        color: #E793A7;
        background: none;
        cursor: default;
      }
    }
  }
}

//     
.btn-select {
  position: relative;

  &__btn {
    position: relative;
    min-width: 120px;
    padding-right: 12px;
    outline-color: rgba($accent, 0.4);
    text-align: left;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    font-size: 14px;
    color: $accent;
    white-space: nowrap;
  }

  &__arrow {
    position: absolute;
    top: 6px;
    right: 0;
    border-style: solid;
    border-width: 7px 4px 0 4px;
    border-color: $accent transparent transparent transparent;
  
    &.open {
      transform: rotate(180deg);
    }

    &.hide {
      opacity: 0;
    }
  }

  &__list {
    display: none;
    position: absolute;
    top: calc(100% + 10px);
    left: 50%;
    z-index: 10;
    margin: 0;
    padding: 20px;
    list-style: none;
    white-space: nowrap;
    background: #fff;
    border-radius: 10px;
    box-shadow: $shadow-large;
    transform: translateX(-50%);

    &.open {
      display: flex;
      flex-direction: column;
      align-items: stretch;
    }
  }

  &__item {
    font-size: 14px;
    transition: color $fastdown;
    
    .radio__label {
      width: 100%;
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &:hover {
      color: $accent;
    }
  }

  @include desktop-1500 {
    &__text {
      font-size: 12px;
    }

    &__list {
      padding: 15px;
    }
  }

  @include tablets {
    &__list {
      left: -15px;
      transform: translateX(0);
    }
  }
}
