.help {
  &__lst {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include lst;
    margin-top: 60px;
    margin-bottom: 25px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 31.5%;
    margin-bottom: 35px;
    padding: 25px;
    background-color: $white;
    border-radius: 10px;
    box-shadow: $shadow-large;
  }

  &__icon {
    width: 100px;
    height: 100px;
    margin-bottom: 20px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    // &--calls {
    //   background-image: url(../img/services/help/collector.svg);
    // }

    // &--no-credit {
    //   background-image: url(../img/services/help/no-credit.svg);
    // }

    // &--money {
    //   background-image: url(../img/services/help/money.svg);
    // }

    // &--in-yan {
    //   background-image: url(../img/services/help/in-yan.svg);
    // }

    // &--home {
    //   background-image: url(../img/services/help/home.svg);
    // }

    // &--stars {
    //   background-image: url(../img/services/help/stars.svg);
    // }
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 18px;
  }

  &__desc {
    color: $grey-2;
  }

  &__btn {
    margin: 0 auto;
  }

  @include laptop {
    &__item {
      width: 48%;
    }
  }

  @include tablets {
    &__lst {
      flex-direction: column;
      margin-top: 40px;
    }

    &__item {
      width: 100%;
    }

    &__icon {
      width: 80px;
      height: 80px;
    }
  }

  @include phones {
    &__icon {
      width: 65px;
      height: 65px;
    }

    &__item {
      margin-bottom: 20px;
    }

    &__desc {
      font-size: 14px;
    }
  }
}
