.two-actions {
  padding-bottom: 0;

  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
  }

  &__block {
    width: calc(50% - 15px);
  }

  &__subtitle {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 22px;
    color: $default;
  }

  &__lst {
    display: flex;
    flex-direction: column;
    @include lst;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 90px;
    padding: 10px;
    padding-right: 20px;
    padding-left: 70px;
    background-color: $grey;
    border-radius: 10px;
    transition: background-color $fastdown;

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &:hover {
      background-color: darken( $grey, 3%);

      .two-actions {
        &__icon {
          svg {
            transform: scale(1.3);
          }
        }
      }
    }
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 32px;
    width: 25px;
    height: 25px;
    transform: translateY(-50%);

    svg {
      width: 100%;
      height: 100%;
      transition: transform $middledown;
    }

    &--green {
      svg {
        fill: $green;
      }
    }

    &--pink {
      svg {
        fill: $pink;
      }
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;
  }

  @include laptop {
    &__item {
      min-height: 100px;
    }
  }

  @include tablets {
    &__wrap {
      flex-direction: column;
      margin-top: 0;
    }

    &__block {
      width: 100%;
    }

    &__item {
      min-height: auto;
      padding: 15px;
      padding-left: 45px;
    }

    &__icon {
      top: 15px;
      left: 15px;
      width: 20px;
      height: 20px;
      transform: translateY(0);
    }

    &__subtitle {
      margin-top: 20px;
      margin-bottom: 10px;
      font-size: 20px;
    }

    &__text {
      font-size: 16px;
    }
  }

  @include phones {
    &__item {
      min-height: auto;
      padding: 15px;
      padding-left: 45px;
    }

    &__subtitle {
      font-size: 18px;
    }
  }
}