.master {
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &--dogovor {
      margin-top: 100px;
    }
  }

  &__block {

    &--person {
      flex: 0 1 490px;
    }

    &--content {
      flex: 1 0 700px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-left: 35px;
    }

    &--top {
      align-self: flex-start;
    }

    &--bondar {
      align-self: flex-start;
    }
  }

  &__person {
    position: relative;
    width: 490px;
    height: 490px;
    margin-top: 110px;
    border-radius: 50%;
    background: $gr-grey;

    &--inverse {

      &::before, &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
  
      &::before {
        background-image: url(../img/bg.jpg);
        opacity: 0.2;
      }
  
      &::after {
        background-color: $white;
        opacity: 0.9;
      }
    }
    
    img {
      position: absolute;
      bottom: 0;
      left: 50%;
      z-index: 1;
      width: 490px;
      transform: translateX(-50%);
    }
  }

  &__note {
    margin-bottom: 40px;
    padding: 5px 10px;
    font-size: 14px;
    color: $white;
    background-color: $pink;
  }

  &__title {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 26px;

    &--big {
      font-size: 34px;
    }

    &--small {
      position: relative;
      padding-top: 40px;
      font-size: 22px;

      svg {
        position: absolute;
        top: 0;
        left: 0;
        width: 30px;
        height: 30px;
      }
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;

    &--big {
      margin-bottom: 25px;
      font-size: 22px;
      color: $default;
    }
  }

  &__name {
    margin-bottom: 10px;
    font-size: 18px;
  }

  &__post {
    margin-bottom: 40px;
    color: $grey-2;
  }

  &__link {
    margin-bottom: 15px;
    font-size: 26px;
    color: $accent;

    &--phone {
      font-weight: 700;
    }
  }

  @include desktop-1500 {
    &__block {
  
      &--content {
        flex: 1 0 565px;
      }
    }
  }

  @include desktop {
    &__block {

      &--person {
        flex: 0 1 390px;
      }
  
      &--content {
        flex: 1 0 520px;
        margin-left: 20px;
      }
    }

    &__person {
      width: 390px;
      height: 390px;
    }
  }

  @include laptop {
    &__wrap {
      &--dogovor {
        margin-top: 40px;
      }
    }

    &__block {

      &--person {
        flex: 0 1 250px;
      }
  
      &--content {
        flex: 1 0 420px;
      }
    }

    &__person {
      width: 250px;
      height: 250px;
    }

    &__text {
      font-size: 16px;
    }

    &__link {
      font-size: 22px;
    }
  }

  @include tablets {

    &__wrap {
      flex-direction: column;

      &--dogovor {
        margin-bottom: 40px;
      }
    }

    &__block {

      &--person {
        flex: 0 1 100%;
      }
  
      &--content {
        flex: 1 0 100%;
        max-width: 100%;
        margin-left: 0;
      }
    }

    &__person {
      margin-top: 60px;
      margin-bottom: 40px;
    }

    &__note {
      margin-bottom: 20px;
    }

    &__title {
      font-size: 22px;
    }

    &__post {
      margin-bottom: 20px;
    }

    &__text {
      margin-bottom: 20px;
    }

    &__link {
      margin-bottom: 5px;
      font-size: 18px;
    }
  }

  @include phones {
    &__person {
      width: 250px;
      height: 250px;
      margin-top: 40px;
      margin-bottom: 20px;
    }

    &__title {
      font-size: 18px;
    }

    &__name {
      font-size: 16px;
    }

    &__post {
      font-size: 14px;
    }
  }
}