.top-m {
  position: relative;

  &::before, &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  &::before {
    z-index: 2;
    background-image: url(../img/top/bg.png);
    background-size: cover;
    background-position: center bottom;
    background-repeat: no-repeat;
    opacity: 0.08;
  }

  &::after {
    z-index: 1;
    background: $accent;
  }

  &__wrap {
    position: relative;
    z-index: 3;
    display: flex; 
    flex-direction: column;
    min-height: 940px;
    padding: 100px 0;
  }

  &__image {
    position: absolute;
    bottom: 0;
    right: -340px;
  }

  &__content {
    position: relative;
    z-index: 1;
    display: flex; 
    flex-direction: column;
    max-width: 825px;
    color: $white;
  }

  &__title {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 45px;
    text-transform: uppercase;
  }

  &__text {
    margin-bottom: 45px;
    font-weight: 400;
  }

  &__btn {
    align-self: flex-start;
    font-size: 14px;
    color: $accent;
    background: $gr-grey;

    &:hover {
      color: $accent;
      background: $gr-grey;
      opacity: 0.9;
    }
  }

  &__lst {
    @include lst;
    margin-top: 100px;
    counter-reset: list-counter;

    &-item {
      position: relative;
      display: flex;
      flex-direction: column;
      padding-left: 70px;

      &:not(:last-child) {
        margin-bottom: 25px;
      }

      &::before {
        content: counter(list-counter);
        counter-increment: list-counter;
        position: absolute;
        top: -2px;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 45px;
        height: 45px;
        font-weight: 700;
        font-size: 18px;
        color: $white;
        border: 2px solid #90AFFD;
        border-radius: 50%;
      }
    }

    &-name {
      margin-bottom: 10px;
      font-size: 26px;
    }

    &-text {
      font-weight: 400;
    }
  }

  @include desktop {
    &__content {
      max-width: 620px;
    }

    &__lst {
      margin-top: 50px;
    }
  }

  @include laptop {
    &__wrap {
      padding: 80px 0;
      
    }

    &__content {
      max-width: 100%;
    }

    &__title {
      font-size: 38px;
    }

    &__lst {
      max-width: 470px;

      &-name {
        font-size: 22px;
      }
    }
  }

  @include tablets {
    &__wrap {
      padding-bottom: 500px;
    }

    &__image {
      right: 50%;
      min-width: 550px;
      transform: translateX(60%);
    }

    &__title {
      font-size: 28px;
    }
  }

  @include phones {

    &__wrap {
      padding: 60px 0;
      padding-bottom: 500px;
    }

    &__title {
      font-size: 21px;
    }

    &__text {
      margin-bottom: 35px;
      font-size: 14px;
    }

    &__lst {
      margin-top: 60px;
  
      &-item {
        padding-left: 50px;

        &::before {
          width: 35px;
          height: 35px;
          font-size: 16px;
        }
      }
    }
  }
}
