.callback {
  position: relative;
  margin-bottom: 20px;
  padding: 25px;
  overflow: hidden;
  background-color: $white;
  border-radius: 10px;
  box-shadow: $shadow-large;

  &--bottom {
    display: none;
  }

  &__head {
    position: relative;
    margin-bottom: 10px;
  }

  &__title {
    margin-bottom: 0;
    font-weight: 700;
    font-size: 18px;
  }

  &__link {
    font-weight: 600;
    font-size: 22px;
  }

  @include laptop {
    padding: 20px;

    &__link {
      font-size: 20px;
    }
  }

  @include tablets {
    
    &--aside {
      display: none;
    }
    &--bottom {
      display: block;
    }
  }

}