@keyframes zoom {
  0% {
    transform: scale(1);
  }
  14% {
    transform: scale(1.25);
  } 
  28% {
    transform: scale(1);
  }
  42% {
    transform: scale(1.25);
  }
  70% {
    transform: scale(1);
  }
}

@keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes shredder {
  0% {
    transform: translateX(-50%) translateY(-100%);
  }

  25% {
    transform: translateX(-50%) translateY(20%);
  }

  40% {
    transform: translateX(-50%) translateY(0%);
  }

  90% {
    transform: translateX(-50%) translateY(150%);
    opacity: 1;
  }

  100% {
    transform: translateX(-50%) translateY(160%);
    opacity: 0;
  }
}

@keyframes analysis {
  0% {
    transform: translate(-50%, 0);
  }
  12% {
    transform: translate(-50%, -14.5%);
  }
  16% {
    transform: translate(-50%, -14.5%);
  }
  26% {
    transform: translate(-50%, -28.5%);
  }
  30% {
    transform: translate(-50%, -28.5%);
  }
  40% {
    transform: translate(-50%, -43%);
  }
  44% {
    transform: translate(-50%, -43%);
  }
  54% {
    transform: translate(-50%, -57.5%);
  }
  58% {
    transform: translate(-50%, -57.5%);
  }
  68% {
    transform: translate(-50%, -71.5%);
  }
  72% {
    transform: translate(-50%, -71.5%);
  }
  82% {
    transform: translate(-50%, -85.8%);
  }
  86% {
    transform: translate(-50%, -85.8%);
  }
  100% {
    transform: translate(-50%, -85.8%);
  }
}


@keyframes zoom-radio {
  0% {
    transform: translateY(-50%) scale(1) ;
  }
  25% {
    transform: translateY(-50%) scale(1.25) ;
  }
  50% {
    transform: translateY(-50%) scale(1.1) ;
  } 
  75% {
    transform: translateY(-50%) scale(1.5) ;
  }
  100% {
    transform: translateY(-50%) scale(1) ;
  }
}


@keyframes arrow-opacity {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0.5;
  }
}

@keyframes show-tooltip {
  0% {
    opacity: 0;
    transform: translate(-50%, 50px);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

@keyframes load-opacity {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}

@keyframes highlightsAnimation {
  0% {
    left: -150%;
  }
  30% {
    left: 150%;
  }
  100% {
    left: 150%;
  }
}