.social-aside { 
  position: relative;
  margin-bottom: 20px;
  padding: 35px;
  overflow: hidden;
  background-color: $white;
  border-radius: 10px;
  box-shadow: $shadow-large;

  &::before {
    content: '';
    position: absolute;
    bottom: -12px;
    right: 0;
    width: 160px;
    height: 190px;
    background-image: url(../img/news/lady.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__title {
    margin-bottom: 20px;
    padding-right: 140px;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.45;
  }

  .social__item{
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  .social__link {
    width: 26px;
    height: 26px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  @include desktop-1500 {
    &::before {
      display: none;
    }

    &__title {
      padding-right: 0;
    }

    &__item {
      margin-bottom: 20px;
    }
  }

  @include desktop {
    padding: 25px;

    &__social {
      .social {
        &__link {
          width: 24px;
          height: 24px;
        }
      }
    }
  }

  @include desktop {
    padding: 20px;

    &__social {
      .social {
        &__link {
          width: 22px;
          height: 22px;
        }
      }
    }
  }

  @include tablets {
    padding: 40px;

    &::before {
      display: block;
    }

    &__title {
      font-size: 22px;
    }
    
    &__item {
      width: 50%;
    }

    &__social {
      .social {
        &__link {
          width: 26px;
          height: 26px;
        }
      }
    }
  }

  @include phones {
    padding: 25px;

    &::before {
      display: none;
    }

    &__social {
      .social {
        &__link {
          width: 20px;
          height: 20px;
        }
      }
    }
  }
}