.reasons {
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, #EDF0F8 15%, #D7DEEF 85%);
    opacity: 0.9;
  }

  &__container {
    position: relative;
    z-index: 10;
  }

  &__lst {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include lst;
    margin-top: 60px;
  }

  &__item {
    position: relative;
    width: 32%;
    margin-bottom: 60px;
    padding-top: 120px;
    padding-right: 20px;
  }

  &__icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 100px;
    height: 100px;
    margin-right: 25px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    filter: drop-shadow(0px 4px 14px rgba(0, 37, 102, 0.1));

    &--1 {
      background-image: url(../img/services/reasons/1.svg);
    }
    &--2 {
      background-image: url(../img/services/reasons/2.svg);
    }
    &--3 {
      background-image: url(../img/services/reasons/3.svg);
    }

    svg {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 70px;
      height: 70px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
  }
  &__name {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 22px;
  }
  &__desc {
    color: $grey-2;
  }

  @include laptop {
    &__lst {
      flex-direction: column;
      align-items: center;
    }

    &__item {
      width: 50%;
      margin-bottom: 25px;
      padding-top: 100px;
      padding-right: 25px;
    }

    &__icon {
      width: 80px;
      height: 80px;

      svg {
        width: 50px;
        height: 50px;
      }
    }
  }

  @include tablets {

    &__item {
      width: 100%;
    }
  }

  @include phones {
    &__lst {
      margin-top: 40px;
    }

    &__item {
      justify-content: space-between;
    }

    &__icon {
      width: 70px;
      height: 70px;

      svg {
        width: 45px;
        height: 45px;
      }
    }

    &__name {
      font-size: 16px;
    }

    &__desc {
      font-size: 14px;
    }

  }
}