.container {
  max-width: 1265px;
  margin: 0 auto;
  padding: 0 20px;

  @include desktop-1500 {
    max-width: 1140px;
  }

  @include desktop {
    max-width: 960px;
    padding: 0 15px;
  }

  @include laptop {
    max-width: 720px;
    padding: 0 10px;
  }

  @include tablets {
    max-width: 540px;
    padding: 0 5px;
  }

  @include phones {
    max-width: 100%;
    padding: 0 22px;
  }
}