.mfc-inner {
  position: relative;
  min-height: 900px;

  &::after, &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  &::before {
    z-index: 2;
    background-image: url(../img/top/bg.png);
    background-size: cover;
    background-position: center bottom;
    background-repeat: no-repeat;
    opacity: 0.07;
  }

  &::after {
    z-index: 1;
    background: $gr-grey;
  }
}

.mfc {
  position: relative;
  z-index: 3;
  padding-top: 20px;
  padding-bottom: 100px;

  &--sale {
    padding: 100px 0;

    a {
      color: $pink;

      &:hover {
        color: lighten($pink, 10%);
      }
    }
  }

  &--benefits {
    padding: 100px 0;
    background: $gr-grey;
  }

  &--grey {
    background-color: #F3F4F4;
  }

  &--new {
    padding: 70px 0;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .note {
      font-weight: 700;
      font-size: 16px;

      &--green {
        background-color: $green;
      }
    }

    &--new {
      width: 850px;
      margin: 0 auto;
    }
  }

  &__header {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: -40px;
    margin-bottom: 65px;
  }

  &__advantages {
    margin-left: 30px;
  }

  &__title {
    margin-top: 5px;
    margin-bottom: 70px;
    font-size: 45px;

    &--new {
      margin-bottom: 30px;
      font-size: 30px;
    }
  }

  &__subtitle {
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 22px;
  }

  &__block-btn {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 100px;

    &--new {
      margin-bottom: 60px;
    }
  }

  &__btn {
    width: 280px;
    margin-right: 35px;
    padding: 30px;
    border-radius: 15px;

    &--pink {
      background: $gr-pink;
      transition: all $slowdown;

      &:hover {
        background: $gr-pink-hover;
      }
    }

    &--green {
      background: $gr-green;
      transition: all $slowdown;

      &:hover {
        background: $gr-green-hover;
      }
    }

    &--new {
      min-width: 250px;
      padding: 16px 40px;
      border-radius: 100px;
    }
  }

  &__hint {
    max-width: 440px;
    font-size: 14px;

    &--big {
      font-size: 18px;
    }

    &--bold {
      font-weight: 700;
    }
  }

  &__lst {
    @include lst;

    &-item {
      position: relative;
      padding-left: 20px;
      font-size: 18px;
      color: #393939;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 10px;
        width: 6px;
        height: 6px;
        background-color: #5381F6;
        border-radius: 50%;
      }

      &:not(:last-child) {
        margin-bottom: 15px;
      }

      &--pink {
        &::before {
          background-color: $pink;
        }
      }

      &--green {
        &::before {
          background-color: $green;
        }
      }
    }
  }

  @include desktop {
    &__header {
      margin-left: -35px;
    }
  }

  @include laptop {
    padding-bottom: 80px;

    &--bottom {
      padding: 80px 0;
    }

    &__wrap {
      &--new {
        width: 100%;
      }
    }

    &__title {
      margin-bottom: 50px;
      font-size: 32px;

      &--new {
        font-size: 28px;
      }
    }
  
    &__subtitle {
      font-size: 20px;
    }

    &__block-btn {
      margin-bottom: 80px;
    }
  }

  @include tablets {
    padding-top: 40px;

    &--sale, &--benefits {
      padding: 60px 0;
    }

    &--new {
      padding: 30px 0;
    }

    &__header {
      margin-left: -30px;
      margin-bottom: 40px;
    }

    &__title {
      &--new {
        margin-bottom: 30px;
        font-size: 22px;
      }
    }

    &__block-btn {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 60px;

      &--new {
        margin-bottom: 30px;
      }
    }

    &__btn {
      margin-bottom: 15px;
      padding: 23px;
    }
  }

  @include phones {
    padding: 40px 0 60px;

    &--bottom {
      padding: 60px 0;
    }

    &__header {
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin-left: 0;
      margin-bottom: 35px;
    }

    &__advantages {
      margin-left: 15px;
    }

    &__wrap {
      .note {
        max-width: 200px;
      }
    }

    &__title {
      margin-bottom: 35px;
      font-size: 22px;

      &--new {
        font-size: 19px;
      }
    }
  
    &__subtitle {
      font-size: 18px;

      &--new {
        margin-bottom: 15px;
        font-size: 16px;
      }
    }

    &__btn {
      width: 100%;
    }

    &__lst {
      &--new {
        .mfc__lst-item {
          font-size: 12px;
        }
      }
    }
    &__lst-item {
      font-size: 16px;
    }
  }
}

.mfc-adv {
  display: flex;
  align-items: center;
  width: 410px;
  padding: 12px 30px;
  border-radius: 100px;

  &--free {
    background-color: $blue;
  }

  &--sale {
    background-color: $red;
  }

  &__image {
    margin-right: 10px;

    &--themis {
      height: 70px;
      min-width: 45px;
      background-image: url(../img/mfc/Themis.png);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &--yandex {
      height: 28px;
      min-width: 73px;
      background-image: url(../img/mfc/Yandex.png);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    font-size: 14px;
    line-height: 1.5;
    color: $white;
  }

  &__stars {
    font-size: 20px;
    color: #FFBB16;

    &--mobile {
      display: none;
    }
  }

  &__stars-yandex {
    display: inline-block;
    min-width: 55px;
    height: 22px;
    margin-right: 8px;
    background-image: url(../img/mfc/Yandex.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }

  @include tablets {
    padding: 15px;
  }

  @include phones {
    position: relative;
    max-width: 240px;
    margin-right: -20px;
    padding: 6px 12px;
    // border-radius: 10px;

    // &::before {
    //   content: '';
    //   position: absolute;
    //   top: 0;
    //   right: -50%;
    //   height: 100%;
    //   width: 50%;
    // }

    &--free {
      background-color: $blue;

      &::before {
        background-color: $blue;
      }
    }
  
    &--sale {
      padding: 8px 10px 12px 10px;
      text-align: center;
      background-color: $red;

      &::before {
        background-color: $red;
      }
    }

    &__image {
      &--themis {
        min-width: 26px;
        height: 45px;
        margin-right: 8px;
      }

      &--yandex {
        display: none;
      }
    }

    &__stars {
      &--desktop {
        display: none;
      }

      &--mobile {
        display: flex;
        align-items: center;
        margin: 0 auto;
        // margin-bottom: 5px;
      }
    }

    &__content {
      font-size: 11px;
    }
  }

  @include small {
    &__content {
      font-size: 9px;
    }
  }
}

.mfc-hint {
  position: relative;
  max-width: 100%;
  padding-left: 50px;

  &__number {
    position: absolute;
    top: 50%;
    left: 0;
    font-weight: 700;
    font-size: 85px;
    color: $accent;
    transform: translateY(-50%);

    &--accent {
      color: $accent;
    }

    &--red {
      color: $red;
    }
  }

  &__text {
    color: $grey-2;
  }

  @include tablets {
    margin-top: 10px;
  }

  @include phones {
    padding-left: 40px;

    &__number {
      font-size: 70px;
    }

    &__text {
      font-size: 12px;
    }
  }

  @include small {
    &__number {
      top: 30%;
    }
  }
}