.cases {
  min-height: 675px;
  
  &__wrap {
    position: relative;
  }

  &__additional {
    display: none;
  }

  &__loader {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 200px;
    min-height: 520px;
  }

  @include desktop {
    &__loader {
      min-height: 360px;
    }
  }

  @include tablets {
    width: 100%;
    
    &__wrap {
      margin-top: 0;
    }

    &__loader {
      min-height: 120px;
    }
  }
}

.case {
  position: relative;
  margin-bottom: 20px;
  padding: 35px;
  background-color: $white;
  box-shadow: $shadow-large;
  border-radius: 20px;

  &__title {
    margin-bottom: 22px;
    padding-right: 60px;
    font-weight: 700;
    font-size: 22px;
  }

  &__number {
    position: absolute;
    top: 35px;
    right: 35px;
    font-weight: 500;
    font-size: 14px;
    color: $grey-2;
  }

  &__video {
    margin-bottom: 35px;
  }

  &__original {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 30px;
  }

  &__data {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__row {
    display: flex;
    justify-content: flex-start;
    color: $grey-2;

    &:not(:last-child) {
      margin-bottom: 15px;
    }

    span {
      color: $default;

      &.text-grey {
        color: $grey-2;
      }
    }

    &--creditors, &--number {
      display: flex;
    }
  }

  &__call {
    &--name {
      flex: 1 0 240px;
      max-width: 240px;
    }

    &--data {
      flex: 0 1 auto;
      padding-left: 5px;
      color: $default;
      white-space: normal;
    }
  }

  @include laptop {
    padding: 25px;
    padding-top: 45px;

    &__title {
      padding-right: 0;
    }

    &__number {
      right: auto;
      left: 25px;
      top: 25px;
    }

    &__video {
      margin-bottom: 25px;
    }

    &__original {
      flex-direction: column;
      margin-bottom: 10px;
    }

    &__row {
      flex-direction: column;
    }

    &__call {
      &--name {
        flex: 1 0 auto;
        margin-bottom: 2px;
      }
  
      &--data {
        flex: 0 1 auto;
        padding-left: 0;
      }
    }
  }

  @include tablets {
    z-index: 11;
    padding: 22px;
    padding-top: 40px;
    border-radius: 20px;

    &__title {
      font-size: 20px;
    }

    &__number {
      left: 22px;
      top: 20px;
    }

    &__video {
      margin-bottom: 15px;
    }

    &__original {
      margin-bottom: 5px;
    }

    &__row {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }

    &__call {
      &--name {
        font-size: 12px;
      }
  
      &--data {
        font-size: 14px;
      }
    }
  }

  @include phones {
    &__title {
      font-size: 18px;
    }

    &__row {
      font-size: 14px;
    }
  }
}
