.reviews {
  margin-top: 100px;

  &__wrap {
    position: relative;
    margin-bottom: 140px;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;

    &--rating {
      position: absolute;
      left: 50%;
      top: 1300px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      transform: translateX(-50%);
    }

    &--services {
      margin-top: 60px;
    }
  }

  &__block {
    &--foto {
      flex: 0 1 490px;
    }
    &--video {
      flex: 1 0 700px;
      margin-left: 35px;
    }
  }

  &__header {
    margin-bottom: 30px;
  }

  &__data {
    position: absolute;
    bottom: 16px;
    left: 16px;
    right: 16px;
    display: flex;
    justify-content: space-between;
    padding: 8px 15px;
    font-size: 14px;
    color: $white;
    background-color: #173887;
    border-radius: 5px;

    span {
      white-space: nowrap;
    }

    &-name {
      font-size: 12px;

      &--mobile {
        display: none;
      }
    }
  }

  &__main {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    margin-left: -20px;

    &--foto {
      .reviews {
        &__item {
          margin-bottom: 0;

          &:hover {
            .reviews__name {
              color: $accent;
            }
          }

          &:nth-child(10n + 1) {
            height: 595px;
            width: 100%;
            margin-bottom: 40px;

            .reviews {
              &__foto {
                height: 395px;
                margin-bottom: 20px;
              }

              &__text {
                display: -webkit-box;
                -webkit-line-clamp: 4;
                -webkit-box-orient: vertical;
                overflow: hidden;
              }

              &__original {
                top: auto;
                bottom: 84px;
              }
            }
          }

          &:not(:nth-child(10n + 1)) {
            width: calc(33% - 20px);
            margin-bottom: 20px;
            cursor: pointer;

            .reviews {
              &__foto {
                height: 150px;
              }

              &__content {
                position: absolute;
                display: none;
                top: 100px;
                left: 40px;
                z-index: 10;
                width: 390px;
                padding: 20px;
                background-color: #ffffff;
                border-radius: 10px;
                box-shadow: $shadow-large;
                opacity: 0;
              }

              &__name {
                margin-bottom: 10px;
                font-size: 22px;
              }

              &__text {
                display: -webkit-box;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
                width: 100%;
                height: 60px;
                overflow: hidden;
              }

              &__original {
                display: none;
              }
      
              &__data {
                bottom: 9px;
                left: 9px;
                right: 9px;
                justify-content: center;
                padding: 5px;
                font-size: 10px;

                &-name {
                  display: none;

                  &--mobile {
                    display: inline;
                    font-size: 10px;
                  }
                }
              }

              &__case {
                display: none;
              }

              &__original {
                display: none;
              }
            }

            &:hover {
              .reviews__content {
                display: block;
                opacity: 1;
              }
            }
          }

          &:nth-child(10) {
            margin-bottom: 600px;
          }
        }

        // &__name {
        //   max-height: 35px;
        //   overflow: hidden;
        // }
      }
    }

    &--video {
      .reviews {

        &__name {
          overflow: hidden;
        }

        &__item {
          &:nth-child(3n + 1) {
            height: 595px;
            width: 100%;

            .reviews {
              // &__name {
              //   height: 36px;
              //   overflow: hidden;
              // }
              &__text {
                display: -webkit-box;
                -webkit-line-clamp: 4;
                -webkit-box-orient: vertical;
                overflow: hidden;
                height: 78px;
              }
            }
          }

          &:not(:nth-child(3n + 1)) {
            display: flex;
            flex-direction: column;
            width: calc(50% - 20px);
            height: 490px;
            margin-bottom: 20px;

            .reviews {
              &__video {
                flex: 1 0 auto;
                margin-bottom: 10px;
              }
              &__content {
                display: flex;
                flex-direction: column;
                height: 280px;
              }
              &__name {
                height: 72px;
              }
              &__text {
                display: -webkit-box;
                -webkit-line-clamp: 6;
                -webkit-box-orient: vertical;
                height: 116px;
                overflow: hidden;
              }
              &__original {
                top: 85px;
              }
            }
          }

          &:nth-child(3) {
            margin-bottom: 600px;
          }
        }
      }
    }
  }

  &__title {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 26px;

    &--big {
      margin-bottom: 10px;
      font-size: 34px;
    }
  }

  &__subtitle {
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;
  }

  &__line {
    display: flex;
    justify-content: flex-start;
    min-height: 65px;
    padding-right: 50px;
  }

  &__lable {
    color: $grey-2;
  }

  &__item {
    position: relative;
    display: block;
    margin-left: 20px;
    margin-bottom: 40px;
  }

  &__foto {
    position: relative;
    overflow: hidden;
    border-radius: 10px;

    img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__case, &__debt {
    text-align: center;
  }

  &__video {
    margin-bottom: 20px;
    overflow: hidden;
  }

  &__inner {
    position: relative;
  }

  &__content {
    display: block;
    &:hover {
      .reviews__name {
        color: $accent;
      }
    }
  }

  &__name {
    margin-bottom: 70px;
    font-weight: 700;
    font-size: 26px;
    color: $default;
    transition: color $fastdown;
  }

  &__original {
    position: absolute;
    top: 50px;
    left: 0;
    display: flex;
    align-items: flex-start;

    .original-link {
      margin-right: 15px;
    }
  }

  &__text {
    font-size: 14px;
    color: $grey-2;

    p {
      display: inline;
      margin-bottom: 0;
    }
  }

  &__detailed {
    display: none;
    font-size: 16px;
    color: $accent;
  }

  &__btn {
    &--video {
      display: none;
      margin-top: 0;
    }
    &--foto {
      display: none;
    }

    &--double {
      margin-top: 30px;
      width: 275px;
    }
  }

  // .navigation {
  //   position: absolute;
  //   bottom: -60px;
  //   left: 50%;
  //   transform: translateX(-50%);
  // }

  @include desktop-1500 {
    &__row {
      &--rating {
        top: 1250px;
      }
    }

    &__block {
      &--foto {
        flex: 0 1 425px;
      }
      &--video {
        flex: 1 0 640px;
        margin-left: 30px;
      }
    }

    &__main {
      &--foto {
        .reviews {
          &__item {
            &:nth-child(10n + 1) {
              height: 610px;

              .reviews {
                &__foto {
                  height: 365px;
                }

                &__original {
                  bottom: 90px;
                }
              }
            }

            &:not(:nth-child(10n + 1)) {
              .reviews {
                &__foto {
                  height: 125px;
                }
                &__data {
                  bottom: 3px;
                  left: 3px;
                  right: 3px;
                  padding: 5px 3px;
                  text-align: center;
                }
              }
            }
          }
        }
      }

      &--video {
        .reviews {
          &__item {
            &:nth-child(3n + 1) {
              height: 560px;
            }

            &:not(:nth-child(3n + 1)) {
              height: 465px;

              .reviews {
                &__original {
                  span {
                    font-size: 10px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  @include desktop {
    &__wrap {
      margin-top: 50px;
    }

    &__row {
      &--rating {
        top: 1650px;
      }
    }

    &__block {
      &--foto {
        flex: 0 1 360px;
      }
      &--video {
        flex: 1 0 540px;
        margin-left: 30px;
      }
    }

    &__main {
      &--foto {
        .reviews {
          &__item {
            &:nth-child(10n + 1) {
              height: 170px;
              margin-bottom: 18px;

              .reviews {
                &__content {
                  position: absolute;
                  display: none;
                  top: 100px;
                  left: 40px;
                  z-index: 1;
                  width: 390px;
                  padding: 20px;
                  background-color: #ffffff;
                  border-radius: 10px;
                  box-shadow: $shadow-large;
                  opacity: 0;
                }
                &__text {
                  display: -webkit-box;
                  -webkit-line-clamp: 3;
                  -webkit-box-orient: vertical;
                  width: 100%;
                  height: 60px;
                  overflow: hidden;
                }
                &__original {
                  display: none;
                }
                &__data {
                  bottom: 3px;
                  left: 3px;
                  right: 3px;
                  justify-content: center;
                  padding: 5px;
                  font-size: 10px;

                  span {
                    font-size: 10px;
                  }

                  &-name {
                    display: inline;
                    &--mobile {
                      display: none;
                    }
                  }
                }
                &__case {
                  display: none;
                }
                &__original {
                  display: none;
                }
              }

              &:hover {
                .reviews__content {
                  display: block;
                  opacity: 1;
                }
              }
            }

            &:nth-child(10) {
              margin-bottom: 0;
            }

            &:nth-child(13n + 1) {
              height: 360px;
              width: 100%;
              margin-bottom: 18px;

              .reviews {
                &__foto {
                  height: 100%;
                }
                &__text {
                  -webkit-line-clamp: 3;
                }
                &__data {
                  bottom: 9px;
                  left: 9px;
                  right: 9px;
                  justify-content: space-between;
                  padding: 5px 10px;
                  font-size: 10px;

                  span {
                    font-size: 10px;
                  }

                  &-name {
                    display: inline;
                    &--mobile {
                      display: none;
                    }
                  }
                }

                &__case {
                  display: block;
                }
              }
            }

            &:not(:nth-child(13n + 1)) {
              width: calc(50% - 20px);

              .reviews {
                &__foto {
                  height: 170px;
                }

                &__data {
                  

                  &-name {
                    display: none;
                    &--mobile {
                      display: inline;
                    }
                  }
                }
              }
            }

            &:nth-child(13) {
              margin-bottom: 500px;

              .reviews {
                &__data {
                  bottom: 3px;
                  left: 3px;
                  right: 3px;
                  padding: 5px;
                  font-size: 10px;
                }

                &__case {
                  display: none;
                }
              }
            }
          }

          &__name {
            margin-bottom: 10px;
            font-size: 22px;
          }
        }
      }

      &--video {
        .reviews {
          &__content {
            height: 185px;
          }
          
          &__original {
            top: 35px;
          }

          &__item {
            &:nth-child(3n + 1) {
              height: 480px;
              margin-bottom: 27px;

              .reviews {
                &__video {
                  margin-bottom: 15px;
                }
                &__content {
                  height: auto;
                }
                &__name {
                  height: 30px;
                  margin-bottom: 55px;
                  font-size: 20px;
                }
              }
            }

            &:not(:nth-child(3n + 1)) {
              width: 100%;
              height: 480px;
              margin-bottom: 27px;

              .reviews {
                &__video {
                  margin-bottom: 15px;
                }
                &__name {
                  height: 30px;
                  margin-bottom: 55px;
                  font-size: 20px;
                }
                &__content {
                  height: auto;
                }
                &__text {
                  display: -webkit-box;
                  -webkit-line-clamp: 4;
                  -webkit-box-orient: vertical;
                  height: 78px;
                  overflow: hidden;
                }

                &__original {
                  top: 35px;

                  span {
                    font-size: 12px;
                  }
                }
              }
            }

            &:nth-child(3) {
              margin-bottom: 500px;
            }
          }
        }
      }
    }

    &__data {
      justify-content: flex-start;
      text-align: center;
    }

    &__lable {
      font-size: 14px;
    }

    &__title {
      font-size: 24px;
    }

    &__text {
      margin-bottom: 10px;
    }

    &__name {
      font-size: 24px;
    }
  }

  @include laptop {
    margin-top: 70px;

    &__wrap {
      margin-top: 40px;
    }

    &__row {
      &--rating {
        top: 1400px;
      }
    }

    &__line {
      min-height: 64px;
    }

    &__block {
      &--foto {
        flex: 0 1 290px;
      }

      &--video {
        flex: 1 0 380px;
      }
    }

    &__header {
      min-height: 145px;
      margin-bottom: 20px;
    }

    &__main {
      &--foto {
        .reviews {
          &__item {
            &:nth-child(10n + 1) {
              height: 140px;
              margin-bottom: 18px;

              .reviews {
                &__content {
                  top: 130px;
                  left: 70px;
                  width: 280px;
                }
              }
            }

            &:not(:nth-child(10n + 1)) {
              height: 140px;
              margin-bottom: 18px;

              .reviews {
                &__content {
                  top: 130px;
                  left: 70px;
                  width: 280px;
                }
                &__name {
                  font-size: 20px;
                }
              }
            }

            &:nth-child(13n + 1) {
              height: 290px;
              margin-bottom: 18px;

              .reviews {
                &__foto {
                  height: 100%;
                }

                &__debt {
                  .reviews {
                    &__data {
                      &-name {
                        display: none;

                        &--mobile {
                          display: inline;
                        }
                      }
                    }
                  }
                }
              }
            }

            &:not(:nth-child(13n + 1)) {
              width: calc(50% - 20px);

              .reviews {
                &__foto {
                  height: 140px;
                }
              }
            }

            &:nth-child(13) {
              margin-bottom: 400px;
            }
          }

          &__name {
            font-size: 20px;
          }

          &__content {
            top: 130px;
            left: 70px;
            width: 240px;
          }
        }
      }

      &--video {
        .reviews {
          &__item {
            &:nth-child(3n + 1) {
              height: 400px;
              margin-bottom: 19px;
            }

            &:not(:nth-child(3n + 1)) {
              height: 400px;
              margin-bottom: 19px;
            }

            &:nth-child(3) {
              margin-bottom: 400px;
            }
          }
        }
      }
    }
  }

  @include tablets {
    margin-top: 30px;

    &__row {
      &--rating {
        display: none;
      }

      &--services {
        flex-direction: column;
        margin-top: 40px;
      }
    }

    &__block {
      &--foto {
        display: none;
      }

      &--video {
        margin-left: 0;
      }
    }

    &__main {
      margin-left: 0;

      &--video {
        .reviews {
          &__item {
            &:nth-child(3n + 1) {
              height: auto;
              margin-bottom: 70px;

              .reviews {
                &__text {
                  display: block;
                  display: -webkit-box;
                  -webkit-line-clamp: 4;
                  -webkit-box-orient: vertical;
                  min-height: 76px;
                }
                &__content {
                  height: auto;
                }
              }
            }

            &:not(:nth-child(3n + 1)) {
              height: auto;
              margin-bottom: 70px;

              .reviews {
                &__text {
                  display: block;
                  display: -webkit-box;
                  -webkit-line-clamp: 4;
                  -webkit-box-orient: vertical;
                  min-height: 76px;
                }

                &__content {
                  height: auto;
                }
              }
            }

            &:nth-child(3) {
              margin-bottom: 550px;
            }

            &:nth-child(6) {
              margin-bottom: 550px;
            }
          }

          &__detailed {
            position: absolute;
            bottom: -25px;
            left: 0;
            display: block;
          }
        }
      }
    }

    &__item {
      margin-left: 0;
    }
    
    &__header {
      display: none;
    }

    &__title {
      font-size: 20px;
    }

    &__text {
      margin-bottom: 0;
    }

    &__detailed {
      font-size: 16px;
    }

    &__btn {
      &--double {
        display: none;
      }

      &--video {
        display: flex;
      }
    }
  }

  @include phones {
    &__wrap {
      margin-top: 20px;
    }

    &__block {
      &--video {
        flex: 0 1 530px;
      }
    }

    &__title {
      font-size: 18px;
    }

    &__subtitle {
      font-size: 16px;
    }

    &__main {
      &--video {
        .reviews {
          &__item {
            display: flex;
            flex-direction: column;

            &:nth-child(3n + 1) {
              height: auto;

              .reviews {
                &__video {
                  flex: none;
                  margin-bottom: 10px;
                }
                &__name {
                  height: 52px;
                  margin-bottom: 105px;
                }
                &__text {
                  margin-bottom: 0;
                }
              }
            }
            &:not(:nth-child(3n + 1)) {
              height: auto;

              .reviews {
                &__video {
                  flex: none;
                  margin-bottom: 10px;
                }
                &__name {
                  height: 52px;
                  margin-bottom: 105px;
                }
                &__text {
                  margin-bottom: 0;
                }
                &__original {
                  top: 65px;
                  flex-direction: column;
                  width: 170px;
                }
              }
            }

            &:nth-child(3) {
              margin-bottom: 410px;
            }

            &:nth-child(6) {
              margin-bottom: 410px;
            }
          }

          &__original {
            top: 65px;
            flex-direction: column;
            width: 170px;
          }
        }
      }
    }
  }

  @include small {
    &__main {
      &--video {
        .reviews {
          &__item {
            &:nth-child(3) {
              margin-bottom: 380px;
            }

            &:nth-child(6) {
              margin-bottom: 380px;
            }
          }
        }
      }
    }
  }
}

.slider-reviews {
  position: absolute;
  display: none;
  width: 100%;
  margin: 60px 0;

  &--first {
    top: 1580px;
  }

  &--second {
    top: 3700px;
  }

  &--services {
    position: static;
    margin: 0;
  }

  &__item {
    position: relative;
    overflow: hidden;
    height: 400px;
    border-radius: 10px;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        to bottom,
        rgba(#000000, 0) 30%,
        rgba(#000000, 1)
      );
    }
  }

  &__foto {
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__content {
    position: absolute;
    bottom: 20px;
    left: 20px;
    right: 20px;
    z-index: 2;
    padding: 5px 10px;
    background-color: #173887;
    border-radius: 5px;
  }

  &__name {
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 18px;
    color: $white;
  }

  &__text {
    display: none;
  }

  &__detailed {
    font-size: 16px;
    color: #90AFFD;
  }

  .swiper {
    &-container {
      overflow: visible;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        z-index: 2;
        height: 100%;
        width: 100%;
        background-color: $white;
      }
    }
  }

  @include tablets {
    display: block;
    width: 540px;
  }

  @include phones {
    max-width: 280px;
    margin: 40px 0;

    &--first {
      top: 1500px;
    }

    &--second {
      top: 3425px;
    }

    &--services {
      margin: 0;
    }

    &__item {
      height: 280px;
    }

    &__name {
      font-size: 14px;
      letter-spacing: 1px;
    }

    &__detailed {
      font-size: 14px;
    }
  }

  @include small {
    &__item {
      height: 250px;
    }
  }
}

.review {
  &__video {
    margin-bottom: 40px;
  }

  &__foto {
    overflow: hidden;
    margin-bottom: 40px;
    border-radius: 10px;

    img {
      width: 100%;
    }
  }

  &__main {
    display: grid;
    grid-template-columns: auto 310px;
    grid-template-rows: repeat(2, auto);
  }

  &__block {

    &--name {
      grid-column: 1/2;
      grid-row: 1/2;
    }

    &--content {
      grid-column: 1/2;
      grid-row: 2/3;
    }

    &--aside {
      grid-column: 2/3;
      grid-row: 1/3;
      padding-left: 30px;
    }
  }

  &__row {
    margin-bottom: 40px;
  }

  &__original {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 25px;

    .original-link {
      margin-right: 0;
    }
  }

  &__name {
    margin-bottom: 5px;
    font-size: 26px;
  }

  &__case {
    margin-bottom: 0;
  }

  &__subtitle {
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 18px;
    color: $default;

    &--small {
      margin-bottom: 15px;
      font-size: 16px;
    }
  }

  &__text {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;
  }

  &__content {
    font-weight: 500;
    font-size: 14px;
    color: $grey-2;

    &--foto {
      font-size: 18px;
    }

    p {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  &__transcript {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 40px;
    color: $grey-2;

    p {
      position: relative;
      display: block;
      margin-bottom: 20px;
      padding-left: 28px;
      font-size: 18px;
      font-style: normal;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  @include desktop {
    &__main {
      grid-template-columns: auto;
      grid-template-rows: repeat(3, auto);
    }
  
    &__block {
  
      &--name {
        grid-column: 1/2;
        grid-row: 1/2;
      }
  
      &--content {
        grid-column: 1/2;
        grid-row: 3/4;
      }
  
      &--aside {
        grid-column: 1/2;
        grid-row: 2/3;
        padding-left: 0;
        margin-bottom: 25px;
      }
    }

    &__row {
      margin-bottom: 25px;
    }

    &__original {
      margin-bottom: 5px;
    }
  }

  @include laptop {
    &__video {
      margin-bottom: 25px;
    }

    &__foto {
      margin-bottom: 25px;
    }

    &__subtitle {
      margin-bottom: 10px;
      font-size: 14px;
    }

    &__content {
      &--foto {
        font-size: 16px;
      }
    }
  }

  @include tablets {
    &__name {
      font-size: 22px;
    }
  }

  @include phones {
    &__name {
      font-size: 18px;
    }

    &__case {
      font-size: 14px;
    }

    &__text {
      font-size: 14px;
    }

    &__transcript {
      margin-bottom: 40px;
      color: $grey-2;

      p {
        margin-bottom: 10px;
        padding-left: 23px;
        font-size: 16px;
      }
    }

    &__content {
      &--foto {
        font-size: 14px;
      }
    } 
  }
}

.transcript {
  display: none;

  &__hero {
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 14px;
  }
}

.reviews-vk {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  margin-left: -20px;

  &__item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: calc(50% - 20px);
    height: 200px;
    margin-left: 20px;
    margin-bottom: 20px;
    overflow: hidden;
    background-color: $grey;
    border-radius: 10px;
    cursor: pointer;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      min-height: 100%;
      object-fit: cover;
      object-position: center;
      transition: opacity $fastdown;
    }

    &:nth-child(5n + 1) {
      width: 100%;
      height: auto;
      padding-top: 56.25%;

      .reviews-vk {
        &__content {
          width: 80%;
        }

        &__title {
          font-size: 26px;
        }
      
        &__btn {
          width: 150px;
          padding: 10px;
          font-size: 16px;
      
          svg {
            width: 20px;
            height: 20px;
          }
        }
      }
    }
  }

  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
    transform: translate(-50%, -50%);
  }

  &__title {
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 16px;
    color: $white;
    text-align: center;
  }

  &__btn {
    width: 110px;
    padding: 5px;
    font-size: 14px;
    border-radius: 7px;

    svg {
      display: block;
      width: 14px;
      height: 14px;
      margin-right: 5px;
      fill: $white;
    }
  }

  &__btn-show {
    min-width: 170px;
    margin: 0 auto;
    margin-top: 20px;
    font-size: 14px;
    transform: translateX(10px);
  }

  @include laptop {
    margin-left: 0;
    
    &__item {
      width: 100%;
      height: auto;
      padding-top: 56.25%;
      margin-left: 0;
      margin-bottom: 20px;

      &:nth-child(5n + 1) {
        padding-top: 56.25%;

        .reviews-vk {
          &__content {
            width: 90%;
          }
  
          &__title {
            font-size: 16px;
          }
        
          &__btn {
            width: 110px;
            padding: 5px;
            font-size: 14px;
        
            svg {
              width: 14px;
              height: 14px;
            }
          }
        }
      }
    }

    &__btn-show {
      transform: translateX(0);
    }
  }

  @include phones {
    &__item {
      padding-top: 66.25%;

      &:nth-child(5n + 1) {
        padding-top: 66.25%;
      }
    }

    &__title {
      margin-bottom: 15px;
    }

    &__btn-show {
      min-width: 100%;
    }
  }
}