.licenses {

  &__lst {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    @include lst;
    margin-top: 60px;
    margin-bottom: 25px;
  }

  &__item {
    position: relative;
    width: 31.5%;
    margin-bottom: 35px;
    box-shadow: $shadow-large;

    &--book {
      
      &:hover {
        .licenses {
          &__link {
            &--book {
              z-index: 1;
              opacity: 1;
            }
          }
        }
      }
    }
  }

  &__link {

    &--book {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 100px;
      line-height: 100px;
      text-align: center;
      background-color: rgba($white, 0.9);
      opacity: 0;
      transition: opacity $fastdown;
    }
  }

  &__slider {
    display: none;
  }
  
  @include laptop {
    &__item {
      position: relative;
      width: 48%;
    }
  }

  @include tablets {
    &__lst {
      display: none;
    }

    &__item {
      width: 100%;
      padding: 20px;
      box-sizing: border-box;
      box-shadow: none;

      img {
        width: 100%;
        box-shadow: $shadow-small;
      }
    }

    &__slider {
      display: block;
      margin-top: 40px;
    }
  }

  @include phones {
    margin-bottom: 30px;

    &__item {
      padding: 10px;
    }
    &__slider {
      margin-top: 20px;
      margin-left: 10px;
    }
  }
}