.myths {
  &__wrap {
    margin-top: 40px;
  }

  &__row {
    display: flex;
    justify-content: space-between;

    &:not(:last-child) {
      border-bottom: 1px solid #D7DEEF;
    }
  }

  &__col {
    position: relative;
    padding-bottom: 60px;
    padding-top: 120px;

    &::before {
      position: absolute;
      left: 0;
      top: 60px;
      padding: 4px 8px;
      font-weight: 600;
      font-size: 18px;
      color: $white;
    }

    &--myths {
      flex: 1 0 520px;
      padding-right: 35px;
      font-weight: 700;
      font-size: 22px;

      &::before {
        content: '';
        background-color: $pink;
      }
    }

    &--truth {
      font-size: 18px;

      &::before {
        content: '';
        background-color: #35D977;
      }
    }
  }

  @include desktop {
    &__col {
      &--myths {
        flex: 1 0 400px;
      }
    }
  }

  @include laptop {
    &__col {
      &--myths {
        flex: 1 0 320px;
      }
    }
  }

  @include tablets {
    padding-top: 40px;

    &__row {
      flex-direction: column;

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    &__col {
      padding-top: 45px;
      padding-bottom: 25px;

      &::before {
        top: 0;
      }

      &--myths {
        flex: 1 0 auto;
        width: 100%;
        padding-right: 0;
        font-size: 18px;
      }
      &--truth {
        width: 100%;
        font-size: 14px;
      }
    }
  }

  @include phones {
    padding-top: 20px;
  }
}