.header {
  position: relative;
  z-index: 100;
  margin-bottom: 62px;
  background-color: $grey;

  &--mfc {
    background: none;
    margin-bottom: 0;
  }

  &__line {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px 0;

    &--mfc {
      padding: 60px 0;
    }
  }

  &__logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &--mfc {
      margin-left: -40px;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  &__bottom {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: $grey;
    border-top: 1px solid $white;
    border-bottom: 1px solid $white;

    &--fixed {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      

      .menu__link {
        padding: 10px 0;

        &--sub {
          padding: 0;
        }
      }
    }

    &::after,
    &::before {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #d6d6d6;
    }

    &::after {
      top: -2px;
    }

    &::before {
      bottom: -2px;
    }

    &--mfc {
      border: none;

      &::after, &::before {
        display: none;
      }
    }
  }

  &__control {
    display: none;
  }

  @include desktop {
    &__line {
      padding: 40px 0;
    }

    &__social {
      min-width: 280px;
    }

    &__logo {
      &--mfc {
        margin-left: -35px;
      }
    }
  }

  @include laptop {
    margin-bottom: 48px;

    &--mfc {
      margin-bottom: 0;
    }

    &__line {
      padding: 30px 0;
    }

    &__social {
      min-width: auto;
    }
  }

  @include tablets {
    margin-bottom: 0;
    background-color: $white;

        
    &--mfc {
      background: none;
    }
    
    &__line {
      padding: 25px 0;
    }

    &__social {
      display: none;
    }

    &__logo {
      &--mfc {
        margin-left: 0;
      }
    }

    &__links {
      display: none;
    }

    &__control {
      position: fixed;
      z-index: 101;
      top: 40px;
      right: 30px;
      display: block;
    }

    &__bottom {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 100;
      display: none;
      width: 100%;
      height: 100vh;
      overflow-y: scroll;
      padding-top: 25px;
      background-color: rgba($white, 0.95);
      backdrop-filter: blur(4px);

      &::before {
        display: none;
      }

      &.show {
        display: block;
      }
    }
  }

  @include phones {
    &__control {
      top: 33px;
      right: 20px;
    }
  }
}

/*  header   */
.body-no-header {
  .header {
    display: none;
  }

  .footer {
    display: none;
  }
}