.history {

  &__text {
    margin-top: 20px;
    margin-bottom: 20px;
    color: $grey-2;
  }

  &__lst {
    display: flex;
    flex-direction: column;
    width: 800px;
    @include lst;
    margin: 0 auto;
    margin-top: 100px;
    color: $grey-2;
  }

  &__item {
    position: relative;
    display: flex;
    padding-left: 210px;

    &:not(:last-child) {
      margin-bottom: 45px;

      .history {
        &__point {
          &::before {
            content: '';
            position: absolute;
            top: 26px;
            left: 50%;
            width: 1px;
            height: calc(100% + 40px);
            background-color: $accent;
            transform: translateX(-50%);
          }
        }
      }
    }

    &:hover {
      .history {
        &__date {
          transform: scale(1.2);
        }
      }
    }
  }

  &__point {
    position: absolute;
    left: 125px;
    top: 0;
    width: 26px;
    height: 100%;
  }

  &__icon {
    position: relative;
    display: block;
    width: 26px;
    height: 26px;
    background-color: $white;
    border: 1px solid $accent;
    border-radius: 50%;

    &--active {
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 12px;
        height: 12px;
        background-color: $accent;
        border-radius: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &__date {
    position: absolute;
    left: 0;
    top: 0;
    font-size: 26px;
    color: $accent;
    transition: transform $fastdown;
  }

  &__desc {
    font-size: 18px;

    &--active {
      font-weight: 700;
    }
  }

  @include laptop {
    &__lst {
      width: 100%;
    }
  }

  @include tablets {

    &__lst {
      margin-top: 40px;
    }

    &__item {
      padding-left: 0;
      padding-top: 35px;
      

      &:not(:last-child) {
        margin-bottom: 25px;
      }
    }

    &__point {
      display: none;
    }

    &__desc {
      font-size: 16px;
    }

    &__date {
      font-size: 26px;
    }
  }

  @include phones {

    &__text {
      font-size: 14px;
    }

    &__lst {
      margin-top: 25px;
    }

  }
}