.online {

  &__wrap {
    display: flex;
    justify-content: space-between;
    padding: 100px 0;
  }

  &__block {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 50%;

    &--slider {
      position: relative;

    }
    &--form {
      min-height: 600px;
    }
  }

  &__title {
    margin-bottom: 10px;
    font-weight: 800;
    font-size: 140px;
    line-height: 1;
    color: #d6d6d6;
  }

  &__image {
    height: 360px;
    transition: transform 1.2s cubic-bezier(0.2, 0.7, 0.8, 1), opacity 1.2s cubic-bezier(0.2, 0.7, 0.8, 1);

    img {
      object-fit: contain;
      object-position: center;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    transition: transform 1.0s cubic-bezier(0.2, 0.5, 0.5, 1), opacity 1s cubic-bezier(0.2, 0.5, 0.5, 1);
  }

  &__sibtitle {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 22px;
    color: $grey-2;
  }

  &__text {
    margin-bottom: 10px;
    font-weight: 400;
    color: $grey-2;
  }

  .swiper {

    &-block-nav {
      position: relative;
      z-index: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      margin-top: 20px;
      

      &-inner {
        position: relative;
        margin-left: 30px;
      }
    }

    &-pagination {
      position: relative;
      z-index: 10;
      

      &-bullets {
        bottom: 0;
      }

      &-bullet {
        width: 20px;
        height: 20px;
        margin: 0 7px;
        font-weight: 700;
        font-size: 12px;
        line-height: 20px;
        color: $accent;
        text-align: center;
        background: none;
        transform: scale(1);
        transition: $fastdown;
        opacity: 1;

        &:hover {
          background-color: $grey;
        }

        &-active {
          color: $accent;
          background: $grey;

          &:hover {
            background-color: $grey;
          }
        }
      }
    }

    &-button-next, &-button-prev {
      top: auto;
      bottom: 1px;
      z-index: 2;
      margin-top: 0;
      width: 20px;
      height: 20px;
      margin-bottom: 0;
      
      border-radius: 50%;
      transition: all 0.2s ease;
      cursor: pointer;

      &::after {
        display: none;
      }

      svg {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 8px;
        height: 8px;
        fill: $accent;
        transform: translate(-40%, -50%);
        transition: $slowdown;
      }

      &:hover {
        background-color: $grey;
        svg {
          fill: $accent;
        }
      }

      &:active {
        transform: scale(0.9)
      }
    }

    &-button-prev {
      left: -30px;
      svg {
        transform: translate(-60%, -50%) rotate(180deg);
      }
    }

    &-button-next {
      right: -30px;
    }
  }

  &__list {
    display: none;
    @include lst;
    margin-top: 40px;

    &-item {
      margin-bottom: 60px;
    }
  }

  &__icon {
    width: 66px;
    height: 66px;
    margin-bottom: 15px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

    &--1 {
      background-image: url(../img/online/1.svg);
    }
    &--2 {
      background-image: url(../img/online/2.svg);
    }
    &--3 {
      background-image: url(../img/online/3.svg);
    }
  }

  @include desktop {
    &__item {
      width: 360px;
    }

    &__image {
      img {
        height: 340px;
      }
    }
  }

  @include laptop {
    &__item {
      width: 300px;
    }

    &__image {
      img {
        height: 300px;
      }
    }
  }

  @include tablets {

    &__wrap {
      padding: 50px 0;
    }

    &__block {
      &--slider {
        display: none;
      }

      &--form {
        flex-direction: column;
        width: 100%;
      }
    }

    &__list {
      display: flex;
      flex-direction: column;
    }

    &__sibtitle {
      font-size: 20px;
    }
  }

  @include phones {
    &__sibtitle {
      font-size: 18px;
    }
  }
}