.calculation {

  &--single {
    margin-top: 40px;
    margin-bottom: 60px;
  }

  &--inpost {
    margin-top: 50px;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: $shadow-large;
  }

  &__wrap {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 60px;
    padding: 25px;
    background-color: $white;
    border-radius: 20px;
    box-shadow: $shadow-large;

    &--single {
      margin-top: 20px;
    }

    &--inpost {
      margin-top: 0;
      padding: 32px;
      padding-top: 15px;
      border-radius: 0;
      box-shadow: none;
    }
  }

  &__header {
    padding: 32px;
    background-color: $white;
    border-bottom: 2px solid $grey;
  }

  &__title-head {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 22px;
  }

  &__info {
    position: relative;
    padding: 25px;
    padding-left: 60px;
    font-weight: 500;
    font-size: 18px;
    color: $grey-2;
    background-color: #E8F4FF;
    border-radius: 15px;

    &::before {
      content: '';
      position: absolute;
      top: 25px;
      left: 25px;
      width: 26px;
      height: 26px;
      background-image: url(../img/icons/info.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  &__inner {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    margin-bottom: 17px;

    &--inpost {
      margin-bottom: 10px;
    }
  }

  &__tab {
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
  }

  &__content {
    margin-bottom: 20px;

    &--small {
      margin-bottom: 0;
    }
  }

  &__title {
    margin-bottom: 10px;
    padding-right: 140px;
    font-weight: 700;
    font-size: 26px;

    &--single {
      padding-right: 0;
    }

    &--small {
      margin-bottom: 20px;
      padding-right: 80px;
      font-weight: 600;
      font-size: 22px;
    }
  }

  &__text {
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;
  }

  &__variants {
    flex: 2 0 auto;
    display: flex;
    flex-wrap: wrap;
    margin-left: -8px;
  }

  &__radio,  &__checkbox {
    flex: 1 0 auto;
    display: flex;
    align-items: center;
    width: calc(50% - 8px);
    min-height: 60px;
    margin-left: 8px;
    margin-bottom: 8px;
    padding: 5px 20px;
    padding-left: 50px;
    background-color: $white;
    border-radius: 10px;
    box-shadow: $shadow-nano;
    
    .radio__label, .checkbox__label {
      white-space: normal;

      &--small {
        font-weight: 500;
        font-size: 14px;
      }
    }

    .radio__new, .checkbox__new {
      &.zoom {
        animation-name: zoom-radio;
        animation-duration: 0.5s;
        animation-fill-mode: none;
        animation-timing-function: ease-in;
      }
    }
  }

  &__slides {
    position: absolute;
    top: 25px;
    right: 25px;
    font-weight: 700;
    font-size: 18px;
    color: $default;

    &--inpost {
      top: 15px;
    }
  }

  &__nav {
    display: flex;
    margin-top: auto;
  }

  &__btn {
    margin-right: 10px;
    padding: 10px 18px;
    font-size: 14px;
    border-radius: 7px;

    &--prev {
      background: linear-gradient(180deg, #959FB9 0%, #818AA3 100%);
      box-shadow: 0px 4px 14px rgba(0, 37, 102, 0.18);

      &.disabled {
        opacity: 0.3;
      }

      &:hover {
        background: linear-gradient(180deg, #959FB9 0%, #818AA3 100%);
        box-shadow: 0px 4px 14px rgba(0, 37, 102, 0.18);
      }
    }
  }

  @include desktop-1500 {
    &__variants {
      &--single {
        min-height: 220px;
      }
    }
  }

  @include desktop {

    &__variants {
      &--single {
        flex-direction: column;
        margin-left: 0;

        .calculation__radio {
          width: 100%;
          margin-left: 0;
        }
      }
    }

    &__info {
      font-size: 16px;
    }

    &__title {
      &--small {
        font-size: 20px;
      }
    }

    &__slides {
      &--inpost {
        font-size: 16px;
      }
    }
  }

  @include laptop {
    &__wrap {
      min-height: 520px;

      &--inpost {
        min-height: 360px;
      }
    }

    &__info {
      padding: 15px;
      padding-left: 45px;
      font-size: 14px;
      border-radius: 10px;

      &::before {
        top: 15px;
        left: 15px;
        width: 22px;
        height: 22px;
      }
    }

    &__variants {
      flex-direction: column;
      margin-left: 0;
    }

    &__radio, &__checkbox {
      width: 100%;
      margin-left: 0;

      &--small {
        flex: 0 0 auto;
      }
    }
  }

  @include tablets {
    &__wrap {
      min-height: 550px;
      margin-top: 40px;

      &--single {
        margin-top: 30px;
        margin-bottom: 60px;
      }

      &--inpost {
        min-height: 400px;
        margin-top: 0;
        padding: 20px;
        padding-top: 10px;
        border-radius: 15px;
      }
    }

    &__header {
      padding: 20px;
    }

    &__title-head {
      margin-bottom: 10px;
      font-weight: 700;
      font-size: 18px;
    }

    &__info {
      border-radius: 7px;
    }

    &__slides {
      font-weight: 500;
      font-size: 16px;

      &--inpost {
        top: 10px;
        font-weight: 700;
      }
    }

    &__title {
      padding-right: 115px;
      font-size: 22px;

      &--small {
        margin-bottom: 10px;
        padding-right: 60px;
        font-size: 18px;
      }
    }

    &__btn {
      padding: 10px 25px;
    }
  }

  @include phones {
    &__wrap {
      min-height: 480px;
      padding: 20px;
      padding-top: 40px;

      &--inpost {
        min-height: 400px;
        padding: 15px;
        padding-top: 10px;
      }
    }

    &__header {
      padding: 15px;
      padding-bottom: 10px;
      font-size: 16px;
    }

    &__info {
      padding: 10px;
      padding-left: 40px;
      padding-right: 20px;
      font-size: 14px;

      &::before {
        top: 10px;
        left: 10px;
        width: 22px;
        height: 22px;
      }
    }

    &__title {
      margin-bottom: 5px;
      padding-right: 0;
      font-size: 18px;

      &--small {
        margin-bottom: 10px;
        padding-right: 50px;
        font-size: 16px;
      }
    }

    &__text {
      margin-bottom: 20px;
      font-size: 14px;
    }

    &__slides {
      top: 18px;
      right: 20px;
      font-size: 14px;

      &--inpost {
        top: 10px;
      }
    }

    &__radio, &__checkbox {
      padding: 5px 15px;
      padding-left: 40px;
      min-height: 40px;
      border-radius: 7px;

      .radio__label, .checkbox__label {
        font-size: 12px;

        &--small {
          font-size: 12px;
        }
      }
    }
  }

  @include small {
    &__wrap {
      min-height: 550px;

      &--inpost {
        min-height: 340px;
      }
    }
  }
}