.content-page {
  position: relative;
  padding: 100px 0;
  font-weight: 400;
  font-size: 18px;
  color: $default;

  h1,
  h2,
  h3 {
    color: $default;
  }

  h1 {
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 34px;
    line-height: 1.45;
    text-align: center;

    @include desktop {
      margin-bottom: 20px;
      font-size: 32px;
    }

    @include laptop {
      font-size: 30px;
    }

    @include tablets {
      font-size: 26px;
      text-align: left;
    }

    @include phones {
      font-size: 22px;
    }
  }

  h2 {
    margin-bottom: 15px;
    font-weight: 500;
    font-size: 26px;
    text-align: center;
  }

  p {
    font-weight: 400;
  }
  
  a {
    color: $grey-2;

    &:hover {
      color: lighten($accent, 20%);
      opacity: 0.8;
    }
  }

  ol {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include lst;
    margin-top: 40px;
    margin-bottom: 25px;
    padding: 0;
    text-align: center;
    counter-reset: list-counter;

    li {
      position: relative;
      padding-left: 30px;
      font-weight: 700;
      font-size: 26px;
      text-align: center;

      &::before {
        position: relative;
        top: 0;
        left: -5px;
        content: counter(list-counter) ".";
        counter-increment: list-counter;
      } 

      &:not(:last-child) {
        margin-bottom: 25px;
      }
    } 

    ol {
      counter-reset: list-counter-2;
      align-items: flex-start;
      margin-top: 30px;

      li {
        position: relative;
        margin-left: -25px;
        padding-left: 35px;
        font-weight: 400;
        font-size: 18px;
        text-align: left;

        &::before {
          position: absolute;
          top: 0;
          left: -5px;
          content: counter(list-counter) "." counter(list-counter-2) ".";
          counter-increment: list-counter-2;
        }

        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }

      ol {
        margin-top: 0;

        li {
          margin-top: 15px;
          padding-left: 30px;

          &::before {
            content: "";
          }

          &:not(:last-child) {
            margin-bottom: 10px;
          }
        }
      }
    }
  }

  &--politics {
    ol {
      margin-bottom: 25px;
      counter-reset: list-politics;
  
      li {
        margin-bottom: 10px;
        font-size: 18px;
  
        &::before {
          position: absolute;
          top: 0;
          left: 0;
          content: counter(list-politics) ".";
          counter-increment: list-politics;
        } 
      } 
    }
  }

  @include desktop {
    padding: 80px 0;
  }

  @include laptop {
    padding: 40px 0;

    h1 {
      font-size: 22px;
    }

    h2 {
      font-size: 22px;
    }
  }

  @include tablets {
    padding: 20px 0;

    h1 {
      text-align: center;
    }

    h2 {
      font-size: 20px;
    }

    p {
      font-size: 16px;
    }

    ol {
  
      li {
        font-size: 18px;
      }
  
      ol {
  
        li {
          padding-left: 25px;
          font-size: 14px;
        }
      }
    }

    &--politics {
      ol {
        li {
          font-size: 16px;
        }
      }
    }
  }
}

.requisites {
  display: flex;
  justify-content: space-between;

  &--column {
    flex-direction: column;
  }

  &__block {
    width: 50%;
    padding: 35px;
    border-radius: 10px;
    box-shadow: $shadow-large;

    &:first-child {
      margin-right: 35px;
    }

    &--only {
      width: 100%;
      margin-right: 0;
      padding: 0;
      box-shadow: none;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 22px;
    color: $default;

    &--big {
      margin-bottom: 25px;
      font-size: 26px;
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
  }

  &__call {
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;

    &--first {
      width: 30%;
      padding-right: 30px;
    }

    &--second {
      width: 70%;
    }
  }

  ul {
    @include lst;
    font-weight: 500;
    font-size: 16px;
    color: $default;
  }

  li {
    margin-bottom: 15px;

    span {
      color: $grey-2;
    }
  }


  @include laptop {
    flex-direction: column;

    &__block {
      width: 100%;
      padding: 25px;
      box-shadow: $shadow-small;

      &--only {
        padding: 0;
        box-shadow: none;
      }
  
      &:first-child {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }

    &__title {
      &--big {
        margin-bottom: 15px;
        font-size: 22px;
      }
    }

    &__call {
      font-size: 16px;
    }
  }

  @include tablets {
    &__block {
      padding: 15px;

      &--only {
        padding: 0;
        box-shadow: none;
      }
      
      &:first-child {
        margin-bottom: 15px;
      }
    }

    &__title {
      margin-bottom: 15px;
      font-size: 18px;
    }
  
    ul {
      font-size: 14px;
    }

    li {
      margin-bottom: 10px;
    }
  }

  @include phones {
    &__row {
      flex-direction: column;
    }

    &__call {
      &--first {
        width: 100%;
        padding-right: 0;
        font-weight: 600;
      }

      &--second {
        width: 100%;
      }
    }
  }
}