.navbar {
  position: fixed;
  left: 0;
  bottom: 30px;
  z-index: 50;
  display: flex;
  min-height: 60px;

  &--mobile {
    display: none;
    box-shadow: $shadow-large;
  }

  &--desktop {
    z-index: -1;
    height: 145px;
    overflow: hidden;
    opacity: 0;
    background-color: #EEF3FF;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    box-shadow: $shadow-large;
    transition: width 0.3s linear;
  }

  &--show {
    z-index: 50;
    opacity: 1;
    transform: translateX(0);
    transition: transform $middledown, opacity $slowdown;
  }

  &--close {
    transform: translateX(-610px);
  }

  &__close {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    width: 36px;
    height: 36px;

    &::before, &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 15px;
      height: 2px;
      background-color: $default;
      transition: background-color $fastdown;
    }

    &::before {
      transform: translate(-50%, -50%) rotate(-45deg);
    }

    &::after {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &:hover {
      &::before, &::after {
        background-color: $accent;
      }
    }
  }

  &__toggle {
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 10;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 110px;
    height: 145px;
    padding-left: 20px;
    background-color: #EEF3FF;
    cursor: pointer;
  }

  &__icon {
    position: relative;
    width: 64px;
    height: 64px;
    margin-bottom: 5px;
    border-radius: 10px;
    // background-image: url(../img/icons/calc.svg);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__note {
    position: relative;
    height: 32px;
    font-size: 12px;
    text-align: center;
    color: $accent;
  }

  &__wrap {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 700px;
    margin: 0 auto;
    padding: 20px;
    padding-left: 120px;
    opacity: 1;
    transform: translateX(0);
    transition: width 0.2s ease-in, transform 0.3s linear, opacity 0.6s ease-in;
    
    &--hide {
      width: 0;
      height: 0;
      padding-left: 110px;
      opacity: 0;
      transform: translateX(-600px);
      transition: width 0.3s ease-in, transform 0.3s linear;
    }
  }

  &__test {
    display: flex;
    flex-direction: column;
  }

  &__btn {
    font-weight: 400;
    font-size: 16px;
    color: $accent;
    border-radius: 8px;

    &:active {
      color: lighten($accent, 10%);
    }
  }

  @include desktop {
    bottom: 20px;
  }

  @include tablets {
    position: fixed;
    bottom: 10px;
    left: 15px;
    right: 15px;
    z-index: 50;
  
    display: flex;
    align-items: center;
    min-height: 60px;
    background-color: $grey;
    border: 1px solid #ffffff;
    border-radius: 15px;
    transform: translateX(0);

    &--desktop {
      display: none;
    }

    &--mobile {
      display: flex;
    }

    &__wrap {
      width: 100%;
      padding: 12px;
      padding-left: 60px;
      background: none;
      box-shadow: none;

      &--spb {
        flex-wrap: nowrap;
        padding-left: 12px;
      }
    }

    &__btn {
      padding: 8px 20px 7px;
      color: $white;
      box-shadow: 0px 5px 14px rgba(0, 37, 102, 0.25);
    }
  }

  @include phones {
    &__btn {
      padding: 7px 12px;
      font-weight: 500;
      font-size: 12px;
      line-height: 1.4;
    }
  }
}

.nav-calc {
  cursor: pointer;

  &:hover {
    .nav-calc__title {
      color: rgba($accent, 0.8);
    }
  }

  &__icon {
    position: absolute;
    top: 25px;
    left: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 72px;
    height: 72px;
    background-image: url(../img/icons/calc.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 10px;
  }

  &__title {
    margin-bottom: 5px;
    font-weight: 500;
    font-size: 18px;
    white-space: nowrap;
    transition: $fastdown;
  }

  &__text {
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 14px;
    color: #717B97;
  }

  @include tablets {
    margin-bottom: 0;

    &__icon {
      top: 50%;
      left: 12px;
      width: 40px;
      height: 40px;
      box-shadow: 0px 5px 14px rgba(0, 37, 102, 0.25);
      transform: translateY(-50%);
    }

    &__text {
      margin-bottom: 0;
      font-weight: 500;
      line-height: 1.2;

      &--spb {
        max-width: 170px;
        font-size: 12px;
        line-height: 1.4;
      }
    }
  }
}