.modal {
  background-color: #ccc;

  &__overlay {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    background-color: rgba(#E5E5E5, .90);
    backdrop-filter: blur(30px);
  }

  &__close {
    @include no-btn;
    position: absolute;
    top: -26px;
    right: 0;
    z-index: 10;
    width: 16px;
    height: 16px;
    cursor: pointer;

    svg {
      fill: $default;
      width: 100%;
      height: 100%;
      transition: fill $fastdown;
    }

    &--out {
      top: -15px;
      right: -15px;
    }

    &--in {
      top: 10px;
      right: 10px;
    }

    &:hover {
      svg {
        fill: $accent;
      }
    }
  }

  &__wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    width: 600px;
    padding: 40px;
    background-color: #ffffff;
    box-shadow: $shadow-large;
    border-radius: 10px;
    transform: translate(-50%, -50%);

    &--online {
      top: 50%;
      left: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      max-height: 520px;
      width: 440px;
      padding: 40px;
      padding-bottom: 50px;
      transform: translate(-50%, -50%);
    }

    &--test {
      top: 0;
      left: 50%;
      width: 640px;
      padding: 50px 0;
      background: none;
      box-shadow: none;
      transform: translate(-50%, 0);
    }

    &--free, &--sale, &--benefits, &--offers, &--mfc-new {
      top: 0;
      width: auto;
      padding: 50px 20px;
      background: none;
      box-shadow: none;
      transform: translate(-50%, 0);
    }

    &--free-new {
      .modal__close {
        svg {
          fill: $accent;
        }
      }
    }

    &--sale-new {
      .modal__close {
        svg {
          fill: $red;
        }
      }
    }

    &--press-kit {
      top: 0;
      width: 1220px;
      padding: 50px 20px;
      background: none;
      box-shadow: none;
      transform: translate(-50%, 0);
    }
  }

  &__inner {
    position: relative;
    width: 640px;
    margin: 0 auto;

    &--bg {
      width: auto;
      max-width: 1225px;
      padding: 65px;
      background-color: $white;
      border-radius: 10px;
    }

    &--press-kit {
      width: 100%;
      padding: 55px;
      background-color: $white;
      border-radius: 10px;
    }

    &--new {
      width: 700px;
    }
  }

  &__form {
    position: relative;
    z-index: 10;
    margin-top: 20px;
  }

  &__title {
    margin-bottom: 10px;
    font-size: 26px;

    &--big {
      font-size: 34px;
      text-align: center;
    }

    &--test {
      margin-bottom: 35px;
    }
  }

  &__subtitle {
    margin-bottom: 20px;
    font-size: 16px;
    color: $grey-2;
  }

  &__wrap-consult {
    position: relative;
    z-index: 20;
    display: flex;
    flex-wrap: wrap;
  }

  &__consult-time {
    width: 100%;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 22px;
    
    span {
      font-weight: 500;
      font-size: 16px;
      color: #B5BDD1;
    }
  }

  &__datepiker {

    input {
      padding: 5px;
      color: $accent;
      background-color: $grey;
      border: none;
      border-radius: 4px;
    }
  }

  &__master {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 10px;
    padding-left: 95px;
  }

  &__icon {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 80px;
    height: 95px;
    margin-right: 15px;

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 80px;
      height: 80px;
      background: $gr-grey;
      border-radius: 50%;
    }
  }

  &__foto {
    position: relative;
    width: 100%;
    height: 100%;
    background-position: center bottom;
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    min-height: 95px;

    &--recording {
      min-height: 75px;
    }
  }
  &__name {
    margin-bottom: 5px;
    font-size: 18px;
    color: $default;
  }
  &__post {
    color: $grey-2;
  }

  &__date, &__time {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 5px;
    font-size: 18px;
  }

  &__date-item, &__time-item {
    padding-right: 5px;

    &--name {
      margin-right: 10px;
      padding-right: 0;
      color: $grey-2;
    }

    color: $default;
  }

  @include desktop {
    &__title {
      &--big {
        font-size: 28px;
      }
    }

    &__wrap {
      &--press-kit {
        width: 960px;
        padding: 40px 20px;
      }
    }
  }

  @include laptop {
    &__wrap {
      &--press-kit {
        width: 100%;
        padding: 40px 20px 20px;
      }
    }
  }


  @include tablets {

    &__wrap {
      width: 500px;

      &--online {
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        min-height: 100%;
        border-radius: 0;
        transform: translate(0, 0);
      }

      &--recording {
        top: 40px;
        transform: translate(-50%, 0);
      }

      &--press-kit {
        width: 100%;
      }
    }

    &__inner {
      width: 100%;

      &--bg {
        padding: 40px;
      }

      &--press-kit {
        padding: 30px;
      }
    }

    &__date-item, &__time-item {

      &--name {
        width: 100%;
      }
    }
  }

  @include phones {
    &__wrap {
      width: 90%;
      padding: 20px;

      &--test {
        width: 100%;
        min-height: 90vh;
      }

      &--online {
        min-width: 100%;
      }

      &--free, &--sale, &--mfc-new {
        width: 100%;
      }

      &--offers {
        width: 100%;
        padding-top: 40px;
      }

      &--services {
        overflow-y: scroll;
        max-height: 100vh;
        margin-top: 10px;

        .modal__close {
          top: 10px;
          right: 10px;
        }
      }

      &--press-kit {
        width: 100%;
        padding: 40px 20px;
      }
    }

    &__close {
      &--out {
        top: -10px;
        right: -10px;
      }
    }

    &__inner {
      &--bg {
        padding: 20px;
      }

      &--press-kit {
        padding: 20px;
      }
    } 

    &__title {
      font-size: 18px;

      &--big {
        font-size: 22px;
      }

      &--test {
        font-size: 22px;
      }

      &--press-kit {
        text-align: left;
      }
    }

    &__subtitle {
      font-size: 14px;
    }

    &__content { 
      &--recording {
        min-height: 65px;
      }
    }

    &__icon {
      bottom: auto;
      top: 0;
    }

    &__master {
      margin-top: 20px;
      padding-left: 0;
      padding-top: 100px;
    }

    &__date, &__time {
      font-size: 16px;
    }

    &__consult-time {
      font-size: 18px;
    }
  }
}

.hidden {
  display: none;
}

.no-visible {
  visibility: hidden;
}

.external-link {

  &__title {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 26px;
  }

  &__name {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 75px;
    margin-bottom: 10px;
  }

  &__favicon {
    display: flex;
    align-items: center;
    width: 75px;
    height: 75px;
    margin-right: 10px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

    svg {
      width: 85%;
      height: 85%;
    }
  }

  &__text {
    margin-bottom: 20px;
    color: $grey-2;
  }
  &__link {
    font-size: 14px;
  }

  @include phones {
    &__title {
      font-size: 18px;
    }

    &__name {
      flex-direction: column;
      align-items: flex-start;
    }

    &__favicon {
      width: 60px;
      height: 60px;
      margin-right: 0;
      margin-bottom: 5px;
    }

    &__name-resource  {
      font-size: 12px;
    }

    &__text {
      font-size: 12px;
    }
  }
}

.calc-modal {

  &__title {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 26px;
  }

  &__subtitle {
    margin-bottom: 20px;
    font-size: 16px;
    color: $grey-2;
  }

  &__block {
    position: relative;
    bottom: -1px;
    display: flex;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 0;
    padding: 30px 20px;
    overflow: hidden;
    border-top: 2px solid $white;

    &::before {
      content: '';
      position: absolute;
      bottom: -5%;
      left: 0;
      z-index: 3;
      width: 100%;
      height: 10%;
      background-color: $white;
    }
  }

  &__loader {
    margin: 0 auto;
    width: 55px;
    height: 55px;
  }

  &__check {
    margin: 0 auto;
    width: 55px;
    height: 55px;
  }

  &__bg {
    position: absolute;
    left: 0;
    top: 50%;
    z-index: 3;
    width: 100%;
    height: calc(100% + 10px);
    transform: translateY(-50%);
    background: linear-gradient(to top, rgba(255,255,255,1) 0, rgba(255,255,255,1) 15%, rgba(255,255,255,0.1) 45%, rgba(255,255,255,0.01) 50%, rgba(255,255,255,0.1) 55%, rgba(255,255,255,1) 85%,rgba(255,255,255,1) 100%);
  }

  &__inner {
    position: absolute;
    top: -25px;
    left: 50%;
    z-index: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    color: $accent;
    text-align: center;
    transform: translateX(-50%);
    animation-name: analysis;
    animation-duration: 20s;
    animation-delay: 1s;
    animation-fill-mode: forwards;
    animation-iteration-count: 1;
    animation-play-state: paused;
    animation-timing-function: linear;

    &.start {
      animation-play-state: running;
    }

    span {
      margin: 40px 0;
      font-size: 22px;
      white-space: nowrap;
    }
  }

  @include tablets {
    &__inner {
      span {
        font-size: 18px;
      }
    }
  }

  @include phones {
    &__title {
      font-size: 20px;
    }

    &__block {
      padding: 25px 15px;
    }

    &__loader, &__check {
      width: 45px;
      height: 45px;
    }
    &__inner {
      span {
        font-size: 11px;
      }
    }
  }
}



.modal-content {
  display: none;
}

.form-calc, .form-test {
  position: relative;
  margin-top: 0;
}

.answer {
  &.sale {
    .loader {
      border-top: 3px solid $pink;
    }

    .calc-modal__inner {
      color: $pink;
    }

    .form__btn {
      background: $gr-pink;
      transition: all $slowdown;

      &:hover {
        background: $gr-pink-hover;
      }
    }
  }

  &.benefits {
    .loader {
      border-top: 3px solid $green;
    }

    .calc-modal__inner {
      color: $green;
    }

    .form__btn {
      background: $gr-green;
      transition: all $slowdown;

      &:hover {
        background: $gr-green-hover;
      }
    }
  }
}