.error {

  &__wrap {
    display: flex;
    justify-content: space-between;
  }

  &__block {
    width: 50%;

    &--img {
      position: relative;
      overflow: hidden;
    }
    &--text {
      min-height: 600px;
      padding-top: 80px;
    }
  }

  &__title {
    position: relative;
    margin-bottom: 10px;
    font-family: $font-accent;
    font-weight: 800;
    font-size: 140px;
    line-height: 1;
    color: #d6d6d6;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: linear-gradient(to bottom, rgba(255,255,255, 0.1), rgba(255,255,255, 1) 85%);
    }
  }

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 410px;
    margin: 0 auto;
  }

  &__text {
    margin-bottom: 20px;
  }

  &__link {
    width: 260px;
    font-size: 14px;
    box-shadow: $shadow-small;
  }

  &__picture {
    position: absolute;
    left: 50%;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    transform: translateX(-50%);

    &--bottom {
      top: 310px;
      height: 26px;
      width: 500px;
      background-image: url(../img/404/shredder-bottom.svg);
    }

    &--top {
      top: 310px;
      height: 26px;
      width: 500px;
      background-image: url(../img/404/shredder-top.svg);
    }

    &--papper {
      top: 40px;
      width: 400px;
      height: 250px;
      background-image: url(../img/404/document.svg);
      animation-name: shredder;
      animation-play-state: play;
      animation-duration: 8s;
      animation-fill-mode: forwards;
      animation-iteration-count: infinite;
      animation-timing-function: ease-in-out;
    }

    &--hide {
      bottom: 0;
      width: 100%;
      height: 260px;
      background: linear-gradient(to bottom, rgba(255,255,255, 0.5), rgba(255,255,255, 1) 40%);
    }
  }

  @include desktop {
    &__block {
  
      &--img {
        width: 60%;
      }
      &--text {
        width: 40%;
      }
    }

    &__inner {
      width: 100%;
    }
  }

  @include laptop {

    &__block {
  
      &--img {
        width: 55%;
      }
    }

    &__picture {
  
      &--bottom {
        width: 100%;
      }
  
      &--top {
        width: 100%;
      }
  
      &--papper {
        width: 300px;
        height: 200px;
      }
    }

    &__title {
      font-size: 100px;
    }
  }

  @include tablets {
    &__block {
      &--img {
        display: none;
      }

      &--text {
        width: 100%;
        min-height: 500px;
        padding-top: 40px;
      }
    }
  }

  @include phones {
    &__block {
      &--text {
        min-height: 400px;
        padding-top: 20px;
      }
    }
  }
}