.oplata {
  position: relative;
  padding: 100px 0;
  font-weight: 400;
  font-size: 18px;
  color: $default;

  h1,
  h2,
  h3 {
    color: $default;
  }

  h1 {
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 34px;
    line-height: 1.45;
    text-align: center;

    @include desktop {
      margin-bottom: 20px;
      font-size: 32px;
    }

    @include laptop {
      font-size: 30px;
    }

    @include tablets {
      font-size: 26px;
      text-align: left;
    }

    @include phones {
      font-size: 22px;
    }
  }

  h2 {
    margin-top: 35px;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 26px;
  }

  p {
    margin-bottom: 15px;
    font-weight: 400;
  }
  
  a {
    color: $accent;

    &:hover {
      color: lighten($accent, 20%);
      opacity: 0.8;
    }
  }

  @include desktop {
    padding: 80px 0;
  }

  @include laptop {
    padding: 40px 0;

    h1 {
      font-size: 22px;
    }

    h2 {
      font-size: 22px;
    }
  }

  @include tablets {
    padding: 20px 0;

    h2 {
      font-size: 20px;
    }

    p {
      font-size: 16px;
    }
  }
}