.guarantees {
  position: relative;
  margin-bottom: 20px;
  overflow: hidden;
  background-color: $white;
  border-radius: 10px;
  box-shadow: $shadow-large;

  &__head {
    position: relative;
    padding: 18px 25px;

    &::before {
      content: '';
      position: absolute;
      top: 15px;
      right: 20px;
      width: 70px;
      height: 70px;
      background-image: url(../img/icons/doc.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  &__title {
    margin-bottom: 0;
    padding-right: 90px;
    font-weight: 700;
    font-size: 22px;
  }

  &__body {
    padding: 25px;
    padding-top: 5px;
    padding-right: 55px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__number {
    margin-bottom: 10px;
    color: $grey-2;
  }

  @include desktop-1500 {
    &__head {
      &::before {
        top: 10px;
        right: 15px;
        width: 60px;
        height: 60px;
      }
    }
  }

  @include desktop {
    &__title {
      font-size: 20px;
    }
  }

  @include laptop {
    &__head {
      padding: 18px 20px;

      &::before {
        width: 50px;
        height: 50px;
      }
    }

    &__body {
      padding: 20px;
      padding-top: 0;
    }

    &__title {
      padding-right: 60px;
    }

    &__item {
      font-size: 14px;
    }
  }
}
