.others {

  h2 {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 22px;
  }

  p {
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 18px;
  }

  ol {
    padding-left: 20px;

    li {
      margin-bottom: 15px;
      padding-left: 2px;
      font-size: 18px;

      &::marker {
        font-weight: 600;
        color: $accent;
      }

      &::before {
        display: none;
      }
    }

    ul {
      li {
        &::before {
          display: none;
        }
      }
    }
  }

  &__lst {
    @include lst;
    color: $accent;

    &-item {
      position: relative;
      margin-bottom: 25px;
      padding-left: 25px;
      font-weight: 600;
      font-size: 18px;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 10px;
        width: 5px;
        height: 5px;
        background-color: $accent;
        border-radius: 50%;
      }
    }
  }

  .sngl-video {
    display: flex;
    align-items: flex-start;
    margin-bottom: 20px;
    padding: 32px;
    background-color: $grey;
    border-radius: 10px;

    img {
      margin: 0;
    }

    .youtube {
      border-radius: 5px;
    }

    &__block {
      &--left {
        width: 55%;
      }
      &--right {
        width: 45%;
        padding-left: 25px;
      }
    }

    &__title {
      margin-bottom: 7px;
      font-weight: 600;
      font-size: 18px;
    }

    &__text {
      max-height: 80px;
      margin-bottom: 20px;
      overflow: hidden;
      font-size: 14px;
      color: $grey-2;
    }

    &__inner {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: 70px;
      line-height: 1.6;
      color: $grey-2;
    }

    &__examples {
      display: none;
    }

    &__author {
      margin-bottom: 2px;
      font-size: 12px;
    }
    &__name {
      margin-bottom: 2px;
      font-weight: 700;
      font-size: 14px;
    }
    &__post {
      font-size: 14px;
    }

    @include desktop {
      flex-direction: column;

      &__block {
        &--left {
          width: 100%;
          margin-bottom: 20px;
        }
        &--right {
          width: 100%;
          padding-left: 0;
        }
      }
    }

    @include laptop {
      padding: 20px;
    }

    @include phones {
      padding: 15px;

      &__block {
        &--left {
          margin-bottom: 10px;
        }
      }

      &__title {
        font-size: 16px;
      }

      &__name {
        line-height: 1.4;
      }

      &__post {
        font-size: 12px;
        line-height: 1.4;
      }
    }
  }

  @include phones {
    h2 {
      margin-bottom: 10px;
      font-size: 20px;
    }
    
    p {
      margin-bottom: 10px;
      font-size: 16px;
    }

    ol {
      li {
        margin-bottom: 10px;
        font-size: 16px;
      }
    }

    &__lst {
      &-item {
        margin-bottom: 15px;
        font-size: 16px;
      }
    }
  }
}