.specialist {
  &__video {
    margin-bottom: 40px;
    overflow: hidden;
  }

  &__info {
    display: flex;
    flex-direction: row-reverse;
  }

  &__data {
    flex: 1 0 175px;
    margin: 0;
    margin-left: 35px;
    padding: 0;
    list-style: none;

    &-item {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 20px;
    }

    &-count {
      font-weight: 800;
      font-size: 28px;
      line-height: 1;
      color: $accent;
    }

    &-text {
      font-weight: 500;
      font-size: 14px;
      color: $grey-2;
    }
  }

  &__content {
    flex: 0 1 595px;
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 26px;
  }

  &__post {
    margin-bottom: 20px;
  }

  &__text {
    color: $grey-2;
  }

  &__cases {
    margin-bottom: 40px;
  }

  @include laptop {
    &__video {
      margin-bottom: 20px;
    }

    &__info {
      flex-direction: column;
    }

    &__data {
      flex: 0 1 auto;
      display: flex;
      justify-content: flex-start;
      margin-left: 0;

      &-item {
        margin-right: 25px;
      }
    }

    &__content {
      flex: 0 1 auto;
    }

    &__post {
      margin-bottom: 10px;
    }
  }

  @include tablets {
    &__name {
      font-size: 22px;
    }
  }

  @include phones {
    &__name {
      font-size: 18px;
    }
  }
}