.news {
  &--main {
    position: relative;
    margin-top: 100px;
  }

  &__wrap {
    margin-bottom: 140px;

    &--mmedia {
      position: absolute;
      left: 50%;
      top: 1550px;
      width: 100%;
      transform: translateX(-50%);
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
  }

  &__block {
    &--company {
      flex: 0 1 490px;

      .news {
        // &__item {
        //   &:nth-child(3) {
        //     margin-bottom: 800px;
        //   }
        // }

        &__link {
          height: 400px;
        }

        &__foto {
          flex: 0 1 300px;
        }

        &__content {
          flex: 1 0 auto;
        }

        &__description {
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          height: 66px;
          overflow: hidden;
        }
      }
    }

    &--bankruptcy {
      flex: 1 0 700px;
      margin-left: 35px;

      .news {
        &__item {
          width: calc(50% - 20px);
          margin-left: 20px;
          margin-bottom: 40px;

          &:nth-child(5n + 1) {
            width: calc(100%);

            .news {
              &__metka {
                span {
                  font-size: 14px;
                }
              }

              &__link {
                height: 560px;
              }

              &__foto {
                flex: 0 1 400px;
                margin-bottom: 20px;
              }

              &__name {
                overflow: hidden;
              }

              &__description {
                height: 45px;
                overflow: hidden;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
              }
            }
          }

          // &:nth-child(5) {
          //   margin-bottom: 800px;
          // }
        }

        &__metka {
          span {
            font-size: 12px;
          }
        }

        &__link {
          height: 320px;
        }

        &__foto {
          flex: 0 1 200px;
        }

        &__content {
          flex: 1 0 auto;
        }

        &__description {
          display: none;
        }

        &__name {
          max-height: 90px;
          overflow: hidden;
        }
      }
    }

    &--mobile {
      display: none;
    }
  }

  &__header {
    margin-bottom: 30px;
  }

  &__title {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 26px;
  }

  &__main {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-left: -20px;

    &--mmedia {
      .news {
        &__foto {
          position: relative;
          height: 440px;
        }
      }
    }
  }

  &__item {
    position: relative;
    width: 100%;
    margin-left: 20px;
    margin-bottom: 40px;
  }

  &__metka {
    position: absolute;
    top: 15px;
    left: 15px;
    z-index: 10;

    span {
      display: inline-block;
      margin-bottom: 5px;
      padding: 5px 10px;
      font-weight: 700;
      font-size: 14px;
      line-height: 1.2;
      color: $white;
      background-color: $pink;
      border-radius: 5px;
    }
  }

  &__link {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &:hover {
      .news {
        &__name {
          color: rgba($accent, 0.8);
        }
        &__foto {
          img {
            transform: scale(1.15);
          }
        }
      }
    }
  }

  &__foto {
    position: relative;
    height: 200px;
    margin-bottom: 10px;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 10px;
    
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(142, 175, 255, 0.25), rgba(142, 175, 255, 0.25))
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      object-fit: cover;
      transition: transform $slowdown;
    }
  }

  // &__mmedia {
  //   position: absolute;
  //   top: -1px;
  //   left: -1px;
  //   z-index: 1;
  //   width: 172px;
  //   height: 55px;
  //   background-color: #f3f4f4;
  //   background-size: 80%;
  //   background-position: center;
  //   background-repeat: no-repeat;
  //   border-bottom-right-radius: 10px;

  //   &--kp {
  //     background-image: url(../img/news/KP.svg);
  //   }
  //   &--kom {
  //     background-image: url(../img/news/Kommersant.svg);
  //   }
  //   &--perv {
  //     background-image: url(../img/news/Pervyj.svg);
  //   }
  //   &--vedom {
  //     background-image: url(../img/news/Vedomosti.svg);
  //   }
  //   &--inter {
  //     background-image: url(../img/news/Interfax.svg);
  //   }
  //   &--poss {
  //     background-image: url(../img/news/Rossiya-24.svg);
  //   }
  // }

  &__name {
    margin-top: 10px;
    margin-bottom: 0;
    font-weight: 700;
    font-size: 22px;
    line-height: 1.4;
    color: $default;
    transition: color $fastdown;

    &--smi {
      font-size: 26px;
    }
  }

  &__description {
    margin-top: 10px;
    margin-bottom: 0;
    font-weight: 500;
    font-size: 16px;
    color: $grey-2;
  }

  &__information {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-top: 20px;
  }

  &__info {
    display: flex;
    justify-content: flex-start;
    @include lst;
    margin-right: 5px;
  }

  &__info-item {
    margin-right: 10px;
    font-size: 14px;
    color: $grey-2;
  }

  &__taxonomies {
    display: flex;
    flex-wrap: wrap;

    span {
      font-size: 14px;
      color: $accent;

      &:not(:last-child) {
        margin-right: 15px;
      }
    }
  }

  &__btn {
    &--desk{
      display: none;
    }
  }


  @include desktop-1500 {
    &__block {
      &--company {
        flex: 0 1 365px;
      }
    }
  }

  @include desktop {
    &__wrap {
      margin-top: 50px;

      &--top {
        margin-bottom: 0;
      }

      &--mmedia {
        top: 1450px;
      }
    }

    &__block {
      &--company {
        flex: 0 1 365px;

        .news {
          // &__item {
          //   &:nth-child(3) {
          //     margin-bottom: 600px;
          //   }
          // }

          &__link {
            height: 390px;
          }

          &__foto {
            flex: 0 1 220px;
          }
        }
      }

      &--bankruptcy {
        flex: 1 0 530px;

        .news {
          &__item {
            &:nth-child(5n + 1) {
              .news {
                &__link {
                  height: 530px;
                }
              }
            }

            // &:nth-child(5) {
            //   margin-bottom: 600px;
            // }
          }

          &__name {
            max-height: 100px;
            font-size: 18px;
          }
        }
      }
    }

    &__name {
      font-size: 20px;
    }
  }

  @include laptop {
    &--main {
      margin-top: 70px;
    }

    &__wrap {
      margin-bottom: 100px;

      &--top {
        margin-bottom: 0;
      }

      &--mmedia {
        top: 1500px;
      }
    }

    &__block {
      &--company {
        flex: 0 1 365px;

        .news {
          &__link {
            height: 400px;
          }

          &__metka {
            span {
              font-size: 12px;
            }
          }
        }
      }

      &--bankruptcy {
        flex: 1 0 360px;

        .news {
          &__item {
            width: 100%;

            &:nth-child(5n + 1) {
              width: calc(100%);

              .news {
                &__link {
                  height: 290px;
                }

                &__metka {
                  span {
                    font-size: 12px;
                  }
                }

                &__foto {
                  flex: 0 1 200px;
                  margin-bottom: 10px;
                }
              }
            }

            &:nth-child(5) {
              margin-bottom: 40px;
            }
            // &:nth-child(4) {
            //   margin-bottom: 600px;
            // }
          }

          &__link {
            height: 290px;
          }

          &__name {
            font-size: 18px;
          }

          &__description {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
          }
        }
      }
    }

    &__header {
      min-height: 50px;
      margin-bottom: 10px;

      &--mmedia {
        min-height: auto;
      }
    }

    &__mmedia {
      width: 130px;
      height: 40px;
      background-size: 70%;
    }
  }

  @include tablets {

    &--main {
      margin-top: 0;
    }

    &--media {
      margin-bottom: 60px;
    }

    &__wrap {
      margin-top: 20px;

      &--mmedia {
        position: static;
        margin-top: 60px;
        margin-bottom: 60px;
        transform: translateX(0);
      }
    }

    &__row {
      display: none;

      &--mmedia {
        display: block;
      }
    }

    &__block {
      &--mobile {
        display: block;
      }
    }

    &__header {
      min-height: auto;
    }

    &__title {
      margin-bottom: 20px;
      font-size: 22px;
    }

    &__main {
      &--mmedia {
        .news {
          &__foto {
            height: auto;
          }

          &__description,
          &__information {
            display: none;
          }

          &__mmedia {
            width: 110px;
            height: 30px;
          }
        }
      }
    }

    &__metka {
      top: 10px;
      left: 10px;

      span {
        padding: 5px 7px;
        font-size: 12px;
      }
    }

    &__foto {
      height: 240px;
      padding-top: 56.25%;
    }

    &__name {
      font-size: 18px;
    }

    &__description {
      font-size: 14px;
    }

    &__btn {
      margin-top: 20px;

      &--double {
        display: none;
      }
    }
  }

  @include phones {

    &__foto {
      height: 170px;
    }
  }
}

.mmedia {
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 480px;
  }

  &__block {
    &--image {
      position: relative;
      width: 490px;
    }
    &--content {
      flex: 1 0 700px;
      margin-left: 35px;
    }
  }

  &__image {
    position: absolute;
    left: -60px;
    top: 50%;
    width: 450px;
    height: 550px;
    transform: translateY(-50%);
  }

  &__title {
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 34px;
  }

  &__text {
    margin-bottom: 40px;
    font-size: 18px;
  }

  &__btn {
    padding: 15px 20px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    @include lst;
    margin-bottom: 30px;

    &--news {
      display: none;
    }
  }

  &__item {
    width: 22%;
    height: 60px;
    margin-right: 8%;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  @include desktop-1500 {
    &__image {
      left: -120px;
    }
  }

  @include desktop {
    &__wrap {
      min-height: 500px;
    }

    &__block {
      &--image {
        position: relative;
        width: 440px;
      }
      &--content {
        flex: 1 0 540px;
        margin-left: 35px;
      }
    }

    &__image {
      left: -60px;
      transform: translateY(-50%) scale(0.7);
    }

    &__item {
      height: 45px;
      margin-right: 10%;
    }
  }

  @include laptop {
    &__wrap {
      min-height: 450px;
    }

    &__block {
      &--image {
        position: relative;
        width: 440px;
      }
      &--content {
        flex: 1 0 360px;
        margin-left: 35px;
      }
    }

    &__image {
      left: -80px;
      transform: translateY(-50%) scale(0.6);
    }

    &__title {
      margin-bottom: 20px;
      font-size: 28px;
    }

    &__text {
      margin-bottom: 30px;
      font-size: 16px;
    }

    &__item {
      height: 35px;
      margin-bottom: 10px;
    }
  }

  @include tablets {
    &--news {
      padding: 40px 0;
      background-color: $grey;
    }

    &__wrap {
      min-height: auto;
    }

    &__block {
      &--image {
        display: none;
      }
      &--content {
        flex: 1 0 100%;
        margin-left: 0;
      }
    }

    &__title {
      margin-bottom: 10px;
      font-size: 22px;
    }

    &__text {
      margin-bottom: 40px;
      font-size: 14px;
    }

    &__list {
      &--news {
        display: flex;
      }
    }

    &__item {
      margin-bottom: 20px;
    }
  }

  @include phones {
    
    &__list {
      justify-content: flex-start;
    }
    &__item {
      width: 100px;
      height: 30px;
      margin-right: 30px;
    }

    &__btn {
      width: 100%;
    }
  }
}

.news-media {
  margin-left: 0;

  &__period {
    margin-bottom: 25px;
    padding: 15px;
    font-weight: 600;
    color: $grey-2;
    background: linear-gradient(90deg, #F3F4F4 0%, rgba(243, 244, 244, 0) 100%);
    border-radius: 10px;
  }

  &__item {
    position: relative;
    margin-bottom: 30px;
  }

  &__link {
    display: block;
    padding: 25px;
    border-radius: 10px;
    box-shadow: $shadow-large;

    &:hover {
      .news-media {
        &__name {
          color: rgba($accent, 0.8);
          transition: transform $slowdown;
        }
      }
    }
  }

  &__header {
    margin-bottom: 20px;
  }

  &__date {
    margin-bottom: 5px;
    font-size: 12px;
    color: $grey-3;
  }

  &__name {
    margin-bottom: 0;
    font-size: 22px;
    font-weight: 700;
    color: $default;
    transition: transform $slowdown;
  }

  &__description {
    font-weight: 500;
    color: $grey-2;
  }

  @include laptop {
    &__item {
      margin-bottom: 30px;
    }

    &__name {
      font-size: 20px;
    }

    &__foto {
      height: 270px;
      margin-bottom: 130px;
    }

    &__media-info {
      bottom: 115px;
    }

    &__period {
      margin-bottom: 15px;
    }
  }

  @include tablets {

    &__period {
      font-weight: 500;
      font-size: 14px;
    }

    &__name {
      font-size: 18px;
    }

    &__foto {
      height: auto;
      padding-top: 65%;
    }
  }

  @include phones {
    &__item {
      margin-bottom: 15px;
    }

    &__link {
      padding: 20px;
    }

    &__media-info {
      bottom: 155px;
    }

    &__description {
      font-size: 14px;
    }
  }
}

.media-info {
  display: flex;
  justify-content: space-between;
  @include lst;
  margin-bottom: 20px;

  &__item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 50%;
  }

  &__logo {
    margin-right: 15px;

    img {
      max-height: 40px;
      min-width: 40px;
    }
  }

  &__foto {
    position: relative;
    flex: 1 0 50px;
    max-width: 50px;
    height: 50px;
    margin-right: 15px;
    overflow: hidden;
    background: #EDF0F8;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 50%;

    &--sorokin {
      background-image: url(../img/news/commentator/sorokin.png);
    }

    &--bodnar {
      background-image: url(../img/news/commentator/bodnar.png);
    }

    &--efremov {
      background-image: url(../img/news/commentator/efremov.png);
    }

    &--kopyrina {
      background-image: url(../img/news/commentator/kopyrina.png);
    }

    &--okladnikov {
      background-image: url(../img/news/commentator/okladnikov.png);
    }
  }

  &__data {
    display: flex;
    flex-direction: column;
  }

  &__title {
    font-weight: 400;
    font-size: 12px;
    color: $grey-2;
  }

  &__link {
    font-size: 14px;
    white-space: nowrap;
    color: $accent;
  }

  &__author {
    font-size: 14px;
    color: $grey-2;
  }

  @include laptop {
    flex-direction: column;

    &__item {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }

    &__logo {
      width: 55px;
    }

    &__foto {
      flex: 1 0 55px;
      max-width: 55px;
      height: 55px;
    }
  }

  @include phones {

    &__foto, &__logo {
      flex: 1 0 55px;
      max-width: 55px;
    }
  }
}