.career {
  margin-top: 100px;

  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-bottom: 110px;
  }

  &__block {
    &--aside {
      position: relative;
      flex: 0 1 490px;
    }
    &--main {
      flex: 1 0 700px;
      margin-left: 35px;
    } 
  }

  &__mob-title {
    display: none;
    font-weight: 700;
    font-size: 22px;
  }

  &__info {
    width: 100%;
    margin-bottom: 60px;

    &:last-child {
      margin-bottom: 30px;
    }
  }

  &__title {
    margin-bottom: 60px;
    font-weight: 700;
    font-size: 26px;
    line-height: 1.45;
    text-align: center;

    span {
      color: $red;
    }
  }

  &__subtitle {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 26px;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__content {
    p {
      margin-bottom: 0;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    ul {
      @include lst;

      li {
        position: relative;
        
        padding-left: 25px;

        &:not(:last-child) {
          margin-bottom: 20px;
        }

        &::before {
          content: '';
          position: absolute;
          top: 9px;
          left: 4px;
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background-color: $accent;
        }
      }
    }
  }

  &__foto {
    height: 440px;
    overflow: hidden;
    margin-bottom: 20px;
    border-radius: 10px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 26px;
  }

  &__description {
    margin-bottom: 20px;
    font-size: 14px;
    color: $grey-2;
  }

  &__link-slider {
    padding: 10px 30px;
    font-size: 14px;
    line-height: 1.2;
    border-radius: 8px;
    box-shadow: $shadow-small;
  }

  &__slider {
    position: relative;
    width: 700px;
  }

  &__vacancies {
    width: 85%;
    margin: 0 auto;
    margin-bottom: 100px;
  }

  .swiper {
    &-container {
      width: 100%;
      margin-top: 30px;
    }

    &-button-next, &-button-prev {
      top: 200px;
      z-index: 2;
      margin-top: 0;
      width: 34px;
      height: 34px;
      margin-bottom: 0;
      background-color: $white;
      border-radius: 50%;
      transition: all 0.2s ease;
      cursor: pointer;

      &::after {
        display: none;
      }

      svg {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 12px;
        height: 12px;
        fill: $accent;
        transform: translate(-40%, -50%);
        transition: $slowdown;
      }

      &:hover {
        background-color: $grey;
        svg {
          fill: $accent;
        }
      }

      &:active {
        transform: scale(0.9)
      }
    }

    &-button-prev {
      left: -15px;
      svg {
        transform: translate(-60%, -50%) rotate(180deg);
      }
    }

    &-button-next {
      right: -15px;
    }
  }

  @include desktop {

    &__wrap {
      margin-bottom: 50px;
    }

    &__block {
      &--main {
        flex: 1 0 600px;
      }
    }

    &__slider {
      width: 600px;
    }

    &__vacancies {
      width: 100%;
    }

    &__foto {
      height: 360px;
    }

    .swiper {
      &-button-next, &-button-prev {
        top: 170px;
      }
    }
  }

  @include laptop {
    margin-top: 70px;

    &__block {
      &--aside {
        flex: 0 2 275px;
      }
      &--main {
        flex: 0 1 400px;
        margin-left: 25px;
      }
    }

    &__info {
      margin-bottom: 40px;
    }

    &__slider {
      width: 400px;
    }

    &__foto {
      height: 240px;
    }

    .swiper {
      &-button-next, &-button-prev {
        top: 100px;
      }
    }
  }

  @include tablets {
    margin-top: 30px;

    &__wrap {
      flex-direction: column;
    }

    &__block {
      &--aside {
        display: none;
      }
      &--main {
        flex: 1 0 100%;
        margin-left: 0;
      }
    }

    &__mob-title {
      display: block;
    }

    &__info {
      margin-bottom: 60px;
    }

    &__slider {
      width: 100%
    }

    &__foto {
      height: 320px;
    }

    &__vacancies {
      margin-bottom: 100px;
    }

    .swiper {
      &-button-next, &-button-prev {
        display: none;
      }
    }
  }

  @include phones {
    &__title {
      margin-bottom: 20px;
      font-size: 22px;
      text-align: left;
    }

    &__foto {
      img {
        object-fit: cover;
        height: 100%;
      }
    }

    &__name {
      font-size: 22px;
    }
  }
}

.hh {
  margin-bottom: 20px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: $shadow-large;

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px;
    background-color: $white;
  }

  &__logo {
    flex: 1 0 auto;
    width: 90px;
    height: 44px;

    svg {
      width: 80px;
      height: 44px;
    }
  }

  &__info {
    flex: 1 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__vac {
    font-size: 14px;
    color: $grey-2;
  }

  &__link {
    padding: 10px 20px;
    font-size: 14px;
    line-height: 1.2;
    border-radius: 8px;
    box-shadow: $shadow-small;

    svg {
      width: 12px;
      height: 12px;
      margin-left: 5px;
    }
  }

  @include desktop-1500 {
    &__info {
      flex-direction: column;
      align-items: flex-start;
    }
    &__vac {
      margin-bottom: 10px;
    }
  }

  @include laptop {
    &__footer {
      padding: 25px 15px;
    }
  }

  @include tablets {
    margin-bottom: 60px;

    &__footer {
      padding: 25px;
    }
    &__info {
      flex-direction: row;
      align-items: center;
    }
    &__vac {
      margin-bottom: 0;
    }
  }

  @include phones {

    &__footer {
      padding: 25px 15px;
    }
    &__info {
      flex-direction: column;
      align-items: flex-start;
    }
    &__vac {
      margin-bottom: 10px;
    }
  }
}

.vacancies {

  &__block {
    position: relative;
    margin-bottom: 5px;
    background-color: $white;
    border-radius: 10px;
    box-shadow: $shadow-large;
  }

  &__head {
    position: relative;
    padding: 25px;
    padding-bottom: 20px;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
    }

    &:hover {
      .vacancies__btn {
        opacity: 0.8;
  
        .vacancies__btn-arrow {
          border-color: rgba($accent, 0.8) transparent transparent transparent;
        }
      }
    }
    &:active, &:focus {
      .vacancies__btn {
        opacity: 0.8;
      }
    }
  }

  &__category {
    padding-right: 130px;
    font-size: 18px;
  }

  &__btn {
    position: absolute;
    top: 20px;
    right: 25px;
    display: inline-block;
    padding: 5px 10px;
    padding-right: 20px;
    font-size: 18px;
    line-height: 1.4;
    color: $accent;
    border-radius: 2px;
    transition: all $fastdown;
    
    &-arrow {
      position: absolute;
      top: 50%;
      right: 3px;
      border-style: solid;
      border-width: 10px 6px 0 6px;
      border-color: $accent transparent transparent transparent;
      transform: translateY(-50%);
      transition: border-color $fastdown;
    }

    &.open {
      .vacancies__btn-arrow {
        transform: translateY(-50%) rotate(180deg);
      } 
    }
  }

  &__list {
    margin: 0;
    padding: 25px;
    padding-top: 0;
    list-style: none;

    &.hide {
      display: none;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__link {
    font-size: 18px;
    color: $accent;
    transition: opacity $middledown;

    &:hover {
      opacity: 0.8;
    }
  }

  @include phones {

    &__category {
      margin-bottom: 5px;
      padding-right: 0;
      font-size: 14px;
    }

    &__btn {
      position: relative;
      right: 0;
      top: 0;
      padding: 0;
      padding-right: 20px;
      font-size: 14px;
    }

    &__item {
      &:not(:last-child) {
        margin-bottom: 12px;
      }
    }
    &__link {
      font-size: 14px;
    }
  }
}

.cta {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 800px;
  margin-bottom: 10px;
  background-image: url(../img/career/bg.jpg);
  background-size: cover;
  background-position: center top;
  background-repeat: no-repeat;


  &__wrap {
    max-width: 500px;
  }

  &__text {
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 34px;
    text-align: center;
  }

  &__link {
    width: 270px;
    margin: 0 auto;
    padding: 16px 30px;
    font-size: 14px;
    text-align: center;
    box-shadow: $shadow-small;
  }

  @include desktop {
    min-height: 600px;
    background-size: cover;

    &__text {
      font-size: 30px;
    }
  }

  @include tablets {
    min-height: 540px;
    margin-bottom: 70px;
    background-image: url(../img/career/bg-mob.jpg);
    background-size: 640px;
    background-size: contain;
    background-position: right center;
    background-repeat: no-repeat;

    &__text {
      margin-bottom: 30px;
      font-size: 28px;
    }
  }

  @include phones {

    &__text {
      margin-bottom: 20px;
      font-size: 22px;
      text-align: left;
    }
    &__link {
      width: 160px;
      margin-left: 0;
      padding: 16px 20px;
    }
  }
}