.about {

  &__lst {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include lst;
    margin-top: 60px;
    margin-bottom: 25px;
    counter-reset: list-counter;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 31.5%;
    margin-bottom: 35px;
    padding: 25px;
    background-color: $white;
    border-radius: 10px;
    box-shadow: $shadow-large;
    transition: transform $middledown;

    &:hover {
      transform: scale(1.1);

      .about {
        &__icon {
          &::before {
            transform: scale(1.4);
          }
        }
      }
    }
  }

  &__icon {
    position: relative;
    width: 100px;
    height: 100px;
    margin-bottom: 20px;

    &::before {
      content: counter(list-counter);
      counter-increment: list-counter;
      position: absolute;
      bottom: 0;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 30px;
      height: 30px;
      font-weight: 700;
      color: $accent;
      background-color: $grey;
      border-radius: 50%;
      transition: transform $middledown;
    }
  }
  
  &__image {
    display: block;
    width: 100px;
    height: 100px;
    overflow: hidden;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 50%;
  }

  &__text {
    margin-bottom: 20px;
    font-weight: 400;
    color: $grey-2;
  }

  &__btn-link {
    position: relative;
    margin-top: auto;
    font-weight: 500;
    color: $accent;

    .arrow, &::after, &::before {
      position: absolute;
      top: 50%;
      right: -20px;
      border-top: 5px solid transparent;
      border-left: 10px solid $accent;
      border-bottom: 5px solid transparent;
      transform: translateY(-50%);
      animation-name: arrow-opacity;
      animation-duration: 1.5s;
      animation-fill-mode: forwards;
      animation-iteration-count: infinite;
      animation-play-state: paused;
      animation-timing-function: linear;
      opacity: 0.5;
    }

    .arrow {
      right: -20px;
      animation-delay: 0s;
    }

    &::after, &::before {
      content: '';
      display: none;
    }

    &::after {
      right: -35px;
      animation-delay: 0.5s;
    }

    &::before {
      right: -50px;
      animation-delay: 1s;
    }


    &:hover {
      .arrow, &::after, &::before {
        animation-play-state: running;
      }
      
      &::after, &::before {
        display: block;
      }
    }
  }

  &__btn {
    margin: 0 auto;
  }

  @include desktop-1500 {
    &__btn-link {
      font-size: 14px;
    }
  }

  @include desktop {
    &__lst {
      justify-content: center;
    }

    &__item {
      width: 48%;

      &:nth-child(even) {
        margin-left: 4%;
      }
    }
  }

  @include tablets {
    &__lst {
      margin-top: 40px;
    }

    &__item {
      width: 100%;
      margin-bottom: 20px;

      &:nth-child(even) {
        margin-left: 0;
      }
    }
  }

  @include phones {
    &__btn-link {
      font-size: 12px;
    }
  }
}