.way {
  margin-bottom: 120px;

  &__title {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 26px;
    color: $default;
  }

  &__text {
    margin-bottom: 35px;
    font-size: 16px;
    color: $default;

    &--description {
      max-width: 350px;
    }

    &--modal {
      font-size: 12px;
      line-height: 1.4;
      color: $grey-2;
    }
  }

  &__subtitle {
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 16px;
    color: $grey-2;

    &--modal {
      margin-bottom: 0;
      font-weight: 700;
      color: $default;
    }
  }

  &__wrap {
    margin-top: 10px;

    &--desktop {
      display: block;
    }

    &--mobile {
      display: none;
    }
  }

  &__line-tabs {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 55px;
  }

  &__tab {
    position: relative;
    padding-left: 26px;
    font-weight: 600;
    font-size: 18px;
    color: $grey-2;
    text-align: left;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    &:not(:last-child) {
      margin-right: 35px;
    }

    &.active {
      color: $accent;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        bottom: -20px;
        width: 85px;
        height: 5px;
        background-color: $accent;
      }
    }
  }

  &__btn {
    margin-bottom: 2px;
    font-weight: 600;
    font-size: 20px;
    color: $grey-2;
    text-align: left;

    &.active {
      color: $accent;
    }
  }

  &__btn-metro {
    position: relative;
    width: 100%;
    padding: 10px;
    padding-left: 35px;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.45;
    color: $grey-2;
    text-align: left;
    border-top: 1px solid $grey;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    &.active {
      .way__icon--arrow {
        transform: translateY(-50%) rotate(180deg);
      }

      & + .way__content--mobile {
        display: block;
      }
    }
  }

  &__btn-item {
    position: relative;
    width: 100%;
    padding: 10px;
    font-size: 14px;
    line-height: 1.45;
    color: $accent;
    text-align: left;
    background-color: $grey;
    box-shadow: 0px -1px 0px #EAECEF, 0px 1px 0px #EAECEF;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    
    &.active {
      .way__icon--arrow {
        transform: translateY(-50%) rotate(180deg);
      }

      & + .way__item--modal  {
        display: block;
      }
    }
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    transform: translateY(-50%);

    &--spasskaya {
      background-color: #EC8A33;
    }
    &--sennaya { 
      background-color: #1389D1;
    }
    &--sadovaya {
      background-color: #984AA9;
    }

    &--modal {
      left: 10px;
    }

    &--arrow {
      position: absolute;
      top: 50%;
      left: auto;
      right: 10px;
      width: 10px;
      height: 10px;
      border-style: solid;
      border-width: 10px 6px 0 6px;
      border-color: $grey-2 transparent transparent transparent;
      border-radius: 0;
      transform: translateY(-50%);
      transition: $slowdown;
    }

    &--grey {
      border-color: #BABFC7 transparent transparent transparent;
    }

    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 12px;
      height: 12px;
      fill: $white;
      transform: translate(-50%, -50%);
    }
  }

  &__block {
    display: none;
    justify-content: space-between;

    &.active {
      display: flex;
    }
  }

  &__instructions {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 40%;
  }

  &__description {
    overflow: hidden;
    max-width: 350px;
    margin-bottom: 35px;
    font-size: 16px;
    color: $grey-2;
    transition: all 0.3s linear;

    &:not(:first-of-type) {
      max-height: 0;
      transition: all 0.3s linear;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 60%;

    &--mobile {
      display: none;
      overflow: hidden;
      transition: all 0.3s linear;
    }
  }

  &__item {
    display: none;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border-radius: 4px;

    &.active {
      display: flex;
    }

    span {
      font-weight: 600;
      font-size: 40px;
      color: $grey-2;
    }

    &--modal {
      display: none;
      padding: 15px 10px;
      text-align: left;
      background: none;
    }
  }

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    overflow: hidden;
    border-radius: 4px;
  }

  &__slide {
    padding: 10px;
  }

  .swiper-slide {
    display: flex;
    flex-direction: column;
    height: auto;
  }
    

  @include laptop {
    &__tab {
      font-size: 16px;
      &:not(:last-child) {
        margin-right: 30px;
      }
    }

    &__btn {
      font-size: 18px;
    }

    &__description {
      margin-bottom: 25px;
      font-size: 14px;
    }

    &__instructions {
      width: 45%;
      padding-right: 10px;
    }

    &__content {
      width: 55%;
    }
  }

  
  @include tablets {
    margin-bottom: 40px;

    .container {
      padding: 0;
    }

    &__wrapper {
      padding: 0 10px;
    }

    &__title {
      margin-bottom: 10px;
      font-size: 22px;
    }

    &__text {
      margin-bottom: 15px;
      font-size: 14px;

      &--modal {
        margin-bottom: 10px;
      }
    }

    &__wrap {
      &--desktop {
        display: none;
      }

      &--mobile {
        display: block;
      }
    }

    &__tab {
  
      &.active {
        &::before {
          bottom: -10px;
          width: 70px;
          height: 4px;
        }
      }
    }

    &__block {
      flex-direction: column;
    }

    &__content {
      width: 100%;
    }

    &__item {
      &--modal {
        min-height: 0;
      }
    }

    &__container {
      &--foto {
        margin-top: auto;
      }
    }
  }

  @include phones {
    &__title {
      font-size: 24px;
    }

    &__tab {
      font-size: 12px;

      &:not(:last-child) {
        margin-right: 20px;
      }

      &.active {
        &::before {
          bottom: -15px;
        }
      }
    }

    &__btn {
      font-size: 16px;
    }

  }

  @include small {
    &__tab {
      font-size: 10px;
  
      &:not(:last-child) {
        margin-right: 20px;
      }
  
      &.active {
        &::before {
          bottom: -10px;
          width: 50px;
          height: 3px;
        }
      }
    }

    &__container {
      min-height: 170px;
    }
  }
}

