.results {
  &--in-dela {
    margin-top: 60px;
    margin-bottom: 100px;
  }

  &__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 30px;
  }

  &__block {
    &--text {
      width: calc(50% - 20px);
      margin-bottom: 40px;
      font-weight: 400;
      color: $grey-2;
    }

    &--image {
      width: 100%;
    }

    &--inner {
      position: relative;
      width: 100%;
      margin-top: 80px;
    }

    p {
      margin-bottom: 0;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: 26px;
    line-height: 1.45;
  }

  &__data {
    position: absolute;
    top: 30px;
    left: 0;

    &--in-dela {
      top: -40px;
    }
  }

  &__lst {
    display: flex;
    align-items: flex-start;
    @include lst;

    &--in-dela {
      flex-direction: column;
    }
  }

  &__lst-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 280px;
    margin-right: 35px;
    margin-bottom: 30px;
  }

  &__number {
    position: relative;
    font-weight: 700;
    font-size: 45px;
    line-height: 1.1;
    color: $blue;
    white-space: nowrap;

    &--symbol {
      // padding-left: 40px;
      // &::before {
      //   content: '>';
      //   position: absolute;
      //   top: 50%;
      //   left: 0;
      //   width: 35px;
      //   height: 35px;
      //   font-weight: 700;
      //   font-size: 45px;
      //   line-height: 30px;
      //   color: $blue;
      //   transform: translateY(-50%);
      // }
    }
  }

  &__label {
    font-weight: 400;
    font-size: 18px;
    color: $grey-2;
  }

  &__note {
    font-size: 14px;
    color: #b5bdd1;
  }

  &__chart {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    @include lst;
    min-height: 490px;

    &-item {
      width: 12%;
    }

    &-line {
      position: relative;
      height: 6px;
      background: linear-gradient(360deg, #8eafff 0%, #5b89fc 100%);
      border-radius: 10px;

      &--1 {
        height: 6px;
      }
      &--2 {
        height: 19px;
      }
      &--3 {
        height: 48px;
      }
      &--4 {
        height: 151px;
      }
      &--5 {
        height: 253px;
      }
      &--6 {
        height: 365px;
      }
      &--last {
        height: 490px;
        background: linear-gradient(360deg, #5b86f0 0%, #164bd0 100%);
      }
    }
  }

  &__digit {
    position: absolute;
    bottom: calc(100% + 25px);
    left: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 700;
    font-size: 30px;
    color: $blue;
    text-align: center;
    white-space: nowrap;
    transform: translateX(-50%);

    &--last {
      color: #174bd0;
    }

    &--middle {
      font-size: 25px;
    }

    &--small {
      font-size: 20px;
    }

    &--current {
      bottom: calc(100% + 15px);
      font-size: 500px;
      font-size: 14px;
    }
  }

  &__year {
    position: absolute;
    top: calc(100% + 16px);
    left: 50%;
    font-size: 16px;
    color: $grey-2;
    transform: translateX(-50%);
  }

  @include desktop {
    &__lst-item {
      width: 160px;
      margin-right: 60px;
    }

    &__number {
      font-size: 40px;
    }
  }

  @include laptop {
    &--in-dela {
      .results {
        &__number {
          font-size: 35px;

          &--symbol {
            // padding-left: 30px;
            // &::before {
            //   width: 35px;
            //   height: 35px;
            //   font-size: 35px;
            // }
          }
        }

        &__label {
          font-size: 16px;
        }

        &__note {
          font-size: 12px;
        }

        &__digit {
          font-size: 25px;

          &--middle {
            font-size: 20px;
          }

          &--current {
            font-size: 12px;
            // white-space: normal;
          }
        }

        &__chart {
          &-item {
            width: 10%;
          }
        }
      }
    }

    &__data {
      top: 0;

      &--in-dela {
        top: -40px;
      }
    }

    &__lst {
      flex-direction: column;
    }

    &__lst-item {
      margin-bottom: 20px;
    }
  }

  @include tablets {
    &__wrap {
      flex-direction: column;
    }

    &__block {
      margin-bottom: 20px;
      &--text {
        width: 100%;
      }
    }

    &__title {
      margin-bottom: 100px;
      font-size: 24px;
    }

    &__data {
      top: -40px;
      width: 200px;
    }

    &__number {
      font-size: 35px;

      &--symbol {
        // padding-left: 30px;
        // &::before {
        //   width: 35px;
        //   height: 35px;
        //   font-size: 35px;
        // }
      }
    }

    &__chart-line {
      border-radius: 5px;
    }

    &__digit {
      font-size: 25px;

      &--middle {
        font-size: 20px;
      }

      &--current {
        font-size: 12px;
      }
    }

    &__year {
      font-size: 14px;
    }
  }

  @include phones {
    &--in-dela {
      .results {
        &__digit {
          font-size: 18px;

          &--middle {
            font-size: 16px;
          }

          &--small {
            font-size: 16px;
          }

          &--current {
            font-size: 12px;
            white-space: normal;
          }
        }
      }
    }

    &__wrap {
      margin-top: 20px;
    }

    &__data {
      top: -70px;
    }

    &__digit {
      bottom: calc(100% + 10px);
      font-size: 18px;

      &--last {
        bottom: calc(100% + 40px);
      }
      &--small {
        font-size: 16px;
      }
      &--current {
        font-size: 12px;
        white-space: normal;
      }
    }

    &__chart {
      &-item {
        width: 8%;
      }
    }

    &__year {
      top: calc(100% + 10px);
      font-size: 12px;
    }
  }
}
