.quote {
  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 55px;
  }

  &__block {

    &--image {
      flex: 0 1 490px;
    }

    &--content {
      flex: 1 0 700px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-left: 35px;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__title {
    position: relative;
    margin-bottom: 25px;
    padding-top: 40px;
    font-weight: 700;
    font-size: 22px;

    svg {
      position: absolute;
      top: 0;
      left: 0;
      width: 30px;
      height: 30px;
    }
  }

  &__author {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 40px;

    &-foto {
      position: relative;
      flex: 1 0 auto;
      width: 60px;
      height: 70px;
      margin-right: 20px;
  
      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 1;
        width: 60px;
        height: 60px;
        background-color: $white;
        border-radius: 50%;
        transform: translateX(-50%);
      }
    }

    &-name {
      display: flex;
      flex-direction: column;
    }
  }

  &__foto {

    &-img {
      position: absolute;
      bottom: 0;
      left: 50%;
      z-index: 2;
      width: 100%;
      height: 100%;
      background-image: url(../img/quote/foto.png);
      background-size: contain;
      background-position: center bottom;
      background-repeat: no-repeat;
      transform: translateX(-50%);
    }
  }

  &__name {
    margin-bottom: 10px;
    font-size: 18px;
  }

  &__post {
    color: $grey-2;
  }

  &__btn {
    font-size: 14px;
  }

  @include desktop-1500 {
    &__block {
      &--content {
        flex: 1 0 565px;
      }
    }
  }

  @include desktop {
    &__block {
      &--image {
        flex: 0 1 390px;
      }
  
      &--content {
        flex: 1 0 520px;
        margin-left: 20px;
      }
    }
  }

  @include laptop {
    &__block {

      &--image {
        flex: 0 1 250px;
      }
  
      &--content {
        flex: 1 0 420px;
      }
    }
  }
  
  @include tablets {
    &__wrap {
      flex-direction: column;
    }

    &__block {

      &--image {
        flex: 0 1 200px;
        margin-bottom: 40px;
      }
  
      &--content {
        flex: 1 0 100%;
        margin-left: 0;
      }
    }

    &__image {
      height: 200px;
    }

    &__title {
      font-size: 20px;
    }
  }

  @include phones {
    &__title {
      font-size: 18px;
    }
  }
}