.mass-media {
  position: relative;
  margin-bottom: 20px;
  padding: 35px;
  padding-top: 25px;
  overflow: hidden;
  background-color: $white;
  border-radius: 10px;
  box-shadow: $shadow-large;

  &__title {
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 22px;
  }
  
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    @include lst;
    margin-bottom: 30px;
  }

  &__item {
    display: flex;
    margin-bottom: 15px;
  }
  
  &__link {
    position: relative;
    width: 120px;
    height: 50px;

    &--kp {
      width: 140px;
      height: 24px;
    }
    &--kom {
      width: 130px;
      height: 18px;
    }
    &--perv {
      width: 35px;
      height: 45px;
    }
    &--vedom {
      width: 120px;
      height: 24px;
    }
    &--inter {
      width: 110px;
      height: 35px;
    }
    &--poss {
      width: 100px;
      height: 23px;
    }

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__link-all {
    width: 210px;
    padding: 8px 18px;
    font-size: 14px;
    border-radius: 10px;
  }

  @include laptop {
    padding: 25px;
  }

  @include tablets {
    padding: 40px;

    &__item {
      width: 50%;
    }
  }

  @include phones {
    padding: 25px;

    &__title {
      margin-bottom: 30px;
    }

    &__list {
      justify-content: flex-start;
    }

    &__link {
      transform: scale(0.9);
    }

    &__link-all {
      margin: 0 auto;
      padding: 15px 18px;
    }
  }
}