.top {
  position: relative;
  background: linear-gradient(135deg, #EDF0F8 15%, #D7DEEF 85%);
  background-image: url(../img/services/bg-top.jpg);
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;

  // &::before {
  //   content: '';
  //   position: absolute;
  //   top: 0;
  //   left: 0;
  //   z-index: 1;
  //   width: 100%;
  //   height: 100%;
  //   background: linear-gradient(135deg, #EDF0F8 15%, #D7DEEF 85%);
  //   opacity: 0.9;
  // }

  &__image {
    position: absolute;
    bottom: 0;
    width: 1080px;
  }

  &__figure {
    position: absolute;
    bottom: 0;
    right: -400px;
    width: 1080px;
    margin: 0;
  }

  &__figcaption {
    position: absolute;
    bottom: 140px;
    left: 50%;
    display: flex;
    flex-direction: column;
    width: 250px;
    transform: translateX(-40%);
  }

  &__label {
    
    &--name {
      margin-bottom: 10px;
      font-size: 18px;
      color: $white;
    }

    &--post {
      color: #D7DEEF;
    }
  }

  &__wrap {
    position: relative;
    z-index: 2;
    display: flex; 
    justify-content: space-between;
    align-items: center;
  }

  &__block {

    &--main {
      position: relative;
      z-index: 1;
      width: 880px;
      padding-top: 80px;
    }

    &--form {
      width: 385px;
    }
  }

  &__inner {
    width: 700px;
    min-height: 660px;
  }

  &__note {
    display: inline-block;
    margin-bottom: 30px;
    padding: 5px 10px;
    font-size: 14px;
    color: $white;
    background-color: $pink;
  }

  &__title {
    margin-bottom: 15px;
    font-size: 40px;
    line-height: 1.2;

    &--accent {
      color: $accent;
    } 
  }

  &__text {
    margin-bottom: 40px;
    font-size: 18px;
    color: $grey-2;
  }

  &__lst {
    display: flex;
    justify-content: flex-start;
    @include lst;

    &-item {
      position: relative;
      padding-left: 50px;
    }

    &-icon {
      position: absolute;
      top: 0;
      left: 0;
      width: 35px;
      height: 35px;
      background-size: contain;
      background-position: left top;
      background-repeat: no-repeat;

      &--first {
        background-image: url(../img/services/delo.svg);
      }
      &--second {
        background-image: url(../img/services/oplata.svg);
      }
    }
  }

  &__form {
    margin-bottom: 10px;
    padding: 25px;
    background-color: $white;
    border-radius: 15px;
  }

  &__form-title {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 22px;
    color: $default;
  }

  &__timer {
    margin-bottom: 35px;
  }

  @include desktop-1500 {
    &__block {
      &--main {
        width: 800px;
      }
    }

    &__figure {
      right: -420px;
    }
  }
  @include desktop {

    &__block {
      &--main {
        width: 700px;
      }
  
      &--form {
        width: 360px;
      }
    }

    &__inner {
      width: 550px;
      min-height: 780px;
    }

    &__title {
      font-size: 38px;
    }

    &__text {
      font-size: 20px;
    }

    &__figure {
      right: -340px;
      width: 880px;
    }
  }

  @include laptop {
    background-position: center top;
    background-size: cover;

    &__block {
      &--main {
        width: 100%;
        padding-top: 60px;
      }
  
      &--form {
        width: 280px;
      }
    }

    &__note {
      margin-bottom: 20px;
    }

    &__inner {
      width: 100%;
      min-height: 700px;
    }

    &__title {
      margin-bottom: 20px;
      font-size: 34px;
    }
    
    &__text {
      margin-bottom: 20px;
      font-size: 18px;
    }

    &__figure {
      display: none;
    }
  }

  @include tablets {
    
    &__block {
      &--main {
        width: 100%;
        padding-bottom: 0;
      }
  
      &--form {
        display: none;
      }
    }

    &__wrap {
      padding-bottom: 40px;
    }

    &__inner {
      min-height: auto;
    }

    &__title {
      font-size: 32px;
    }

    &__timer {
      margin-bottom: 20px;
    }
  }

  @include phones {
    &__block {
      &--main {
        padding-top: 50px;
      }
    }

    &__wrap {
      min-height: 400px;
      padding-bottom: 20px;
    }

    &__title {
      font-size: 26px;
      line-height: 1.4;
    }

    &__text {
      font-weight: 500;
      font-size: 14px;
    }

    &__lst {
      &-item {
        width: 100%;
      }
    }
  }
}