.spoiler {

  &--promo {
    @include lst;
    margin-top: 30px;
    counter-reset: free-counter;
  }

  &__wrap {
    @include lst;
    margin-top: 40px;
    margin-bottom: 80px;
    list-style-type: none;
    counter-reset: free-counter;

    &--nonumb {
      margin-top: 25px;
      margin-bottom: 25px;
    }
  }

  &__block {
    position: relative;
    margin-bottom: 10px;
    background-color: $white;
    border-radius: 10px;
    box-shadow: $shadow-nano;

    &:hover {
      .spoiler__category {
        color: rgba($accent, 0.8);
      } 
  
      .spoiler__btn {
        opacity: 0.8;
      }
  
      .spoiler__btn-arrow {
        border-color: rgba($accent, 0.8) transparent transparent transparent;
      }
    }

    &::before {
      position: absolute;
      left: 25px;
      top: 23px;
      content: counter(free-counter);
      counter-increment: free-counter;
      font-size: 18px;
      color: $grey-2;
    }

    &--promo {

      &::before {
        top: 18px;
        left: 18px;
        width: 32px;
        height: 32px;
        font-weight: 700;
        font-size: 14px;
        line-height: 32px;
        color: $accent;
        text-align: center;
        background-color: $grey;
        border-radius: 50%;
      }

      &:hover {
        .spoiler__category {
          color: rgba($accent, 0.8);
        }
        .spoiler__btn-arrow {
          border-color: $grey-2 transparent transparent transparent;
        } 
      }
    }

    &--nonumb {
      box-shadow: 0px 10px 34px rgba(0, 29, 104, 0.12);

      &::before {
        display: none;
        
      }
    }
  }

  &__head {
    position: relative;
    padding: 25px;
    line-height: 1.3;
    cursor: pointer;

    &--promo {
      padding: 22px;

      &:hover {
  
        .spoiler__btn {
          opacity: 1;
        }

        .spoiler__btn-arrow {
          border-color: $grey-2 transparent transparent transparent;
        }
      }
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
    }

    &:active, &:focus {
      .spoiler__btn {
        opacity: 0.8;
      }
    }
  }

  &__category {
    position: relative;
    padding-left: 35px;
    padding-right: 130px;
    font-size: 18px;
    transition: color $fastdown;

    &--promo {
      padding-right: 60px;
      font-weight: 600;
      color: $grey-2;
    }

    &--nonumb {
      padding-left: 0;
      padding-right: 50px;
      font-weight: 600;
      color: $grey-2;
    }
  }

  &__btn {
    position: absolute;
    top: 25px;
    right: 25px;
    display: inline-block;
    height: 25px;
    padding: 5px 10px;
    padding-right: 20px;
    font-size: 18px;
    line-height: 1.4;
    color: $accent;
    border-radius: 2px;
    transition: all $fastdown;
    
    &-arrow {
      position: absolute;
      top: 50%;
      right: 3px;
      border-style: solid;
      border-width: 10px 6px 0 6px;
      border-color: $grey-2 transparent transparent transparent;
      transform: translateY(-50%);
      transition: border-color $fastdown;
    }

    &--promo {
      top: 10px;
      right: 15px;
      
      .spoiler__btn-arrow {
        border-width: 9px 5px 0 5px;
        border-color: #B5BDD1 transparent transparent transparent;
      }
    }

    &.open {
      .spoiler__btn-arrow {
        transform: translateY(-50%) rotate(180deg);
      } 
    }
  }

  &__content {
    margin: 0;
    margin-top: -5px;
    padding: 25px;
    padding-top: 0;
    color: $grey-2;
    transition: max-height $slowdown;

    &--video {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
    }

    p {
      margin-bottom: 0;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    ul {
      @include lst;

      li {
        position: relative;
        padding-left: 15px;

        &::before {
          content: '';
          position: absolute;
          top: 9px;
          left: 0;
          width: 6px;
          height: 6px;
          background-color: $accent;
          border-radius: 50%;
        }
      }
    }

    &.hide {
      display: none;
    }

    &--promo {
      max-height: 185px;
      margin-top: 0;
      overflow: hidden;
      padding: 22px;
      padding-top: 0;
      font-weight: 400;
      color: #393939;
      opacity: 1;
      transition: max-height 0.3s linear, padding-bottom 0.3s linear, opacity 0.2s ease-in;

      &.hide {
        display: block;
        max-height: 0;
        opacity: 0;
        padding-bottom: 0;
      }
    }

    &--nonumb {
      font-weight: 400;
      color: #393939;
    }
  }

  &__text {
    margin-bottom: 10px;
  }

  &__link {
    display: block;
    font-weight: 400;
    color: $accent;
  }

  &__unit {
    &--video {
      flex: 1 0 40%;
      margin-right: 25px;
    }
  }

  @include laptop {
    &__content { 
      &--video {
        flex-direction: column;
      }
    }

    &__unit {
      &--video {
        flex: 1 0 100%;
        margin-right: 0;
        margin-bottom: 15px;
      }
    }
  }

  @include tablets {
    &__block {
      &::before {
        top: 19px;
        left: 20px;
        font-size: 14px;
        color: $default;
      }

      &--promo {
        &::before {
          top: 13px;
          width: auto;
          height: auto;
          color: $grey-2;
          background: none;
          content: counter(free-counter)'.';
        }
      }
    }

    &__head {
      padding: 20px;
    }

    &__category {
      padding-left: 15px;
      padding-right: 20px;
      font-size: 14px;

      &--promo {
        font-weight: 700;
        padding-left: 18px;
      }

      &--nonumb {
        padding-left: 0;
      }
    }

    &__btn {
      top: 20px;
      right: 20px;

      &--promo {
        top: 10px;
        right: 15px;
      }
    }

    &__content {
      &--promo {
        max-height: 240px;
        font-size: 14px;
      }

      &--nonumb {
        padding: 20px;
        padding-top: 0;
      }
      
      p {
        font-size: 14px;
      }

      span {
        font-size: 14px;
      }
      
      ul {
        font-size: 14px;
      }
    }
  }

  @include phones {
    &__content {
      &--promo {
        max-height: 310px;
      }
    }
  }
}

.speaker {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 70px;
  padding-left: 90px;
  line-height: 1.6;
  color: $grey-2;

  &__foto {
    position: absolute;
    top: 0;
    left: 0;
    width: 70px;
    height: 70px;
    overflow: hidden;
    background: $gr-grey;
    border-radius: 10px;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 140%;
      height: 140%;
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      transform: translate(-50%, -30%);
    }

    &--bodnar {
      &::before {
        background-image: url(../img/services/humans/bodnar-e-i_small.png);
      }
    }

    &--okladnikov {
      &::before {
        background-image: url(../img/services/humans/ilya-viktorovich_small.png);
      }
    }

    &--kopyrina {
      &::before {
        background-image: url(../img/services/humans/anzhelika-vladimirovna_small.png);
      }
    }

    &--efremov {
      &::before {
        background-image: url(../img/services/humans/aleksandr-igorevich_small.png);
      }
    }
  }

  &__author {
    margin-bottom: 2px;
    font-size: 12px;
  }
  &__name {
    margin-bottom: 2px;
    font-weight: 700;
    font-size: 14px;
  }
  &__post {
    font-size: 14px;
  }

  @include phones {
    padding-left: 80px;

    &__name {
      line-height: 1.4;
    }

    &__post {
      font-size: 12px;
      line-height: 1.4;
    }
  }
}