::placeholder {
  color: #B5BDD1;
}

::selection {
  background-color: $accent;
  color: #fff;
}

*,
::before,
::after {
  box-sizing: inherit;
}

html {
  overflow-x: hidden;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  width: 100%;
  height: 100%;

  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.4;
  font-family: $font-default;
  color: $default;
}

a {
  color: $accent;
  text-decoration: none;
  transition: color $fastdown;

  &:hover {
    color: rgba($accent, 0.8);
  }

  &:active {
    color: rgba($accent, 0.8);
  }
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
textarea {
  border-radius: 0;

  &::placeholder {
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

select {
  border-radius: 0;
}

// ie11 X removal
input {
  &::-ms-clear,
  &::-ms-reveal {
    display: none;
  }
}

// chrome search X removal
input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {
  appearance: none;
}

// input[number] arrows removal
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

input[type='number'] {
  appearance: textfield;
}

// ios button \ inputs reset
select,
textarea,
input:matches([type='email'], [type='number'], [type='password'], [type='search'], [type='tel'], [type='text'], [type='url']) {
  appearance: none;
}

button,
[type='button'],
[type='reset'],
[type='submit'] {
  appearance: none;
}
