.form {
  display: flex;
  flex-direction: column;

  &--width-100 {
    width: 100%;
  }

  &__info {
    position: relative;
    margin-bottom: 20px;
    padding: 12px;
    padding-left: 50px;
    font-weight: 600;
    font-size: 16px;
    background-color: #E8F4FF;
    border-radius: 10px;

    &::before {
      content: '';
      position: absolute;
      top: 10px;
      left: 12px;
      width: 26px;
      height: 26px;
      background-image: url(../img/icons/info.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  &__row {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 10px;

    &--test {
      margin-bottom: 25px;
    }
  }

  &__label {
    margin-bottom: 5px;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.6;
    color: $grey-2;

    &--light {
      font-weight: 400;
    }

    &--test {
      margin-bottom: 10px;
      font-weight: 500;
      font-size: 18px;
      color: $default;
    }
  }

  &__field {
    width: 100%;
    padding: 10px 12px;
    font-size: 14px;
    line-height: 1.5;
    color: $default;
    background-color: $grey;
    border: none;
    border-radius: 10px;

    &--white {
      background-color: $white;
    }

    &--callback {
      padding: 13px 20px;
      font-size: 16px;
      text-align: center;
    }

  }

  &__note {
    margin-top: 5px;
    line-height: 1.2;

    &-recover {
      font-weight: 400;
      font-size: 14px;
      color: $accent;
    }
  }

  &__btn {
    font-size: 14px;
    line-height: 1.5;

    &--hurry {
      align-self: flex-start;
      width: auto !important;
      margin-bottom: 0;
      margin-bottom: -10px;
      padding: 8px 20px;
      border-radius: 7px;
    }

    &--test {
      padding: 25px;
      font-size: 20px;
    }

    &:hover {
      background: $gr-blue-hover;
    }
  }

  @include desktop {
    &__info {
      font-size: 16px;
    }
  }


  @include tablets {
    &__field {
      &--white {
        background-color: $grey;
      }
    }
  }
}

.contact-form {
  position: relative;
  overflow: hidden;
  margin-top: 1px;
  background: $gr-grey;
  border-top: 1px solid $white;

  // &::before {
  //   content: '';
  //   position: absolute;
  //   left: 0;
  //   top: 0;
  //   width: 100%;
  //   height: 1px;
  //   background-color: #d6d6d6;
  // }
  // &::after {
  //   content: '';
  //   position: absolute;
  //   left: 0;
  //   bottom: 0;
  //   width: 100%;
  //   height: 1px;
  //   background-color: #d6d6d6;
  // }

  &__wrap {
    display: flex;
    justify-content: center;
  }

  &__block {
    position: relative;

    &--form {
      flex: 0 1 595px;
      padding: 100px 0;
    }

    &--image {
      display: flex;
      margin-left: 70px;
      padding-top: 40px;
    }
  }

  &__title {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 34px;
    line-height: 1.5;
    color: $default;
  }

  &__text {
    margin-bottom: 40px;
    font-size: 16px;
    color: $grey-2;
  }

  &__form {
    position: relative;
    z-index: 1;
  }

  &__image {
    margin-top: auto;
  }

  &__lable {
    position: absolute;
    right: -30px;
    bottom: 65px;
    display: flex;
    flex-direction: column;
    width: 270px;
    padding: 15px;
    background-color: $white;
    border-radius: 10px;
    box-shadow: $shadow-large;
  }

  &__name {
    font-weight: 700;
    margin-bottom: 10px;
  }

  &__post {
    font-size: 14px;
    color: $grey-2;
  }

  @include laptop {
    &__title {
      font-size: 30px;
    }
  }

  @include tablets {
    margin-bottom: 60px;
    background-color: $white;

    &--mt-phones {
      margin-top: -40px;
    }

    &::before {
      display: none;
    }

    &__wrap {
      flex-direction: column;
      width: 100%;

    }

    &__block {
      &--form {
        flex: 0 1 100%;
        padding: 40px 0;
      }

      &--image {
        justify-content: center;
        margin-left: 0;
        padding-top: 0;
      }

      &--no-pt {
        padding-top: 0;
      }
    }

    &__title {
      margin-bottom: 10px;
      font-size: 26px;
    }

    &__text {
      margin-bottom: 20px;
    }

    &__image {
      margin-bottom: -160px;
    }

    &__lable {
      right: 20px;
    }
  }

  @include phones {

    &--mt-phones {
      margin-top: -80px;
    }

    &__wrap {
      padding: 30px 0;
    }

    &__title {
      font-size: 22px;
    }

    &__image {
      width: 240px;
      margin-bottom: -100px;
    }

    &__lable {
      bottom: 0;
      right: 50%;
      transform: translateX(50%);
    }
  }
}

.form-aside {
  margin-bottom: 20px;
  padding: 35px;
  background-color: $white;
  border-radius: 10px;
  box-shadow: $shadow-large;

  &--single {
    padding: 25px;
  }

  &__title {
    margin-bottom: 15px;
    font-size: 22px;
    line-height: 1.45;

    &--big {
      font-size: 26px;
    }

    &--small {
      font-size: 18px;
    }
  }

  @include desktop {
    &__title {
      font-size: 20px;

      &--small {
        font-size: 18px;
      }
    }
  }

  @include laptop {
    padding: 25px;
  }
}

.form-online {
  width: 420px;
  padding: 40px;
  background-color: $white;
  border-radius: 10px;
  box-shadow: $shadow-large;

  &__title {
    margin-bottom: 20px;
    font-size: 26px;
    line-height: 1.45;
  }

  &__text {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 14px;
    color: $grey-2;
  }

  &__links {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__hot-line {
    font-weight: 400;
    font-size: 14px;
    color: $accent;
  }

  @include desktop {
    width: 380px;
  }

  @include desktop {
    width: 100%;
  }

  @include tablets {
    margin-bottom: 20px;
  }

  @include phones {
    margin: 0 auto;
    padding: 0;
    border-radius: 0;
    box-shadow: none;
  }
}

.form-filter {
  position: relative;
  z-index: 10;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  padding: 25px;
  background-color: $white;
  border-radius: 20px;
  box-shadow: $shadow-large;
  
  &::before, &::after {
    content: '';
    position: absolute;
    top: 20px;
    bottom: 20px;
    z-index: 20;
    width: 26px;
  }

  &::before {
    left: 0;
    background: linear-gradient(90deg,  $white 0, $white 30%, rgba($white, 0.3) 100%);
  }

  &::after {
    right: 0;
    background: linear-gradient(-90deg,  $white 0, $white 30%, rgba($white, 0.3) 100%);
  }

  &__wrap {
    display: flex;
    flex-wrap: wrap;
  }

  &__block {
    width: 100%;
    margin-bottom: 20px;

    &--double {
      width: 50%;
    }

    &--reset {
      margin-bottom: 0;
    }

    &--mobile {
      margin-bottom: 10px;
    }

    &--banks {
      position: relative;

      // &::before {
      //   content: '';
      //   position: absolute;
      //   left: 0;
      //   top: 0;
      //   z-index: -1;
      //   width: 100%;
      //   height: 100%;
      // }

      &.disabled {
        opacity: 0.6;

        &::before {
          z-index: 10;
        }
      }
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-left: -10px;
    padding-top: 5px;

    &--line {
      // height: 26px;
      overflow: hidden;
    }

    &--range {
      position: relative;
      height: 60px;
      margin-top: -5px;
      margin-bottom: 10px;
    }

    &.open {
      height: auto;
    }
  }

  &__subtitle {
    margin-bottom: 10px;
    font-weight: 700;
  }

  &__range {
    position: relative;
    left: 50%;
    z-index: 10;
    width: 96%;
    background: none;
    transform: translateX(-50%);
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
    outline-color: rgba($accent, 0.4);

    &::-webkit-slider-thumb {
      width: 20px;
      height: 20px;
      background-color: $white;
      border: 2px solid $range;
      border-radius: 50%;
      cursor: pointer;
      -webkit-appearance: none;
    }
  }

  &__btn-sort {
    display: none;
    min-width: 100%;
    max-width: 100%;
    margin: 0 auto;
    margin-top: 10px;
    font-size: 14px;
    line-height: 1.4;
  }

  &__btn-show {
    position: absolute;
    top: 0;
    right: 0;
    padding: 8px 15px;
    font-weight: 400;
    font-size: 14px;
    border-radius: 5px;
  }

  &__reset {
    display: flex;
    justify-content: center;
    align-self: center;
    width: 100%;
    margin-bottom: 0;
    margin-left: 0;
    padding: 10px;
    font-weight: 500;
    font-size: 14px;
    color: $accent;
    text-align: center;
    background: #EEF3FF;
    border-radius: 50px;
    box-shadow: none;

    svg {
      width: 10px;
      height: 10px;
      margin-right: 5px;
      fill: $accent;
      transition: transform $slowdown;
    }

    &:hover {
      color: $accent;
      background: darken(#EEF3FF, 2%);

      svg {
        transform: rotate(180deg);
      }
    }

    &.hide {
      display: none;
    }
  }

  .range-list {
    position: absolute;
    top: 80%;
    left: 50%;
    display: flex;
    justify-content: space-between;
    width: 104%;
    @include lst;
    transform: translateX(-50%);

    &--current {
      top: 0;
      width: 96%;
    }

    &--line {
      top: 50%;
      width: 92%;
    }

    &--lable {
      transform: translateX(-49%);
    }

    &__line {
      display: block;
      flex: 1 0 auto;
      height: 4px;
      margin: 0 2px;
      border-radius: 2px;
      background-color: $range;
    }

    &__item {
      font-weight: 400;
      font-size: 10px;
      color: $grey-2;
      white-space: nowrap;

      &--current {
        display: flex;
        justify-content: center;
        width: 20px;
        font-size: 12px;
        color: $accent;
        opacity: 0;

        &.show {
          opacity: 1;
        }
      }

      &--mr10 {
        margin-right: -10px;
      }
      &--mr30 {
        margin-right: -30px;
      }

      &:first-child {
        justify-content: flex-start;
      }
      &:last-child {
        justify-content: flex-end;
      }
    }

    @include desktop-1500 {
      &--lable {
        display: none;
      }

      &--current {
        width: 94%;
      }

      &--line {
        top: 50%;
        width: 92%;
      }
    }

    @include desktop {
      &--current {
        width: 100%;
      }

      &--line {
        width: 100%;
      }
    }
  }

  .radio {
    position: relative;
    display: flex;
    align-items: center;
    margin-left: 10px;
    margin-bottom: 10px;
    padding-left: 22px;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 5px;
    }
  
    &--btn {
      margin-left: 0;
      padding-left: 0;

      &:not(:last-child) {
        margin-right: 0;
      }
    }

    &__default {
      display: none;
  
      &:checked + .radio__new {
        &::before {
          display: block;
        }
      }

      &:checked ~ .radio__label {
        color: $accent;
      }
    }
  
    &__new {
      position: absolute;
      left: 0;
      top: 50%;
      width: 16px;
      height: 16px;
      border: 1px solid $accent;
      border-radius: 50%;
      box-shadow: $shadow-radio;
      transform: translateY(-50%);
  
      &::before {
        content: '';
        position: absolute;
        left: 50%;
        top: 50%;
        display: none;
        width: 10px;
        height: 10px;
        background: $gr-blue;
        border-radius: 50%;
        transform: translate(-50%, -50%);
      }
    }
  
    &__label {
      font-weight: 400;
      font-size: 12px;
      color: $grey-2;
      white-space: nowrap;
      cursor: pointer;
  
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
      }

      &--reset {
        position: relative;
        font-size: 14px;
        color: $accent;
        text-align: center;

        svg {
          position: absolute;
          left: -15px;
          top: 50%;
          width: 10px;
          height: 10px;
          fill: $accent;
          transform: translateY(-50%);
        }
      }
    }

    &.active {
      color: $accent;
      cursor: default;
    }

    &:hover {
      .radio__label {
        color: $accent;
      }
    }

    @include tablets {
      &--reset {
        border-radius: 10px;
      }
    }
  
    @include phones {
      &--reset {
        border-radius: 5px;
      }

      &__label {
        &--reset {
          font-size: 12px;
        }
      }

      &__new {
        &--accent {
          left: 12px;
        }
      }
    }
  }

  .btn-show {
    position: relative;
    display: none;
    margin-bottom: 10px;
    padding-right: 12px;
    outline-color: rgba($accent, 0.4);

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    &__text {
      font-size: 14px;
      color: $accent;
    }

    &__arrow {
      position: absolute;
      top: 6px;
      right: 0;
      border-style: solid;
      border-width: 7px 4px 0 4px;
      border-color: $accent transparent transparent transparent;
    
      &.open {
        transform: rotate(180deg);
      }
    }

    @include tablets {
      display: block;
    }
  }

  @include desktop-1500 {
    &__row {
      &--range {
        width: calc(100% + 10px);
        margin-bottom: 0;
      }
      &--reviews {
        flex-direction: column;
      }
    }
  }

  @include desktop {
    &__block {
      &--double {
        width: 100%;
      }
    }

    &__row {
      &--range {
        width: 100%;
        margin-left: 0;
      }
    }

    &__range {
      width: 100%;
    }
  }

  @include laptop {
    padding: 20px;

    &::before, &::after {
      width: 20px;
    }
  }

  @include tablets {
    border-radius: 15px;

    &__wrap {
      display: none;
      margin-top: 10px;

      &.open {
        display: block;
      }
    }

    &__block {
      &--double {
        width: 50%;
      }

      &--desktop {
        display: none;
      }

      &--mobile {
        position: relative;
        display: block;
        margin-bottom: 0;
      }
    }

    &__row {
      &--range {
        height: 55px;
        margin-bottom: 5px;
      }

      &--reviews {
        flex-direction: row;
      }

      &--line {
        height: auto;
      }
    }

    &__subtitle {
      &--mobile {
        margin-bottom: 0;
        font-weight: 500;
        font-size: 12px;
        color: $grey-2;
      }
    }

    &__btn-sort {
      display: block;
    }

    &__reset {
      border-radius: 5px;
    }
  }

  @include phones {
    padding: 15px;
    border-radius: 15px;

    &::before, &::after {
      width: 15px;
    }

    &__block {
      margin-bottom: 10px;

      &--double {
        width: 100%;
      }

      &--mobile {
        margin-bottom: 0;
      }

      &--reset {
        margin-bottom: 0;
      }
    }

    &__subtitle {
      font-size: 14px;
    }

    &__subtitle {
      &--mobile {
        font-size: 12px;
      }
    }

    &__btn-show {
      top: 5px;
      padding: 6px 12px;
      font-size: 12px;
    }
  }
}


.politics {
  position: relative;
  margin-top: 15px;
  padding-left: 30px;

  &--hurry {
    margin-top: 0;
    margin-bottom: 20px;
  }
  &__icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;

    &--timer {
      background-image: url(../img/icons/timer.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &--shild {
      background-image: url(../img/icons/shild.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }

    svg {
      width: 20px;
      height: 20px;
    }
  }

  &__text {
    margin-bottom: 0;
    font-size: 12px;
    line-height: 1.5;
    color: $grey-2;
    text-align: left;
  }
}

//   WordPress 
.wpcf7 {
  width: 100%;

  form {
    &.invalid .wpcf7-response-output {
      margin: 1em 0 1em;
      padding: 15px;
      font-size: 14px;
      color: $grey-2;
      background: rgba(255, 0, 0, 0.06);
      border: 1px solid rgba(255, 0, 0, 0.12);
      border-radius: 10px;
    }
    &.unaccepted .wpcf7-response-output {
      margin: 1em 0 1em;
      padding: 15px;
      font-size: 14px;
      color: $grey-2;
      background: rgba(255, 0, 0, 0.06);
      border: 1px solid rgba(255, 0, 0, 0.12);
      border-radius: 10px;
    }
  }
}

.wpcf7-form-control-wrap {
  width: 100%;
}

.wpcf7-form {
  p {
    margin-bottom: 0;
  }

  input[type='submit'] {
    width: 100%;
    white-space: normal;
  }

  .ajax-loader {
    position: absolute;
    bottom: 0;
    left: 50%;
    margin: 0;
    transform: translateY(-50%);
  }
}

.grecaptcha-badge {
  display: none !important;
}

.wpcf7-not-valid-tip {
  font-size: 14px;
}
