.press-kit {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 50px;
  margin-top: 20px;
  padding-left: 65px;
  line-height: 1.4;
  color: $accent;

  &--mobile {
    display: none;
  }

  &__icon {
    position: absolute;
    left: 0;
    top: 50%;
    width: 48px;
    height: 48px;
    background: $gr-grey;
    border-radius: 50%;
    transform: translateY(-50%);

    span {
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      width: 30px;
      height: 30px;
      background-image: url(../img/icons/press-kit.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      transform: translate(-50%, -50%);
      transition: transform $fastdown;
    }
  }

  &__date {
    font-weight: 400;
    font-size: 12px;
    color: $grey-2;
  }

  &:hover {
    .press-kit {
      &__icon {
        span {
          transform: translate(-50%, -50%) scale(1.15);
        }
      }
    }
  }

  @include laptop {
    &__date {
      font-size: 10px;
    }
  }

  @include tablets {
    &--mobile {
      display: flex;
      min-height: 25px;
      margin-top: 0;
      padding-left: 25px;
    }

    &__icon {
      &--mobile {
        width: 20px;
        height: 20px;
        background: none;

        span {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  @include small {
    &--mobile {
      min-height: 20px;
      padding-left: 30px;
    }

    &__icon {
      &--mobile {
        width: 18px;
        height: 18px;
      }
    }
  }
}

.press-block {
  margin-top: 45px;

  &__title {
    margin-bottom: 25px;
    padding: 15px;
    font-weight: 600;
    font-size: 22px;
    color: $grey-2;
    background: $gr-grey-2;
    border-radius: 10px;
  }

  &__text {
    margin-bottom: 45px;
    font-weight: 400;
    color: $grey-2;

    &--first {
      font-size: 18px;
      color: $default;
    }

    &--bottom {
      margin-bottom: 25px;
    }
  }

  &__lst {
    display: flex;
    justify-content: space-between;
    @include lst;
    margin-top: 30px;
    margin-bottom: 45px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 32%;
  }

  &__foto {
    position: relative;
    width: 280px;
    height: 280px;
    margin-top: 60px;
    margin-bottom: 20px;
    border-radius: 50%;
    background: $gr-grey;
    
    img {
      position: absolute;
      bottom: 0;
      left: 50%;
      z-index: 1;
      width: 280px;
      transform: translateX(-50%);
    }

    &--small {
      position: absolute;
      bottom: -20px;
      left: 0;
      width: 140px;
      height: 140px;
      margin-bottom: 0;

      img {
        width: 140px;
      }
    }
  }

  &__content {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  &__name {
    margin-bottom: 10px;
    font-size: 18px;
  }

  &__post {
    color: $grey-2;

    &--small {
      margin-bottom: 10px;
      font-weight: 400;
      font-size: 18px;
    }
  }

  &__footer {
    position: relative;
    width: 55%;
    margin-bottom: 60px;
    // padding-left: 170px;
  }

  &__phone {
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 22px;
    color: $grey-2;
  }

  &__mail {
    font-weight: 400;
    font-size: 22px;
    color: $accent;
  }

  @include desktop {
    margin-top: 30px;

    &__item {
      width: 28%;
    }

    &__foto {
      width: 220px;
      height: 220px;
      margin-top: 40px;

      img {
        width: 220px;
      }

      &--small {
        bottom: 0;
        width: 120px;
        height: 120px;
  
        img {
          width: 120px;
        }
      }
    }

    &__footer {
      padding-left: 140px;
    }
  }

  @include laptop {
    &__title {
      margin-bottom: 20px;
    }

    &__text {
      margin-bottom: 40px;
    }

    &__foto {
      width: 180px;
      height: 180px;
      margin-top: 30px;

      img {
        width: 180px;
      }

      &--small {
        bottom: 0;
        width: 120px;
        height: 120px;
  
        img {
          width: 120px;
        }
      }
    }
  }

  @include tablets {

    &__text {
      font-size: 16px;
    }

    &__lst {
      flex-direction: column;
    }

    &__item {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    &__foto {
      width: 220px;
      height: 220px;
      margin-top: 60px;

      img {
        width: 220px;
      }

      &--small {
        bottom: 0;
        width: 120px;
        height: 120px;
  
        img {
          width: 120px;
        }
      }
    }

    &__name {
      margin-bottom: 5px;
    }

    &__post {
      &--small {
        font-size: 16px;
      }
    }

    &__phone, &__mail {
      font-size: 20px;
    }

    &__footer {
      margin-bottom: 40px;
    }
  }

  @include phones {
    &__foto {
      width: 180px;
      height: 180px;
      margin-top: 30px;

      img {
        width: 180px;
      }

      &--small {
        position: relative;
        width: 140px;
        height: 140px;
        margin-top: 70px;
        margin-bottom: 30px;
  
        img {
          width: 140px;
        }
      }
    }

    &__text {
      font-size: 12px;

      &--first {
        font-size: 14px;
      }
    }

    &__title {
      margin-bottom: 15px;
      padding-right: 20%;
      font-size: 16px;
    }

    &__name {
      font-size: 14px;
    }

    &__post {
      font-size: 12px;

      &--small {
        font-size: 14px;
      }
    }

    &__footer {
      width: 100%;
      padding-left: 0;
    }

    &__phone, &__mail {
      font-size: 18px;
    }
  }
}