.test {

  &__title {
    margin-bottom: 35px;
    font-weight: 700;
    font-size: 22px;
    color: #393939;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    width: 100%;

    &--mfc {
      margin-top: 30px;
    }
  }

  &__inner {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
  }

  &__block {
    margin-bottom: 30px;
  }

  &__tab {
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
  }

  &__question {
    position: relative;
    padding-left: 20px;
    color: #393939;

    &-icon {
      position: absolute;
      top: 0;
      left: 0;
    }

    &--mfc {
      font-size: 18px;
    }

    &--new {
      padding-left: 0;
      font-weight: 600;
      font-size: 18px;
    }
  }

  &__variants {
    flex: 2 0 auto;
    display: flex;
    flex-direction: column;
  }

  &__radio, &__checkbox {
    flex: 1 0 auto;
    display: flex;
    align-items: center;
    min-height: 60px;
    margin-bottom: 5px;
    padding: 5px 20px;
    padding-left: 50px;
    background-color: $white;
    border-radius: 10px;
    box-shadow: $shadow-nano;

    &.active {
      color: $white;
      background: $gr-blue;

      .radio__label {
        color: $white;
      }
    }

    .radio__label {
      white-space: normal;
    }

    &--mfc {
      min-height: 45px;
      background-color: $white;
      border-radius: 6px;
    }

    &--new {
      min-height: auto;
      margin-bottom: 18px;
      padding: 0;
      padding-left: 27px;
      background: none;
      box-shadow: none;

      .checkbox__new, .radio__new {
        left: 0;
        background-color: $white;
        border: 1px solid #3c70f4;
        box-shadow: none;

        &--red {
          border: 1px solid $red;

          &::before {
            background-color: $red;
          }
        }
      }
    }
  }

  &__btn {
    padding: 30px;
    font-size: 20px;
    border-radius: 15px;

    &--pink {
      background: $gr-pink;
      transition: all $slowdown;

      &:hover {
        background: $gr-pink-hover;
      }
    }

    &--green {
      background: $gr-green;
      transition: all $slowdown;

      &:hover {
        background: $gr-green-hover;
      }
    }

    &--red {
      background: $gr-red;
      transition: all $slowdown;

      &:hover {
        background: $gr-red-hover;
      }
    }

    &--full {
      width: 100%;
    }
  }


  @include tablets {
    &__wrap {
      width: 360px;
      margin: 0 auto;

      &--mfc {
        width: 100%;
      }
    }
  }

  @include phones {
    &__wrap {
      width: 280px;

      &--mfc {
        width: 100%;
      }
    }

    &__question {
      &--mfc {
        font-size: 16px;
      }
    }

    &__btn {
      padding: 23px;
      font-size: 18px;
    }
  }
}

.answer-form {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

  &__wrap {
    width: 570px;
  }

  &__title {
    margin-top: 20px;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 26px;
    text-align: center;
  }

  &.sale {
    .loader {
      border-top: 3px solid $red;
    }

    .calc-modal__inner {
      color: $red;
    }

    .form__btn {
      background: $gr-red;
      transition: all $slowdown;

      &:hover {
        background: $gr-red-hover;
      }
    }

    .politics__text {
      a {
        color: $red;

        &:hover {
          color: rgba($red, 0.8);
        }
      }
    }
  }

  @include phones {
    &__wrap {
      width: 100%;
      margin: 0 22px;
    }
  }
}