.offers {
  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;

    &--modal {
      margin-bottom: 0;
      max-width: 1095px;
    }
  }

  &__block {
    width: 50%;
    
    &--first {
      padding-right: 5%;
    }
    &--second  {
      padding-left: 5%;
    }
  }

  &__item {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 60px;
  }

  &__foto {
    position:relative;
    width: 185px;
    height: 115px;
    margin-left: -30px;
    overflow: hidden;
    margin-right: 20px;
    border-radius: 5px;
    transform: skewX(-18deg);

    &--modal {
      transform: skewX(-18deg) scale(0.8) translateX(20%);
      transform-origin: center top;

      .offers__image {
        width: 125%;
      }
    }
  }

  &__image {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 120%;
    height: 120%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transform: translate(-50%, -50%) skewX(18deg);

    &--1 {
      background-image: url(../img/services/offers/1.jpg);
    }
    &--2 {
      background-image: url(../img/services/offers/2.jpg);
    }
    &--3 {
      background-image: url(../img/services/offers/3.jpg);
    }
    &--4 {
      background-image: url(../img/services/offers/4.jpg);
    }
    &--5 {
      background-image: url(../img/services/offers/5.jpg);
    }
    &--6 {
      background-image: url(../img/services/offers/6.jpg);
    }
    &--7 {
      background-image: url(../img/services/offers/7.jpg);
    }
    &--8 {
      background-image: url(../img/services/offers/8.jpg);
    }
    &--9 {
      background-image: url(../img/services/offers/9.jpg);
    }
    
    &.lazy {
      background-image: none;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    width: 365px;

    &--modal {
      width: 280px;
    }
  }

  &__name {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 18px;
  }

  &__desc {
    color: $grey-2;
  }

  &__btn-wrap {
    display: flex;
    justify-content: center;
  }

  @include desktop-1500 {
    &__content {
      width: 310px;

      &--modal {
        width: 260px;
      }
    }
  }

  @include desktop {
    &__foto {
      width: 160px;
      height: 100px;

      &--modal {
        width: 100px;
        height: 60px;
        transform: skew(-18deg, 0) scale(0.9) translateX(10%);
      }
    }

    &__content {
      width: 240px;

      &--modal {
        max-width: 220px;
      }
    }
  }

  @include laptop {
    &__wrap {
      flex-direction: column;
      margin-top: 30px;
    }

    &__block {
      width: 100%;
      
      &--first {
        padding-right: 0;
      }
      &--second  {
        padding-left: 0;
      }
    }

    &__item {
      justify-content: center;
    }

    &__foto {
      width: 185px;
      height: 115px;

      &--modal {
        width: 130px;
        height: 90px;
        transform-origin: right top;
      }
    }

    &__content {
      width: 365px;
      margin-left: 30px;
      
      &--modal {
        margin-left: 10px;
      }
    }
  }

  @include phones {
    &__wrap {
      &--modal {
        margin-top: 30px;
      }
    }

    &__item {
      flex-direction: column;
      margin-bottom: 40px;
    }

    &__foto {
      width: 150px;
      height: 100px;
      margin-left: 20px;
      margin-bottom: 20px;

      &--modal {
        margin-left: -5px;
        margin-bottom: 10px;
      }
    }

    &__content {
      width: 100%;
      margin-left: 0;

      &--modal {
        max-width: 100%;
      }
    }

    &__name {
      margin-bottom: 10px;
      font-size: 16px;
    }

    &__desc {
      font-size: 14px;
    }
  }
}